import React from "react";
import { Checkbox, FormControlLabel } from "@material-ui/core";
import * as PropTypes from "prop-types";
import { wrappedSetStatus } from "../utils/wrappedFunctions";

export const WrappedCheckBoxMUI = ({
  field: { name, value },
  form: { status, setStatus, setFieldValue, initialValues, validateField },
  label,
  disabled = false,
  customOnChange
}) => (
  <FormControlLabel
    control={<Checkbox indeterminate={value === null} color="primary" />}
    onBlur={() => {
      validateField(name);
    }}
    onChange={() => {
      if (customOnChange) {
        customOnChange({
          name,
          value: !value,
          initialValues,
          setFieldValue,
          status,
          setStatus: wrappedSetStatus(setStatus, status)
        });
      }
      setFieldValue(name, !value);
    }}
    value={`${value}`}
    checked={value}
    label={label}
    disabled={disabled}
  />
);

WrappedCheckBoxMUI.defaultProps = {
  label: " ",
  customOnChange: undefined
};

WrappedCheckBoxMUI.propTypes = {
  field: PropTypes.shape({}).isRequired,
  form: PropTypes.shape({}).isRequired,
  label: PropTypes.string,
  disabled: PropTypes.bool.isRequired,
  customOnChange: PropTypes.func
};
