import React from "react";
import * as PropTypes from "prop-types";
import { get } from "lodash";
import { ReactSelectMUI } from "../../reactSelectMUI";
import { checkIfFieldHasValidationError } from "../utils/checkIfFieldHasValidationError";
import { wrappedSetStatus } from "../utils/wrappedFunctions";

export const WrappedReactSelect = ({
  field: { name, value, onBlur },
  label,
  customOptionsName,
  options: hardCodedOptions,
  isMulti,
  isDisabled,
  placeholder,
  form: {
    errors,
    touched,
    setFieldTouched,
    setFieldValue,
    initialValues,
    status: { optionsMap },
    status,
    setStatus
  },
  customOnChange
}) => {
  //  ((      optionsMap ,  name  field    
  let resName;
  if (/./.test(name)) {
    const tmpName = name.split(".");
    resName = tmpName[tmpName.length - 1];
  } else {
    resName = name;
  }

  const { options, loaded } = get(optionsMap, customOptionsName || resName) || {
      options: hardCodedOptions,
      loaded: true
    } || { options: [], loaded: true };
  const textFieldProps = checkIfFieldHasValidationError(name, touched, errors);

  let placeHolderText;
  if (!loaded) {
    placeHolderText = "  ...";
  } else if (placeholder) {
    placeHolderText = placeholder;
  } else if (isDisabled && value == null) {
    placeHolderText = "";
  }
  return (
    <ReactSelectMUI
      options={options}
      isMulti={isMulti}
      onMenuClose={() => setFieldTouched(name, true)}
      textFieldProps={textFieldProps}
      isDisabled={isDisabled || !loaded}
      placeholder={placeHolderText}
      label={label}
      value={value}
      onBlur={onBlur}
      handleChange={selected => {
        if (customOnChange) {
          customOnChange({
            name,
            value: selected,
            initialValues,
            setFieldValue,
            status,
            setStatus: wrappedSetStatus(setStatus, status)
          });
        }
        setFieldValue(name, selected);
      }}
    />
  );
};

WrappedReactSelect.defaultProps = {
  label: "",
  options: [],
  placeholder: undefined,
  customOnChange: undefined,
  customOptionsName: undefined
};

WrappedReactSelect.propTypes = {
  field: PropTypes.shape({
    name: PropTypes.string.isRequired,
    value(props, propName, componentName) {
      if (props.isMulti === true && !Array.isArray(props[propName])) {
        return new Error(
          `prop \`${propName}\` of type Array should be supplied` +
            ` \`${componentName}\`. Validation failed.`
        );
      }
      return undefined;
    },
    onBlur: PropTypes.func
  }).isRequired,
  label: PropTypes.string,
  options: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string.isRequired })
  ),
  isMulti: PropTypes.bool.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  form: PropTypes.shape({}).isRequired,
  placeholder: PropTypes.string,
  customOnChange: PropTypes.func,
  customOptionsName: PropTypes.string
};
