import Yup from "../../../yup";
import { computePropsForFormikMUI } from "../../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { halfWith } from "../../../widths";
import { baseNumberProps, baseTextAreaProps } from "../../../baseFieldProps";

const baseForm = {
  title: " ",
  titleItem: "   ",
  headerText: "label",
  url: "/api/commonAction",
  method: "patch",
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "label",
          type: "text",
          widths: halfWith
        },
        {
          label: "  ",
          name: "longTime",
          ...baseNumberProps(),
          widths: halfWith
        },
        {
          label: "",
          name: "description",
          ...baseTextAreaProps
        },
        {
          name: "isWork",
          label: "   ",
          type: "checkbox"
        },
        {
          name: "isMinistration",
          label: "   ",
          type: "checkbox"
        },
        {
          name: "isClaim",
          label: "   ",
          type: "checkbox"
        }
      ]
    }
  ]
};

const forms = {
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" }),
  edit: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      validationSchema: Yup.object({
        label: Yup.string().required()
      })
    },
    mode: "edit"
  })
};
export default forms;
