import { makeStyles } from "@material-ui/styles";
import { Grid } from "@material-ui/core";
import * as PropTypes from "prop-types";
import React from "react";
import { RenderBlock } from "../../renderBlock";

const useStyles = makeStyles(theme => ({
  divider: {
    marginLeft: `${theme.spacing.unit}px`,
    height: "1px",
    backgroundColor: "white",
    flex: "1  1 auto"
  },
  field: {
    width: "100%"
  }
}));

export const RenderBlockForm = ({ formObj, handleSubmit, style }) => {
  const classes = useStyles();
  return (
    <form onSubmit={handleSubmit}>
      <div style={{ padding: "4px 16px 10px 16px", ...style }}>
        <Grid container spacing={8} direction="column">
          {formObj.blocks.map((block, bIndex) => (
            <Grid item key={bIndex}>
              <RenderBlock block={block} bIndex={bIndex} classes={classes} />
            </Grid>
          ))}
        </Grid>
      </div>
    </form>
  );
};

RenderBlockForm.defaultProps = {
  style: undefined
};

RenderBlockForm.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  formObj: PropTypes.shape({}).isRequired,
  style: PropTypes.shape({})
};
