import React, { useContext } from "react";
import { TableCellProps } from "react-virtualized";
import { IconButton } from "@material-ui/core";
import { Add, Done, Remove } from "@material-ui/icons";
import axios from "axios";
import { connect } from "react-redux";
import { Action, Dispatch } from "redux";
import { EventType, IOption, TypeEnum } from "./types";
import { TableContext } from "./ScheduleTable";
import { addEvent, removeEvent } from "./tableReducer";
import { openSnackBar } from "../../actions/typedActions";
import { defaultErrorHandler } from "../../utils/defaultErrorHandler";

type PrivateActionButtonsProps = ReturnType<typeof mapStateToProps> &
  ReturnType<typeof mapDispatchToProps> &
  TableCellProps & {
    openSnackBarWithMessage: Function;
  };

const mapPerformer = ({ label, value }: IOption) => ({
  fullName: label,
  id: value
});

const PrivateActionButtons: React.FC<PrivateActionButtonsProps> = ({
  cellData: events,
  rowIndex,
  rowData,
  openSnackBarWithMessage
}) => {
  const { dispatch } = useContext(TableContext);
  const addEventPrivate = () => {
    dispatch(addEvent(rowIndex));
  };
  if (events.length === 0) {
    return (
      <div>
        <IconButton onClick={addEventPrivate}>
          <Add />
        </IconButton>
      </div>
    );
  }
  return events.map((event: EventType, eventIndex: number) => {
    const {
      canEdit: canEditEvent,
      from,
      to,
      uniqueId,
      type,
      typeEnum,
      performers
    } = event;
    if (!canEditEvent) {
      return null;
    }
    return (
      <div key={uniqueId}>
        <IconButton
          onClick={() => {
            if (typeEnum === TypeEnum.ministration) {
              const dateTimestamp = `${rowData.day} ${
                from.length > 5 ? from : `${from}:00`
              }`;
              const dateEndTimestamp = `${rowData.day} ${
                to.length > 5 ? to : `${to}:00`
              }`;
              let promise;

              const obj = {
                objectTypeCode: type.value,
                performers: performers.map(mapPerformer),
                dateTimestamp,
                dateEndTimestamp
              };

              if (typeof uniqueId === "string") {
                promise = axios.post("/api/ministration", obj);
              } else {
                promise = axios.patch(`/api/ministration/${uniqueId}`, obj);
              }

              promise
                .then(({ data }) => {
                  if (data) {
                    openSnackBarWithMessage(data);
                  }
                })
                .catch(reason => defaultErrorHandler(reason));
            }
          }}
          disabled={!canEditEvent || event.typeEnum !== TypeEnum.ministration}
        >
          <Done />
        </IconButton>
        <IconButton onClick={addEventPrivate}>
          <Add />
        </IconButton>
        <IconButton
          onClick={() => {
            if (typeof uniqueId === "string") {
              dispatch(removeEvent(rowIndex, eventIndex));
            } else {
              switch (typeEnum) {
                case TypeEnum.ministration: {
                  axios
                    .delete(`/api/ministration/${uniqueId}`)
                    .then(({ data }) => {
                      dispatch(removeEvent(rowIndex, eventIndex));
                      if (data) {
                        openSnackBarWithMessage(data);
                      }
                    })
                    .catch(reason => defaultErrorHandler(reason));
                  break;
                }
                case TypeEnum.slot: {
                  axios
                    .delete(`/api/slots/${uniqueId}`)
                    .then(({ data }) => {
                      dispatch(removeEvent(rowIndex, eventIndex));
                      if (data) {
                        openSnackBarWithMessage(data);
                      }
                    })
                    .catch(reason => defaultErrorHandler(reason));
                  break;
                }
                case TypeEnum.workItem: {
                  axios
                    .delete(`/api/workitem/delete/${uniqueId}`)
                    .then(({ data }) => {
                      dispatch(removeEvent(rowIndex, eventIndex));
                      if (data) {
                        openSnackBarWithMessage(data);
                      }
                    })
                    .catch(reason => defaultErrorHandler(reason));
                  break;
                }
                default:
                  break;
              }
            }
          }}
        >
          <Remove />
        </IconButton>
      </div>
    );
  });
};

const mapStateToProps = (state: any) => ({});

const mapDispatchToProps = (dispatch: Dispatch<Action>) => ({
  openSnackBarWithMessage: (message: string) => dispatch(openSnackBar(message))
});

export const ActionButtons = connect(
  undefined,
  mapDispatchToProps
)(PrivateActionButtons);
