import React from "react";
import { Button, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import classNames from "classnames";
import { addFiltersToUrl } from "../../actions";
import { baseUrlsToDownLoadReport } from "../../constants/pages/statistics/reports/journal";
import { downloadFile } from "../formikMUi/utils/downLoad";

const getStyles = makeStyles(theme => ({
  xlsBtn: {
    color: theme.palette.getContrastText("#2c5d2d"),
    backgroundColor: "#2c5d2d",
    "&:hover": {
      backgroundColor: "#204020"
    }
  },
  pdfBtn: {
    color: theme.palette.getContrastText("#820b09"),
    backgroundColor: "#820b09",
    "&:hover": {
      backgroundColor: "#500b09"
    }
  },
  btn: {
    marginTop: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  textCont: {
    marginLeft: theme.spacing.unit * 2
  }
}));

export const DownloadReportButtons = ({ reportName, filters, ...rest }) => {
  const classes = getStyles();
  const url = baseUrlsToDownLoadReport[reportName];
  if (typeof url === "undefined") {
    throw new Error(`  Url     : ${reportName}`);
  }
  return (
    <>
      <Button
        className={classNames(classes.xlsBtn, classes.btn)}
        variant="contained"
        onClick={() => {
          if (typeof url === "function") {
            if (filters) {
              downloadFile(
                addFiltersToUrl(filters, url("xlsx")),
                `${reportName}.xlsx`
              );
            }
          }
        }}
      >
          Xls
      </Button>
      <Button
        className={classNames(classes.pdfBtn, classes.btn)}
        variant="contained"
        onClick={() => {
          if (typeof url === "function") {
            if (filters) {
              downloadFile(
                addFiltersToUrl(filters, url("pdf")),
                `${reportName}.pdf`
              );
            }
          }
        }}
      >
          Pdf
      </Button>
    </>
  );
};

export const DownLoadReportStep = () => {
  const classes = getStyles();
  return (
    <Typography variant="body1" className={classes.textCont}>
            
    </Typography>
  );
};
