import React from "react";
import { connect, Field, FieldArray } from "formik";
import { TextField } from "formik-material-ui";
import locale from "date-fns/locale/ru";
import axios from "axios";
import { Fab, Paper, Grid } from "@material-ui/core";
import { Add, Delete } from "@material-ui/icons";
import { get, hasIn } from "lodash";
import { makeStyles } from "@material-ui/styles";
import defineItemVisibility from "../utils/dependenciesUt";
import constructKeyForRow from "../utils/constructKeyForRow";
import { WrappedReactSelect } from "../../../wrappedComponents/wrappedReactSelect";
import IMaskedRegExpFilteredTextField from "../../../wrappedComponents/IMaskedRegExpFilteredTextField";
import getMask from "../utils/getMask";
import RegExpFilteredTextField from "../../../../regExpFilteredTextField";
import { WrappedReactDropZoneMUI } from "../../../wrappedComponents/wrappedReactDropZoneMUI";
import { IntegrateDatePickerWithMUI } from "../../../../IntegrateDatePickerWithMUI";
import IntegrateDateRangePickerWithMUI from "../../../../IntegrateDateRangePickerWithMUI";
import { WrappedCheckBoxMUI } from "../../../wrappedComponents/wrappedCheckBoxMUI";
import { validateField } from "../utils/validateField";
import { defaultErrorHandler } from "../../../../../utils/defaultErrorHandler";
import {
  findAllAttributesByPredicate,
  findAttributeByPredicate,
  findByNamePredicate,
  findBySuperAttributeNamePredicate
} from "../utils/find";
import { urlToDownLoadClaimAttachmentFromFtp } from "../../../utils/downLoad";

const customOnChange = formObj => ({ name, setFieldValue }) => {
  const foundLowerAttrs = findAllAttributesByPredicate(
    formObj,
    findBySuperAttributeNamePredicate(name)
  );
  if (foundLowerAttrs && Array.isArray(foundLowerAttrs)) {
    foundLowerAttrs.forEach(({ name: lowerAttrName }) =>
      setFieldValue(lowerAttrName, null)
    );
  }
};

const getStyles = makeStyles(theme => ({
  field: {
    width: "100%"
  }
}));

export const saveFile = (data, claimUuid) =>
  axios({
    method: "post",
    url: `/api/claim/attachment/save/${claimUuid}`,
    data,
    config: {
      "Content-Type": "multipart/form-data"
    }
  });

const loadNsiOptions = (nsiName, superNsiName, superNsiValue) =>
  axios
    .get("/api/nsi/getNsiContent", {
      params: {
        nsiName,
        superNsiName,
        superNsiValue
      }
    })
    .then(({ data: { dictionary } }) => dictionary)
    .catch(e => {
      defaultErrorHandler(e);
    });

const renderMultyItems = (
  props,
  name,
  multiItems,
  readOnly,
  classes,
  additionalProps
) => {
  const {
    form: { values, initialValues },
    push,
    remove
  } = props;
  const fieldArrValues = get(values, name);

  const renderedMultyItems = fieldArrValues.map((item, index) => (
    <Paper
      style={{
        backgroundColor: "#e4f5fc",
        padding: "8px",
        marginTop: "8px",
        marginBottom: "8px"
      }}
      key={`${name}.${index}`}
    >
      {multiItems[0].group.map(
        row =>
          defineItemVisibility(
            row,
            multiItems[index],
            fieldArrValues[index]
          ) && (
            <Grid item key={constructKeyForRow(row)}>
              <Grid container spacing={8}>
                {row.attribute.map((field, fIndex) =>
                  typeof field.name !== "undefined" ? (
                    <Grid
                      item
                      key={`${name}.${index}.${field.name}`}
                      lg={12 / row.attribute.length}
                      md={12 / row.attribute.length}
                      xl={12 / row.attribute.length}
                    >
                      <RenderField
                        field={field}
                        name={`${name}.${index}.${field.name}`}
                        classes={classes}
                        additionalProps={additionalProps}
                      />
                    </Grid>
                  ) : (
                    <Grid
                      item
                      key={`${name}.${index}.${fIndex}`}
                      lg={12 / row.attribute.length}
                      md={12 / row.attribute.length}
                      xl={12 / row.attribute.length}
                    />
                  )
                )}
              </Grid>
            </Grid>
          )
      )}
      {fieldArrValues.length > 1 && !readOnly ? (
        <Fab color="secondary" size="small" onClick={() => remove(index)}>
          <Delete />
        </Fab>
      ) : null}
    </Paper>
  ));

  return (
    <>
      {renderedMultyItems}
      {!readOnly && (
        <Fab
          onClick={() => push({ ...get(initialValues, name)[0] })}
          color="primary"
          size="small"
        >
          <Add />
        </Fab>
      )}
    </>
  );
};

const PrivateRenderField = ({
  field,
  name: customName,
  additionalProps,
  formik: {
    status: { optionsMap, readOnly },
    status,
    values,
    setStatus,
    setFieldValue,
    errors
  },
  formObj
}) => {
  const classes = getStyles();
  if (field === "" || field === null || typeof field === "undefined") {
    return null;
  }
  const {
    label,
    type: { name: typeName, allowedpattern, maxlength, mask },
    name
  } = field;
  switch (typeName) {
    case "dictionary": {
      const {
        type: {
          elements: { element: options }
        }
      } = field;
      return (
        <Field
          key={customName || name}
          label={label}
          name={customName || name}
          validate={value => validateField({ value, field })}
          component={WrappedReactSelect}
          options={options}
          isMulti={false}
          isDisabled={readOnly}
          classes={{ root: classes.field }}
        />
      );
    }
    case "dictionary-nsi": {
      const { nameTag, superAttributeName } = field.type;
      const superFieldValue = get(values, superAttributeName);
      //     , ..           
      if (superAttributeName) {
        if (
          superFieldValue &&
          !hasIn(
            optionsMap,
            `${nameTag}.${superAttributeName}.${superFieldValue.value}`
          )
        ) {
          //    ,  -> Xml   
          const {
            type: { nameTag: nsiMasterName }
          } = findAttributeByPredicate(
            formObj,
            findByNamePredicate(superAttributeName)
          );

          //    
          setStatus({
            ...status,
            optionsMap: {
              ...optionsMap,
              [`${nameTag}.${superAttributeName}.${superFieldValue.value}`]: {
                options: [],
                loaded: false
              }
            }
          });

          loadNsiOptions(nameTag, nsiMasterName, superFieldValue.value).then(
            r => {
              setStatus({
                ...status,
                optionsMap: {
                  ...optionsMap,
                  [`${nameTag}.${superAttributeName}.${
                    superFieldValue.value
                  }`]: {
                    options: r,
                    loaded: true
                  }
                }
              });
            }
          );
        }
      }
      // todo           
      //      , ..  
      else if (!hasIn(optionsMap, nameTag)) {
        //    
        setStatus({
          ...status,
          optionsMap: {
            ...optionsMap,
            [nameTag]: {
              options: [],
              loaded: false
            }
          }
        });

        loadNsiOptions(nameTag).then(r => {
          setStatus({
            ...status,
            optionsMap: {
              ...optionsMap,
              [nameTag]: {
                options: r,
                loaded: true
              }
            }
          });
        });
      }
      return (
        <Field
          key={customName || name}
          label={label}
          name={customName || name}
          validate={value => validateField({ value, field })}
          component={WrappedReactSelect}
          customOptionsName={
            superAttributeName && superFieldValue
              ? `${nameTag}.${superAttributeName}.${superFieldValue.value}`
              : nameTag
          }
          customOnChange={
            !superAttributeName ? customOnChange(formObj) : undefined
          }
          isMulti={false}
          closeMenuOnSelect
          isDisabled={readOnly}
          classes={{ root: classes.field }}
        />
      );
    }
    case "string-32": {
      if (mask) {
        // mask   "_" - regExp Imask, replace
        // mask   "#" - mask
        return (
          <Field
            key={customName || name}
            label={label}
            name={customName || name}
            validate={value => validateField({ value, field })}
            component={TextField}
            InputProps={{
              readOnly,
              name: customName || name,
              inputComponent: IMaskedRegExpFilteredTextField,
              inputProps: {
                mask: getMask(mask),
                setFieldValue,
                name: customName || name
              }
            }}
            classes={{ root: classes.field }}
          />
        );
      }
      if (allowedpattern || maxlength) {
        return (
          <Field
            key={customName || name}
            label={label}
            name={customName || name}
            validate={value => validateField({ value, field })}
            component={RegExpFilteredTextField}
            regExp={allowedpattern ? RegExp(allowedpattern, "g") : undefined}
            maxLength={maxlength}
            classes={{ root: classes.field }}
            InputProps={{
              readOnly
            }}
          />
        );
      }
      return (
        <Field
          key={customName || name}
          name={customName || name}
          label={label}
          validate={value => validateField({ value, field })}
          component={TextField}
          classes={{ root: classes.field }}
          InputProps={{
            readOnly
          }}
        />
      );
    }
    case "string-256": {
      if (mask) {
        // mask   "_" - regExp Imask, replace
        // mask   "#" - mask
        return (
          <Field
            key={customName || name}
            label={label}
            name={customName || name}
            validate={value => validateField({ value, field })}
            component={TextField}
            InputProps={{
              readOnly,
              name: customName || name,
              inputComponent: IMaskedRegExpFilteredTextField,
              inputProps: {
                mask: getMask(mask),
                setFieldValue,
                name: customName || name
              }
            }}
            classes={{ root: classes.field }}
          />
        );
      }
      if (allowedpattern) {
        return (
          <Field
            key={customName || name}
            label={label}
            name={customName || name}
            validate={value => validateField({ value, field })}
            component={RegExpFilteredTextField}
            regExp={allowedpattern ? RegExp(allowedpattern, "g") : undefined}
            maxLength={maxlength}
            classes={{ root: classes.field }}
            InputProps={{
              readOnly
            }}
          />
        );
      }
      return (
        <Field
          key={customName || name}
          name={customName || name}
          label={label}
          validate={value => validateField({ value, field })}
          component={TextField}
          classes={{ root: classes.field }}
          InputProps={{
            readOnly
          }}
        />
      );
    }
    case "string": {
      return (
        <Field
          key={customName || name}
          name={customName || name}
          label={label}
          multiline
          rowsMax={6}
          validate={value => validateField({ value, field })}
          component={TextField}
          classes={{ root: classes.field }}
          InputProps={{
            readOnly
          }}
        />
      );
    }
    case "attachment": {
      const { claimUuid } = status;
      return (
        <Field
          key={customName || name}
          name={customName || name}
          label={label}
          component={WrappedReactDropZoneMUI}
          validate={value => validateField({ value, field })}
          onDrop={files => {
            const data = new FormData();
            data.append("file", files[0]);
            saveFile(data, claimUuid)
              .then(({ data: { name } }) => {
                // [{"label":"dug.jpg","value":"998160b8-95f8-4af6-868e-646d96480be7","lastModified":1531486007949,"progress":100,"complete":true,"name":"dug.jpg"}]
                setFieldValue(field.name, {
                  label: files[0].name,
                  value: [
                    {
                      uuidClaim: claimUuid,
                      label: name,
                      name,
                      lastModified: new Date().valueOf(),
                      progress: 100,
                      complete: true
                    }
                  ]
                });
              })
              .catch(error => {
                defaultErrorHandler(error);
              });
          }}
          customUrlToDownLoadFile={urlToDownLoadClaimAttachmentFromFtp}
          disabled={readOnly || false}
        />
      );
    }
    case "date": {
      const pickerProps = {
        locale: { ...locale },
        moveRangeOnFirstSelection: false,
        showDateDisplay: false
      };
      const {
        type: { more, less }
      } = field;
      //     "23.01.2018"
      if (typeof more !== "undefined") {
        pickerProps.minDate =
          more === "now" ? new Date() : new Date(...more.split(".").reverse());
      }
      if (typeof less !== "undefined") {
        pickerProps.maxDate =
          less === "now" ? new Date() : new Date(...less.split(".").reverse());
      }
      return (
        <Field
          key={customName || name}
          name={customName || name}
          label={label}
          component={IntegrateDatePickerWithMUI}
          readOnly={readOnly || false}
          validate={value => validateField({ value, field })}
          pickerProps={pickerProps}
        />
      );
    }
    case "daterange": {
      const pickerProps = {
        locale: { ...locale },
        moveRangeOnFirstSelection: false,
        showDateDisplay: false
      };
      return (
        <Field
          key={customName || name}
          name={customName || name}
          label={label}
          pickerProps={pickerProps}
          component={IntegrateDateRangePickerWithMUI}
          readOnly={readOnly || false}
        />
      );
    }
    case "boolean": {
      return (
        <Field
          name={customName || name}
          key={customName || name}
          label={label}
          validate={value => validateField({ value, field })}
          component={WrappedCheckBoxMUI}
          disabled={readOnly}
          error={errors[customName || name]}
        />
      );
    }
    case "multy-items": {
      const multiItems = field.type["multy-items"];
      return (
        <FieldArray
          validateOnChange={false}
          key={customName || name}
          name={customName || name}
          render={props =>
            renderMultyItems(
              props,
              customName || name,
              multiItems,
              readOnly,
              classes,
              additionalProps
            )
          }
        />
      );
    }
    default: {
      throw new Error(
        `    "${typeName}"   "${name}"`
      );
    }
  }
};

export const RenderField = connect(PrivateRenderField);
