import React, { ReactElement } from "react";
import { IconButton, Typography, TableCell } from "@material-ui/core";
import { InsertDriveFileOutlined } from "@material-ui/icons";
import classNames from "classnames";
import { makeStyles } from "@material-ui/styles";
import { IOption } from "./types";

interface MonthWordCellProps {
  cellData: IOption;
  style?: any;
}
const getStyles = makeStyles(theme => ({
  tableCell: {
    flex: 1,
    flexDirection: "column"
  },
  TableCellRoot: {
    borderBottom: "none"
  },
  flexContainer: {
    display: "flex",
    alignItems: "center",
    boxSizing: "border-box"
  }
}));

export const MonthWordCell = ({
  cellData,
  style
}: MonthWordCellProps): ReactElement => {
  // const { label, value } = cellData;
  const classes = getStyles();
  return (
    <TableCell
      component="div"
      variant="body"
      classes={{ root: classes.TableCellRoot }}
      className={classNames(classes.tableCell, classes.flexContainer)}
      style={style}
    >
      {cellData && <Typography>{cellData.label}</Typography>}
      <IconButton>
        <InsertDriveFileOutlined />
      </IconButton>
    </TableCell>
  );
};
