import React, { useEffect, useRef, useState } from "react";
import * as PropTypes from "prop-types";
import {
  TextField,
  Popper,
  Fade,
  Paper,
  Button,
  Grid,
  ClickAwayListener
} from "@material-ui/core";
import { DateRangePicker } from "react-date-range";
import { format, parse } from "date-fns";
import { makeStyles } from "@material-ui/styles";
import { defaultStaticRanges, defaultInputRanges } from "./defaultRanges";
import "react-date-range/dist/styles.css"; // main style file
import "react-date-range/dist/theme/default.css";
import { checkIfFieldHasValidationError } from "../formikMUi/utils/checkIfFieldHasValidationError"; // theme css file

const getStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing.unit
  },
  buttonPanel: {
    borderTop: "solid 1px #eff2f7"
  },
  popper: {
    zIndex: 2000
  }
}));

const formatDateValue = selection => {
  const startDate = selection.startDate
    ? `${format(selection.startDate, "YYYY-MM-DD")} 00:00:00`
    : null;
  const endDate = selection.endDate
    ? `${format(selection.endDate, "YYYY-MM-DD")} 23:59:59`
    : null;
  return [startDate, endDate];
};

const IntegrateDateRangePickerWithMUI = props => {
  const classes = getStyles();
  const [open, setOpen] = useState(false);
  const {
    pickerProps,
    form: { touched, errors, setFieldValue },
    field: { name, value },
    label,
    readOnly
  } = props;
  const errorHelperTextProps = checkIfFieldHasValidationError(
    name,
    touched,
    errors
  );

  const [dateRange, setDateRange] = useState({
    selection: {
      startDate: new Date(),
      endDate: new Date(),
      key: "selection"
    }
  });

  const textFieldValue =
    value && Array.isArray(value) && value.length > 1
      ? `${format(value[0], "DD.MM.YYYY")} - ${format(value[1], "DD.MM.YYYY")}`
      : "";

  useEffect(() => {
    if (value && Array.isArray(value) && value.length > 1) {
      setDateRange({
        selection: {
          startDate: parse(value[0], "YYYY-MM-DD", new Date()),
          endDate: parse(value[1], "YYYY-MM-DD", new Date()),
          key: "selection"
        }
      });
    }
  }, [value]);

  const containerRef = useRef(null);

  return (
    <div ref={containerRef}>
      <TextField
        label={label}
        value={textFieldValue}
        onClick={() => {
          if (readOnly === false) {
            setOpen(true);
          }
        }}
        {...errorHelperTextProps}
        fullWidth
      />
      <Popper
        open={open}
        anchorEl={containerRef.current}
        placement="bottom-start"
        transition
        className={classes.popper}
      >
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={350}>
            <ClickAwayListener
              onClickAway={() => {
                setOpen(false);
              }}
            >
              <Paper>
                <DateRangePicker
                  ranges={[dateRange.selection]}
                  staticRanges={[...defaultStaticRanges]}
                  inputRanges={[...defaultInputRanges]}
                  onChange={v => setDateRange(v)}
                  {...pickerProps}
                />
                <Grid
                  container
                  justify="flex-end"
                  className={classes.buttonPanel}
                >
                  <Grid item>
                    <Button
                      variant="outlined"
                      className={classes.button}
                      onClick={() => setOpen(false)}
                    >
                      
                    </Button>
                  </Grid>
                  <Grid item>
                    <Button
                      variant="outlined"
                      color="primary"
                      className={classes.button}
                      onClick={() => {
                        setFieldValue(
                          name,
                          formatDateValue(dateRange.selection)
                        );
                        setOpen(false);
                      }}
                    >
                      
                    </Button>
                  </Grid>
                </Grid>
              </Paper>
            </ClickAwayListener>
          </Fade>
        )}
      </Popper>
    </div>
  );
};

IntegrateDateRangePickerWithMUI.defaultProps = {
  readOnly: false,
  label: ""
};

IntegrateDateRangePickerWithMUI.propTypes = {
  pickerProps: PropTypes.shape({}).isRequired,
  form: PropTypes.shape({ errors: PropTypes.shape({}).isRequired }).isRequired,
  field: PropTypes.shape({
    name: PropTypes.string.isRequired,
    value: PropTypes.arrayOf(PropTypes.string).isRequired
  }).isRequired,
  readOnly: PropTypes.bool,
  label: PropTypes.string
};

export default IntegrateDateRangePickerWithMUI;
