import forms from "./form";
import { phoneMask } from "../../masks";
import {
  constructRebuildReceivedOptionsLabelLabelValueUniqueId,
  rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue
} from "../../rebuildRecieved";

const users = {
  requiresFunction: [
    {
      functionName: "administration_user_modifier",
      allowedActions: ["create", "view", "edit", "delete"]
    },
    {
      functionName: "administrator_hram_user_modifier",
      allowedActions: ["create", "view", "edit", "delete"]
    },
    {
      functionName: "member_user_view",
      allowedActions: ["view"]
    }
  ],
  title: " ",
  url: "/api/journalUsers",
  filtersPanel: {
    filters: [
      { label: "", name: "surnameInitials", type: "text" },
      {
        label: "",
        name: "accost",
        type: "text"
      },
      {
        label: " ",
        name: "memberId",
        type: "select",
        dictUrl: "/api/strMember/search/present",
        rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId
      },
      {
        label: "",
        name: "role",
        type: "select",
        dictUrl: "/api/user/role",
        rebuildReceivedOptions: rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue
      },
      {
        label: "",
        name: "phone",
        type: "formatted",
        mask: phoneMask
      }
    ]
  },
  tableHeader: [
    {
      label: "",
      sortable: true,
      path: "fullName",
      width: "20%"
    },
    {
      label: "",
      sortable: false,
      path: "accost",
      width: "20%"
    },
    {
      label: " ",
      sortable: true,
      path: "memberName",
      width: "35%"
    },
    {
      label: "",
      sortable: true,
      path: "role",
      width: "15%"
    },
    {
      label: "",
      sortable: false,
      path: "phone",
      width: "15%"
    }
  ],
  forms: { ...forms }
};
export default users;
