import React, { useState, useEffect } from "react";
import axios from "axios";
import * as PropTypes from "prop-types";
import { Grid, Typography, CircularProgress } from "@material-ui/core";
import { set } from "lodash";

export const defaultPreloader = ({ message, size = 120 }) => (
  <Grid
    justify="center"
    alignContent="center"
    direction="column"
    alignItems="center"
    container
    style={{
      height: "300px"
    }}
  >
    <Typography variant="h6">
      {message || "    "}
    </Typography>
    <CircularProgress size={size} />
  </Grid>
);

defaultPreloader.propTypes = {
  message: PropTypes.string,
  size: PropTypes.number
};

defaultPreloader.defaultProps = {
  message: "    ",
  size: 120
};

const CompletePromiseThenRenderComponent = ({
  config,
  errorHandler,
  PreloaderComponent = CircularProgress,
  Component,
  rebuildDownloadedObj,
  ...rest
}) => {
  const [isLoading, setIsLoading] = useState(true);
  const [downloaded, setDownloaded] = useState({});

  useEffect(() => {
    let localDownloaded = {};
    let onAdditionalDemandConfig = [];
    //       props
    /*    loadStuffDependingOnConfig(
      config,
      localDownloaded,
      onAdditionalDemandConfig
    ) */
    const source = axios.CancelToken.source();
    axios
      .all(
        config.map(item =>
          axios.get(item.urlToDownload, { cancelToken: source.token })
        )
      )
      .then(response =>
        config.forEach(
          (
            {
              whereToStore,
              rebuildReceivedValue,
              postLoadDoSomethingThenPassResultAsProp,
              extendAdditionalOnDemandConfig
            },
            index
          ) => {
            const { data } = response[index];

            set(
              localDownloaded,
              whereToStore,
              rebuildReceivedValue ? rebuildReceivedValue(data) : data
            );

            if (postLoadDoSomethingThenPassResultAsProp) {
              const {
                func,
                passResultAs
              } = postLoadDoSomethingThenPassResultAsProp;
              set(
                localDownloaded,
                passResultAs,
                func(data, onAdditionalDemandConfig)
              );
            }
            if (extendAdditionalOnDemandConfig) {
              onAdditionalDemandConfig = [
                ...onAdditionalDemandConfig,
                ...extendAdditionalOnDemandConfig(localDownloaded)
              ];
            }
          }
        )
      )
      //        
      .then(r =>
        // loadStuffDependingOnConfig(onAdditionalDemandConfig, localDownloaded)
        axios
          .all(
            onAdditionalDemandConfig.map(item =>
              axios.get(item.urlToDownload, { cancelToken: source.token })
            )
          )
          .then(response =>
            onAdditionalDemandConfig.forEach(
              (
                {
                  whereToStore,
                  rebuildReceivedValue,
                  postLoadDoSomethingThenPassResultAsProp
                },
                index
              ) => {
                const { data } = response[index];

                set(
                  localDownloaded,
                  whereToStore,
                  rebuildReceivedValue ? rebuildReceivedValue(data) : data
                );

                if (postLoadDoSomethingThenPassResultAsProp) {
                  const {
                    func,
                    passResultAs
                  } = postLoadDoSomethingThenPassResultAsProp;
                  set(
                    localDownloaded,
                    passResultAs,
                    func(data, onAdditionalDemandConfig)
                  );
                }
              }
            )
          )
      )
      .then(r => {
        //       
        if (
          rebuildDownloadedObj &&
          typeof rebuildDownloadedObj === "function"
        ) {
          localDownloaded = {
            ...localDownloaded,
            ...rebuildDownloadedObj(localDownloaded)
          };
        }
        setDownloaded(localDownloaded);
        setIsLoading(false);
      })
      .catch(error => {
        if (!axios.isCancel(error)) {
          if (errorHandler) {
            errorHandler(error);
          }
          console.log(error);
        }
      });
    return () => {
      source.cancel("Cancelling in cleanup");
    };
  }, [config, errorHandler, rebuildDownloadedObj]);
  return isLoading ? (
    <PreloaderComponent />
  ) : (
    <Component {...downloaded} {...rest} />
  );
};

export default CompletePromiseThenRenderComponent;

const configObjectPropTypes = PropTypes.shape({
  urlToDownload: PropTypes.string.isRequired,
  whereToStore: PropTypes.string.isRequired,
  rebuildReceivedValue: PropTypes.func
});

CompletePromiseThenRenderComponent.defaultProps = {
  PreloaderComponent: CircularProgress,
  errorHandler: undefined,
  rebuildDownloadedObj: undefined
};

CompletePromiseThenRenderComponent.propTypes = {
  config: PropTypes.arrayOf(configObjectPropTypes).isRequired,
  Component: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.element,
    PropTypes.shape({})
  ]).isRequired,
  PreloaderComponent: PropTypes.func,
  errorHandler: PropTypes.func,
  rebuildDownloadedObj: PropTypes.func
};
