import { OpenInNew } from "@material-ui/icons";
import forms from "./form";
import { baseDictResponseRefactor } from "../../../index";
import { extractPayloadFromBaseSpringDateRestStructure } from "../../../../utils/commonFormFunctions";

const mvExchange = {
  navigationLabel: " ",
  requiresFunction: [
    {
      functionName: "administration_mv_modifier",
      allowedActions: ["create", "view", "edit"]
    },
    { functionName: "member_mv_view", allowedActions: ["view"] },
    {
      functionName: "member_mv_modifier",
      allowedActions: ["view"]
    }
  ],
  url: "/api/journalMvObjects",
  icon: OpenInNew,
  filtersPanel: {
    filters: [
      {
        label: "  ",
        name: "name",
        type: "text"
      },
      /* {
        label: "",
        name: "isFunction",
        type: "text"
      }, */
      {
        label: " ",
        name: "mainCode",
        type: "text"
      },
      {
        label: " ",
        name: "providerId",
        type: "select",
        dictUrl: "/api/mvProvider/search/present",
        rebuildReceivedOptions: data =>
          baseDictResponseRefactor(data).map(({ label, uniqueId }) => ({
            label,
            value: uniqueId
          }))
      },
      {
        label: "",
        name: "memberId",
        type: "select",
        dictUrl: "/api/structureMember/search/present",
        rebuildReceivedOptions: data => {
          const extractedData = extractPayloadFromBaseSpringDateRestStructure(
            data
          );
          return Array.isArray(extractedData)
            ? extractedData.map(({ uniqueId, fullName }) => ({
                label: fullName,
                value: uniqueId
              }))
            : [];
        }
      },
      {
        label: "",
        name: "directionId",
        type: "select",
        dictUrl: "/api/mvDirection/search/present",
        rebuildReceivedOptions: data =>
          baseDictResponseRefactor(data).map(({ label, uniqueId }) => ({
            label,
            value: uniqueId
          }))
      }
    ]
  },
  tableHeader: [
    {
      label: "  ",
      sortable: true,
      dynamicWidth: true,
      path: "name"
    },
    /* {
      label: "",
      sortable: true,
      dynamicWidth: true,
      path: "isFunction"
    }, */
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "mainCode"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "provider"
    },
    {
      label: "",
      sortable: true,
      dynamicWidth: true,
      path: "member"
    },
    {
      label: "",
      sortable: true,
      dynamicWidth: true,
      path: "direction"
    }
  ],
  forms: { ...forms }
};
export default mvExchange;
