import { constructRebuildReceivedOptionsLabelFullNameValueUniqueId } from "../../../rebuildRecieved";
import { getAuth } from "../../../index";
import { extractPayloadFromBaseSpringDateRestStructure } from "../../../../utils/commonFormFunctions";

export const report_1 = {
  blocks: [
    {
      fields: [
        {
          name: "dateRecording",
          label: "  ",
          type: "date-range"
        },
        {
          name: "memberId",
          label: "",
          type: "select",
          dictUrl: "/api/structureMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
          readOnly: userFunctions =>
            !userFunctions.some(x => x === "administration_statistics"),
          defaultValue: userFunctions => {
            if (!userFunctions.some(x => x === "administration_statistics")) {
              const { memberFullName, memberId } = getAuth();
              return { label: memberFullName, value: memberId };
            }
            return null;
          },
          widths: { md: 8, lg: 8, xl: 8 }
        }
      ]
    }
  ]
};
