import React, { useState } from "react";
import { connect } from "react-redux";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  LinearProgress
} from "@material-ui/core";
import * as PropTypes from "prop-types";
import { cancelButton } from "./modalWrapper/buttons";
import { CLOSE_CONFIRM_DIALOG_FORMIK } from "../constants";
import { FormikMUI } from "./formikMUi/formikNewRealisation";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "./completePromiseThenRenderComponent";
import { findFieldsWithDictThenConstructArrayToDownload } from "../utils/commonFormFunctions";
import { defaultErrorHandler } from "../utils/defaultErrorHandler";

const ConfirmDialogFormik = props => {
  const {
    confirmDialog: {
      title,
      open,
      initialValues,
      primaryButtonHandler,
      formObj,
      primaryButtonText,
      additionalStatusProps
    },
    closeDialog
  } = props;
  let refToFormSubmitFunc = null;
  const [isSubmitting, setIsSubmitting] = useState(false);
  return (
    <Dialog
      open={open}
      maxWidth="sm"
      fullWidth
      disableBackdropClick
      disableEscapeKeyDown
    >
      <DialogTitle>{title}</DialogTitle>
      <DialogContent>
        <CompletePromiseThenRenderComponent
          config={findFieldsWithDictThenConstructArrayToDownload(formObj)}
          Component={FormikMUI}
          errorHandler={defaultErrorHandler}
          PreloaderComponent={defaultPreloader}
          initialValues={initialValues}
          formObj={formObj}
          onSubmit={primaryButtonHandler}
          bindFormSubmit={ref => {
            refToFormSubmitFunc = ref;
          }}
          additionalStatusProps={additionalStatusProps}
          bindIsSubmitting={setIsSubmitting}
        />
        {isSubmitting && <LinearProgress />}
      </DialogContent>
      <DialogActions>
        {cancelButton(closeDialog)}
        <Button
          variant="contained"
          color="primary"
          disabled={isSubmitting}
          onClick={() => refToFormSubmitFunc()}
        >
          {primaryButtonText}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

ConfirmDialogFormik.propTypes = {
  confirmDialog: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    formObj: PropTypes.shape().isRequired,
    primaryButtonText: PropTypes.string.isRequired,
    initialValues: PropTypes.shape().isRequired,
    title: PropTypes.string.isRequired,
    primaryButtonHandler: PropTypes.func.isRequired
  }).isRequired,
  closeDialog: PropTypes.func.isRequired
};
const mapStateToProps = state => ({
  confirmDialog: state.wrapperReducer.confirmDialogFormik
});
const mapDispatchToProps = dispatch => ({
  closeDialog: () => dispatch({ type: CLOSE_CONFIRM_DIALOG_FORMIK })
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ConfirmDialogFormik);
