import HtmlToReact from "html-to-react";

const HtmlToReactParser = new HtmlToReact.Parser();

const getRegEx = search => new RegExp(search, "g");

const ReconstructDescription = ({ htmlInput }) =>
  HtmlToReactParser.parse(
    htmlInput.replace(getRegEx("&lt;"), "<").replace(getRegEx("&gt;"), ">")
  );

export default ReconstructDescription;
