import React, { useEffect } from "react";
import * as PropTypes from "prop-types";
import classNames from "classnames";
import { connect } from "react-redux";
import { Grid, Divider, CssBaseline, Drawer } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import axios from "axios";
import CustomSnackbar from "../snackBar";
import {
  dispatchPushModal,
  POP_HEAD_MODAL,
  RESET_COMPUTED_FILTERS,
  RESET_SORTABLE,
  TOGGLE_IS_COLLAPSED
} from "../../constants";
import CollapsableMenuContainer from "../CollapsableMenuContainer";
import ConfirmDialog from "../confirmDialog";
import ConfirmDialogFormik from "../confirmDialogFormik";
import CustomAppBar from "../appBar/AppBar";
import { customRouteParametersHandler } from "../modalWrapper/utils/customRouteParametersHandler";
import { GetJournalMainContent } from "./getJournalMainContent";
import {
  computeUrlThenFetchJournalData,
  loadFiltersOptions,
  setBaseUrl,
  setFilterPanelOptionsMap,
  setPage
} from "../../actions";
import {
  findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks,
  getOptionsMapBeforeLoading
} from "../../utils/commonFormFunctions";
import { INITIAL_PARAMETERS } from "../../constants/intialParameters";
import { ScheduleCreationMaster } from "../schedule/dialogs/scheduleCreationMaster";

const getStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: "789px"
  },
  grow: {
    flexGrow: 1
  },
  appBarShift: {
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  hide: {
    display: "none"
  },
  drawerPaper: {
    position: "relative",
    whiteSpace: "nowrap",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    }),
    overflowX: "hidden"
  },
  drawerPaperClose: {
    overflowX: "hidden",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    width: theme.spacing.unit * 5,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing.unit * 8
    }
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 4px",
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    height: "calc(100vh - 64px)",
    padding: theme.spacing.unit
  }
}));

const Wrapper = ({
  match: {
    params: { category, page, uniqueId }
  },
  isMenuMinimized,
  confirmDialogIsOpen,
  confirmDialogFormikIsOpen,
  allowedActions,
  resetFilterPanel,
  toggleFilterPanel,
  resetSortable,
  setPage,
  setBaseUrl,
  computeUrlThenFetchJournalDataW,
  loadFiltersOptionsMap,
  setFiltersPanelOptionsMap,
  scheduleCreationMasterIsOpen
}) => {
  const classes = getStyles();
  let initialParameters;
  if (INITIAL_PARAMETERS[category]) {
    if (
      INITIAL_PARAMETERS[category].pages &&
      INITIAL_PARAMETERS[category].pages[page]
    ) {
      initialParameters = INITIAL_PARAMETERS[category].pages[page];
    } else {
      initialParameters = INITIAL_PARAMETERS[category].page;
    }
  }

  //           ,     Url   
  useEffect(() => {
    resetFilterPanel();
    toggleFilterPanel(true);
    resetSortable();
    setPage(1);
    if (initialParameters && initialParameters.url) {
      setFiltersPanelOptionsMap(
        getOptionsMapBeforeLoading(initialParameters.filtersPanel.filters)
      );
      //     options
      const filtersSource = axios.CancelToken.source();
      loadFiltersOptionsMap(
        findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks({
          fields: initialParameters.filtersPanel.filters
        }),
        filtersSource
      );
      setBaseUrl(initialParameters.url);
      computeUrlThenFetchJournalDataW();
      return () => {
        filtersSource.cancel("Cancelling in cleanup");
      };
    }
  }, [
    category,
    computeUrlThenFetchJournalDataW,
    initialParameters,
    loadFiltersOptionsMap,
    page,
    resetFilterPanel,
    resetSortable,
    setBaseUrl,
    setFiltersPanelOptionsMap,
    setPage,
    toggleFilterPanel
  ]);

  //      UniqueId
  useEffect(() => {
    if (uniqueId) {
      customRouteParametersHandler(uniqueId, initialParameters.url)
        .then(data => {
          if (typeof data.uniqueId === "undefined" || data.uniqueId === null) {
            throw new Error(
              "    : uniqueId"
            );
          }
          const resultingModal = { category, page, mode: "view", item: data };
          dispatchPushModal(resultingModal);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }, [category, initialParameters.url, page, uniqueId]);

  return (
    <Grid container direction="column">
      <CssBaseline />
      <Grid item>
        <CustomAppBar />
      </Grid>
      <Grid
        container
        direction="row"
        wrap="nowrap"
        style={{ height: "calc(100vh - 80px)" }}
      >
        <Grid item>
          <Drawer
            style={{
              height: "100%",
              margin: "8px 0px 8px 8px",
              boxShadow:
                "0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12)"
            }}
            variant="permanent"
            classes={{
              paper: classNames(
                classes.drawerPaper,
                !isMenuMinimized && classes.drawerPaperClose
              )
            }}
            open={isMenuMinimized}
            elevation={8}
          >
            <CollapsableMenuContainer isMenuMinimized={isMenuMinimized} />
            <Divider />
          </Drawer>
        </Grid>
        <Grid item className={classes.content}>
          <GetJournalMainContent
            allowedActions={allowedActions}
            initialParameters={initialParameters}
            category={category}
            page={page}
          />
        </Grid>
      </Grid>
      <CustomSnackbar />
      {confirmDialogIsOpen && <ConfirmDialog />}
      {confirmDialogFormikIsOpen && <ConfirmDialogFormik />}
      {scheduleCreationMasterIsOpen && <ScheduleCreationMaster />}
    </Grid>
  );
};

Wrapper.propTypes = {
  match: PropTypes.shape({}).isRequired,
  history: PropTypes.shape({}).isRequired,
  isMenuMinimized: PropTypes.bool.isRequired,
  resetFilterPanel: PropTypes.func.isRequired,
  toggleFilterPanel: PropTypes.func.isRequired,
  resetSortable: PropTypes.func.isRequired,
  confirmDialogIsOpen: PropTypes.bool.isRequired,
  confirmDialogFormikIsOpen: PropTypes.bool.isRequired,
  allowedActions: PropTypes.shape({}).isRequired,
  setPage: PropTypes.func.isRequired,
  setBaseUrl: PropTypes.func.isRequired,
  loadFiltersOptionsMap: PropTypes.func.isRequired,
  setFiltersPanelOptionsMap: PropTypes.func.isRequired,
  scheduleCreationMasterIsOpen: PropTypes.bool.isRequired
};

const mapStateToProps = state => ({
  isMenuMinimized: state.collapsableMenuReducer.isMenuMinimized,
  expandedIndex: state.collapsableMenuReducer.expandedIndex,
  value: state.collapsableMenuReducer.value,
  confirmDialogIsOpen: state.wrapperReducer.confirmDialog.open,
  confirmDialogFormikIsOpen: state.wrapperReducer.confirmDialogFormik.open,
  modalIsShowed: state.journalReducer.modalState.isModalShowed,
  openedModals: state.journalReducer.modalState.openedModals,
  allowedActions: state.wrapperReducer.allowedActions,
  scheduleCreationMasterIsOpen:
    state.wrapperReducer.scheduleCreationMasterIsOpen
});

const mapDispatchToProps = dispatch => ({
  toggleFilterPanel: isCollapsed =>
    dispatch({ type: TOGGLE_IS_COLLAPSED, payload: { isCollapsed } }),
  resetFilterPanel: () => dispatch({ type: RESET_COMPUTED_FILTERS }),
  resetSortable: () => dispatch({ type: RESET_SORTABLE }),
  popHeadModal: () => dispatch({ type: POP_HEAD_MODAL }),
  setPage: page => dispatch(setPage(page)),
  setBaseUrl: url => dispatch(setBaseUrl(url)),
  computeUrlThenFetchJournalDataW: () =>
    dispatch(computeUrlThenFetchJournalData()),
  loadFiltersOptionsMap: (config, source) =>
    dispatch(loadFiltersOptions(config, source)),
  setFiltersPanelOptionsMap: optionsMap =>
    dispatch(setFilterPanelOptionsMap(optionsMap))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Wrapper);
