import { set } from "lodash";
import { baseDictRebuildReceivedOptions } from "../constants";

export const doSomethingWithFields = (forms, formName, functionToExecute) => {
  forms[formName].blocks.forEach(block =>
    block.fields.forEach(field => {
      functionToExecute(field);
    })
  );
};

export const getAllowedActionsOnPageForCurrentUser = (
  requiresFunction,
  functions
) => {
  const actionsSet = [];
  requiresFunction
    .filter(
      ({ functionName }) =>
        functions.includes(functionName) || functionName === "any"
    )
    .forEach(({ allowedActions }) =>
      allowedActions.forEach(action => {
        if (!actionsSet.includes(action)) {
          actionsSet.push(action);
        }
      })
    );
  return actionsSet;
};

const getAllAllwedActionForCategory = (pages, functions) => {
  const pageKeys = Object.keys(pages);
  const result = {};
  pageKeys.forEach(page => {
    const allAllowedActionsOnPage = getAllowedActionsOnPageForCurrentUser(
      pages[page].requiresFunction,
      functions
    );
    if (allAllowedActionsOnPage.length > 0) {
      result[page] = allAllowedActionsOnPage;
    }
  });
  return result;
};

export const constructAllowedActionsForAllJournals = (
  functions,
  initParams
) => {
  const result = {};
  const categories = Object.keys(initParams);
  categories.forEach(category => {
    //           

    if (initParams[category].pages) {
      const allowedActionsForCategory = getAllAllwedActionForCategory(
        initParams[category].pages,
        functions
      );
      if (Object.keys(allowedActionsForCategory).length > 0) {
        result[category] = allowedActionsForCategory;
      }
    } else {
      const allowedActions = getAllowedActionsOnPageForCurrentUser(
        initParams[category].page.requiresFunction,
        functions
      );
      if (allowedActions.length > 0) {
        result[category] = allowedActions;
      }
    }
  });
  return result;
};

const naturalOrder = ({ label: a }, { label: b }) => {
  if (a > b) {
    return 1;
  }
  if (a < b) {
    return -1;
  }
  // a    b
  return 0;
};

const wrapBaseDictReceivedReceivedOptions = data => ({
  options: baseDictRebuildReceivedOptions(data).sort(naturalOrder),
  loaded: true
});

const wrappedRebuildReceivedOptions = rebuildReceivedOptions => data => ({
  options: rebuildReceivedOptions(data).sort(naturalOrder),
  loaded: true
});

export const findFieldsWithDictThenConstructArrayToDownload = ({ blocks }) => {
  const configArray = [];
  blocks.forEach(({ fields }) =>
    fields.forEach(({ type, name, dictUrl, rebuildReceivedOptions }) => {
      if (type === "select" && dictUrl) {
        configArray.push({
          whereToStore: `optionsMap.${name}`,
          urlToDownload: dictUrl,
          rebuildReceivedValue: rebuildReceivedOptions
            ? wrappedRebuildReceivedOptions(rebuildReceivedOptions)
            : wrapBaseDictReceivedReceivedOptions
        });
      }
    })
  );
  return configArray;
};

export const findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks = ({
  fields
}) => {
  let configArray = [];
  fields.forEach(field => {
    const { type, name, dictUrl, rebuildReceivedOptions } = field;
    if (type === "select" && dictUrl) {
      configArray.push({
        whereToStore: `optionsMap.${name}`,
        urlToDownload: dictUrl,
        rebuildReceivedValue: rebuildReceivedOptions
          ? wrappedRebuildReceivedOptions(rebuildReceivedOptions)
          : wrapBaseDictReceivedReceivedOptions
      });
    }
    if (type === "fieldArray") {
      configArray = [
        ...configArray,
        ...findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks(field)
      ];
    }
  });
  return configArray;
};

export const extractPayloadFromBaseSpringDateRestStructure = data =>
  data._embedded ? Object.values(data._embedded)[0] : [];

export const getOptionsMapBeforeLoading = fields => {
  const optionMap = {};
  fields.forEach(field => {
    const { type, name, dictUrl } = field;
    if (type === "select" && dictUrl) {
      set(optionMap, name, { loaded: false, options: [] });
    }
  });
  return optionMap;
};
