import { createStandardAction } from "typesafe-actions";
import { OPEN_CONFIRM_DIALOG_FORMIK, TOGGLE_SNACKBAR } from "../constants";
import { ActionType } from "../components/schedule/tableReducer";

export const openConfirmDialogFormik = (payload: any): ActionType => ({
  type: OPEN_CONFIRM_DIALOG_FORMIK,
  payload: { open: true, ...payload }
});

export const openSnackBar = createStandardAction(TOGGLE_SNACKBAR).map(
  (message: string) => ({
    payload: { message, isSnackBarOpened: true }
  })
);
