import React from "react";
import * as PropTypes from "prop-types";
import Grid from "@material-ui/core/Grid/Grid";
import Paper from "@material-ui/core/Paper/Paper";
import { Form, Formik } from "formik";
import { connect } from "react-redux";
import { makeStyles } from "@material-ui/styles";
import { isEqual } from "lodash";
import { SET_COMPUTED_FILTERS } from "../../constants";
import ShadowedOutlinedButton from "../ShadowedOutlinedButton";
import { calcFieldWidth } from "../formikMUi/utils/calcFieldWidth";
import { constructInitialValuesForEmptyFormWithOutBlocks } from "../formikMUi/utils/initialValuesUtils";
import { renderField } from "../formikMUi/renderField";

const getStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing.unit
  },
  input: {
    display: "none"
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  panel: { marginBottom: theme.spacing.unit, padding: theme.spacing.unit },
  field: {
    width: "100%"
  }
}));

//  redux          ,   
const getInitialValues = (computedFilters, filters) =>
  Object.keys(computedFilters).length > 0
    ? computedFilters
    : constructInitialValuesForEmptyFormWithOutBlocks({ fields: filters });

const FilterPanel = props => {
  const {
    filters,
    onApplyFiltersClick,
    saveFilters,
    isCollapsed,
    computedFilters,
    optionsMap
  } = props;
  const classes = getStyles();
  return (
    !isCollapsed && (
      <Paper square elevation={5} className={classes.panel}>
        <Formik
          enableReinitialize
          initialValues={getInitialValues(computedFilters, filters)}
          onSubmit={(values, { setSubmitting }) => {
            saveFilters(values);
            onApplyFiltersClick();
            setSubmitting(false);
          }}
          initialStatus={{
            optionsMap
          }}
          render={({
            isSubmitting,
            handleSubmit,
            status,
            initialValues,
            setFieldValue,
            setStatus
          }) => {
            if (!isEqual(optionsMap, status.optionsMap)) {
              setStatus({ optionsMap });
            }
            return (
              <Form>
                <Grid container spacing={8}>
                  {filters.map(filter => (
                    <Grid key={filter.name} item {...calcFieldWidth(filter)}>
                      {renderField({
                        field: filter,
                        classes,
                        readOnly: false,
                        initialValues,
                        setFieldValue,
                        status
                      })}
                    </Grid>
                  ))}

                  {/*    */}
                  <Grid
                    item
                    style={{ alignSelf: "flex-end", marginLeft: "auto" }}
                  >
                    <Grid container spacing={8}>
                      <Grid item>
                        <ShadowedOutlinedButton
                          variant="outlined"
                          onClick={() => {
                            saveFilters({});
                            handleSubmit();
                          }}
                          disabled={isSubmitting}
                        >
                          
                        </ShadowedOutlinedButton>
                      </Grid>
                      <Grid item>
                        <ShadowedOutlinedButton
                          type="submit"
                          variant="outlined"
                          disabled={isSubmitting}
                          color="primary"
                        >
                          
                        </ShadowedOutlinedButton>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Form>
            );
          }}
        />
      </Paper>
    )
  );
};

FilterPanel.defaultProps = {
  optionsMap: {}
};

FilterPanel.propTypes = {
  onApplyFiltersClick: PropTypes.func.isRequired,
  filters: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  saveFilters: PropTypes.func.isRequired,
  optionsMap: PropTypes.shape({})
};
const mapStateToProps = state => ({
  isCollapsed: state.filtersReducer.isCollapsed,
  computedFilters: state.filtersReducer.computedFilters,
  optionsMap: state.filtersReducer.optionsMap
});

const mapDispatchToProps = dispatch => ({
  saveFilters: values =>
    dispatch({ type: SET_COMPUTED_FILTERS, payload: values })
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FilterPanel);
