import { computePropsForFormikMUI } from "../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { fullWidth, halfWith } from "../../widths";
import {
  constructRebuildReceivedOptionsLabelLabelValueUniqueId,
  rebuildReceivedValueLabelLabelValueUniqueId
} from "../../rebuildRecieved";
import { baseTextAreaProps } from "../../baseFieldProps";
import {
  disableConfirmDialogPrimaryBtn,
  dispatchFetchJournalData,
  dispatchOpenConfirmDialogFormik,
  dispatchOpenSnackBar,
  dispatchPopHeadModal
} from "../../index";
import { store } from "../../../store";
import {
  closeConfirmDialog,
  closeConfirmDialogFormik,
  constructRequest,
  openConfirmDialog
} from "../../../actions";

const completeConfig = {
  title: " ",
  formObj: {
    blocks: [
      {
        fields: [
          {
            label: "",
            name: "comment",
            ...baseTextAreaProps
          }
        ]
      }
    ]
  },
  initialValues: {
    comment: ""
  },
  primaryButtonText: "",
  primaryButtonHandler: uniqueId => values =>
    constructRequest(
      {
        url: `/api/claim/confirm/${uniqueId}`,
        method: "post",
        data: values
      },
      ({ status }) => {
        disableConfirmDialogPrimaryBtn(true);
        if (status === 200) {
          dispatchOpenSnackBar(`  "${uniqueId}" `);
        }
        store.dispatch(closeConfirmDialogFormik());
        dispatchPopHeadModal();
        dispatchFetchJournalData();
      }
    )
};

const baseForm = {
  url: "/api/claim/getById",
  title: " ",
  titleItem: "",
  method: "get",
  headerText: "uniqueId",
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: " /",
          name: "commonAction",
          type: "select",
          dictUrl: "/api/getCommonActionForClaimList",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: " ",
          name: "claimDictStatus",
          type: "select",
          dictUrl: "/api/claimDictStatus/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: " ",
          name: "uniqueId",
          type: "text",
          widths: halfWith
        },
        {
          label: " ",
          name: "isHram",
          type: "checkbox",
          widths: halfWith
        },
        {
          label: "  ",
          name: "firstTimestamp",
          type: "text",
          widths: halfWith
        },
        {
          label: "  ",
          name: "endTimestamp",
          type: "text",
          widths: halfWith
        },
        {
          label: " ",
          name: "userFullName",
          type: "text",
          widths: fullWidth
        },
        {
          label: " ",
          name: "phone",
          type: "text",
          widths: halfWith
        },
        {
          label: "Email ",
          name: "email",
          type: "text",
          widths: halfWith
        },
        {
          label: " ",
          name: "address",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "description",
          ...baseTextAreaProps
        },
        {
          label: " ",
          name: "anketa",
          type: "text",
          widths: fullWidth
        },
        {
          label: "",
          name: "performer",
          type: "text",
          widths: halfWith
        },
        {
          label: "",
          name: "comment",
          ...baseTextAreaProps
        },
        {
          name: "isFree",
          hidden: true
        }
      ]
    }
  ],
  additionalActionButtonsDependedOnProps: [
    {
      text: "  ",
      variant: "contained",
      style: {
        backgroundColor: "#357a38",
        color: "white"
      },
      onClick: ({ firstFormInitialValues: { uniqueId } }) => {
        store.dispatch(
          openConfirmDialog({
            mainText: `     "${uniqueId}"?`,
            primaryButtonHandler: () => {
              disableConfirmDialogPrimaryBtn(true);
              constructRequest(
                {
                  url: `/api/claim/toWork/${uniqueId}`,
                  method: "post"
                },
                ({ status }) => {
                  disableConfirmDialogPrimaryBtn(true);
                  if (status === 200) {
                    dispatchOpenSnackBar(
                      `  "${uniqueId}"    `
                    );
                  }
                  store.dispatch(closeConfirmDialog());
                  dispatchPopHeadModal();
                  dispatchFetchJournalData();
                }
              );
            }
          })
        );
      },
      conditionallyRender: ({
        firstFormInitialValues: { claimDictStatus },
        modal: { mode }
      }) => claimDictStatus.enum === "NEW" && mode === "view"
    },
    {
      text: "",
      variant: "contained",
      style: {
        backgroundColor: "#357a38",
        color: "white"
      },
      onClick: ({ firstFormInitialValues: { uniqueId } }) => {
        dispatchOpenConfirmDialogFormik({
          ...completeConfig,
          primaryButtonHandler: completeConfig.primaryButtonHandler(uniqueId)
        });
      },
      conditionallyRender: ({
        firstFormInitialValues: { claimDictStatus },
        modal: { mode }
      }) => claimDictStatus.enum === "IN_PROGRESS" && mode === "view"
    },
    {
      text: "",
      variant: "contained",
      style: {
        backgroundColor: "#357a38",
        color: "white"
      },
      onClick: ({ firstFormInitialValues: { uniqueId } }) => {
        store.dispatch(
          openConfirmDialog({
            mainText: `   "${uniqueId}"?`,
            primaryButtonHandler: () => {
              disableConfirmDialogPrimaryBtn(true);
              constructRequest(
                {
                  url: `/api/claim/hide/${uniqueId}`,
                  method: "post"
                },
                ({ status }) => {
                  disableConfirmDialogPrimaryBtn(true);
                  if (status === 200) {
                    dispatchOpenSnackBar(
                      `  "${uniqueId}"  `
                    );
                  }
                  store.dispatch(closeConfirmDialog());
                  dispatchPopHeadModal();
                  dispatchFetchJournalData();
                }
              );
            }
          })
        );
      },
      conditionallyRender: ({
        firstFormInitialValues: { claimDictStatus },
        modal: { mode }
      }) => claimDictStatus.enum === "CLOSED" && mode === "view"
    },
    {
      text: " ",
      variant: "contained",
      style: {
        backgroundColor: "#357a38",
        color: "white"
      },
      onClick: ({ firstFormInitialValues: { uniqueId, userFullName } }) => {
        store.dispatch(
          openConfirmDialog({
            mainText: `  "${userFullName}"?`,
            primaryButtonHandler: () => {
              disableConfirmDialogPrimaryBtn(true);
              constructRequest(
                {
                  url: `/api/claim/block/${uniqueId}`,
                  method: "post"
                },
                ({ status }) => {
                  disableConfirmDialogPrimaryBtn(true);
                  if (status === 200) {
                    dispatchOpenSnackBar(
                      ` "${userFullName}" `
                    );
                  }
                  store.dispatch(closeConfirmDialog());
                  dispatchPopHeadModal();
                  dispatchFetchJournalData();
                }
              );
            }
          })
        );
      },
      conditionallyRender: ({
        firstFormInitialValues: { claimDictStatus },
        modal: { mode }
      }) => claimDictStatus.enum === "CLOSED" && mode === "view"
    },
    {
      text: "",
      variant: "contained",
      style: {
        backgroundColor: "#357a38",
        color: "white"
      },
      onClick: ({ firstFormInitialValues: { uniqueId } }) => {
        store.dispatch(
          openConfirmDialog({
            mainText: `   "${uniqueId}"?`,
            primaryButtonHandler: () => {
              disableConfirmDialogPrimaryBtn(true);
              constructRequest(
                {
                  url: `/api/claim/cancel/${uniqueId}`,
                  method: "post"
                },
                ({ status }) => {
                  disableConfirmDialogPrimaryBtn(true);
                  if (status === 200) {
                    dispatchOpenSnackBar(`  "${uniqueId}" `);
                  }
                  store.dispatch(closeConfirmDialog());
                  dispatchPopHeadModal();
                  dispatchFetchJournalData();
                }
              );
            }
          })
        );
      },
      conditionallyRender: ({
        firstFormInitialValues: { claimDictStatus },
        modal: { mode }
      }) => claimDictStatus.enum === "IN_PROGRESS" && mode === "view"
    }
  ]
};

const forms = {
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" })
};
export default forms;
