import React from "react";
import { TextField as MuiTextField } from "@material-ui/core";
import { fieldToTextField } from "formik-material-ui";

const strReducer = (accumulator, currentValue) => accumulator + currentValue;

const RegExpFilteredTextField = props => {
  const { regExp, maxLength, ...rest } = props;
  const {
    field: { name },
    form: { setFieldValue }
  } = props;
  return (
    <MuiTextField
      {...fieldToTextField(rest)}
      onChange={({ target: { value } }) => {
        let filteredValue = "";

        if (regExp && value && value !== "") {
          const matchedSymbols = value.match(regExp);
          if (matchedSymbols !== null) {
            filteredValue = matchedSymbols
              .slice(0, maxLength)
              .reduce(strReducer, "");
          }
        } else {
          filteredValue = value.slice(0, maxLength);
        }
        setFieldValue(name, filteredValue);
      }}
    />
  );
};

export default RegExpFilteredTextField;
