import {
  SET_ACCESS_TOKEN,
  SET_AUTH,
  SET_AUTHENTICATED,
  SET_LOGIN_ERROR_MESSAGE,
  SET_CURRENT_USER_OBJECT
} from "../constants";

const initialState = {
  accessToken: null,
  isAuthenticated: false,
  errorMessage: "",
  role: "",
  surnameInitials: "",
  functions: [],
  userId: -1,
  previousUserId: -1
};

export default function authReducer(state = initialState, action) {
  switch (action.type) {
    case SET_ACCESS_TOKEN: {
      //    
      return {
        ...state,
        accessToken: action.payload,
        errorMessage: ""
      };
    }
    case SET_AUTHENTICATED: {
      //     
      return {
        ...state,
        isAuthenticated: action.payload
      };
    }
    case SET_AUTH: {
      const { isAuthenticated, accessToken } = action.payload;
      return {
        ...state,
        isAuthenticated,
        accessToken
      };
    }
    case SET_CURRENT_USER_OBJECT: {
      const {
        role,
        functions,
        surnameInitials,
        userId,
        memberId,
        memberFullName
      } = action.payload;
      return {
        ...state,
        role,
        functions,
        surnameInitials,
        userId,
        memberId,
        memberFullName
      };
    }
    case SET_LOGIN_ERROR_MESSAGE: {
      return {
        ...state,
        errorMessage: action.payload.errorMessage
      };
    }
    default:
      return state;
  }
}
