import React from "react";
import axios from "axios";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "../../../../components/completePromiseThenRenderComponent";
import { computePropsForFormikMUI } from "../../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { callDefaultErrorHandlerThenPopModal } from "../../../index";
import { FormikMUI } from "../../../../components/formikMUi/formikNewRealisation";

const baseForm = {
  title: " ",
  url: "/api/mvLog",
  method: "post",
  headerText: "uuidReq",
  projection: "formMvLog",
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: " ",
          name: "uuidReq",
          type: "text",
          widths: { xs: 6, sm: 6, md: 6, lg: 6, xl: 6 }
        },
        {
          label: " ",
          name: "uuidOut",
          type: "text",
          widths: { xs: 6, sm: 6, md: 6, lg: 6, xl: 6 }
        },
        {
          label: " ",
          name: "serviceUuid",
          type: "text",
          widths: { xs: 6, sm: 6, md: 6, lg: 6, xl: 6 }
        },
        {
          type: "spaceFiller",
          widths: { xs: 6, sm: 6, md: 6, lg: 6, xl: 6 }
        },
        {
          label: "  ",
          name: "createdTimestamp",
          type: "text"
        },
        {
          type: "spaceFiller",
          widths: { xs: 4, sm: 4, md: 4, lg: 4, xl: 4 }
        },
        {
          label: "  ",
          name: "outTimestamp",
          type: "text"
        },
        {
          label: "",
          name: "consumer",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "",
          name: "provider",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: " ",
          name: "mvObject",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          },
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        },
        {
          label: "",
          name: "status",
          type: "text"
        },
        {
          label: " ",
          name: "errorCode",
          type: "text"
        },
        {
          label: " ",
          name: "errorText",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "  ",
          name: "xmlIn",
          type: "file",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          widths: { xs: 6, sm: 6, md: 6, lg: 6, xl: 6 }
        },
        {
          label: "  ",
          name: "xmlOut",
          type: "file",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          widths: { xs: 6, sm: 6, md: 6, lg: 6, xl: 6 }
        }
      ]
    }
  ],
  additionalTabs: [
    {
      tabTitle: "",
      tabContent: {
        Component: ({
          modal: {
            item: { uuid }
          }
        }) => (
          <CompletePromiseThenRenderComponent
            Component={FormikMUI}
            readOnly
            config={[
              {
                urlToDownload: `/api/claim/jsonMvClaimIn?uuid=${uuid}&mode=view`, // todo     view?     
                whereToStore: "formObj"
              }
            ]}
            variant="xmlToForm"
            PreloaderComponent={defaultPreloader}
            errorHandler={callDefaultErrorHandlerThenPopModal}
          />
        )
      }
    },
    {
      tabTitle: "",
      tabContent: {
        Component: ({
          modal: {
            item: { uuid }
          }
        }) => (
          <CompletePromiseThenRenderComponent
            Component={FormikMUI}
            readOnly
            config={[
              {
                urlToDownload: `/api/claim/jsonMvClaimOut?uuid=${uuid}`,
                whereToStore: "formObj"
              }
            ]}
            variant="xmlToForm"
            PreloaderComponent={defaultPreloader}
            errorHandler={callDefaultErrorHandlerThenPopModal}
          />
        )
      },
      conditionallyDisable: ({ firstFormInitialValues: { uuidOut } }) =>
        !uuidOut
    }
  ]
};

const forms = {
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" })
};

export default forms;
