import { computePropsForFormikMUI } from "../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { baseTextAreaProps } from "../../baseFieldProps";
import Yup from "../../yup";
import { fullWidth, halfWith } from "../../widths";
import { constructRebuildReceivedOptions } from "../../rebuildRecieved";

const baseForm = {
  title: "",
  titleItem: "",
  headerText: "label",
  url: "/api/libraryObject",
  projection: "formLibraryObject",
  method: "post",
  doBeforeSend: { select: ({ value }) => value },
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "type",
          type: "select",
          dictUrl: "/api/libraryDictType/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptions(
            ({ label, code }) => ({
              label,
              value: `/api/libraryDictType/${code}`
            })
          ),
          rebuildReceivedValue: ({ label, code }) => ({
            label,
            value: `/api/libraryDictType/${code}`
          }),
          widths: halfWith
        },
        {
          label: "",
          name: "authorRef",
          type: "text",
          widths: halfWith
        },
        {
          label: "",
          name: "label",
          type: "text",
          widths: fullWidth
        },
        {
          label: "   ",
          name: "url",
          type: "text",
          widths: fullWidth
        },
        {
          label: "",
          name: "description",
          ...baseTextAreaProps
        },
        {
          name: "isPublished",
          label: "",
          type: "checkbox"
        },
        {
          name: "file",
          label: "  ",
          type: "file",
          widths: fullWidth
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    label: Yup.string().required(),
    type: Yup.object()
      .nullable()
      .required()
  })
};

export default {
  create: computePropsForFormikMUI({ formObj: baseForm, mode: "create" }),
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" }),
  edit: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      method: "patch"
    },
    mode: "edit"
  })
};
