import { LibraryBooks } from "@material-ui/icons";
import forms from "./form";
import { baseDictResponseRefactor } from "../../../index";

const nsi = {
  navigationLabel: "",
  requiresFunction: [
    {
      functionName: "administration_nsi_modifier",
      allowedActions: ["create", "view", "edit"]
    },
    {
      functionName: "member_nsi_modifier",
      allowedActions: ["create", "view", "edit"]
    }
  ],
  title: " ",
  url: "/api/journalNsiMasters",
  icon: LibraryBooks,
  filtersPanel: {
    filters: [
      { label: " ", name: "label", type: "text" },
      { label: " ", name: "name", type: "text" },
      {
        label: "",
        name: "memberId",
        type: "select",
        dictUrl: "/api/structureMember/search/present",
        rebuildReceivedOptions: data =>
          baseDictResponseRefactor(data).map(({ uniqueId, fullName }) => ({
            label: fullName,
            value: uniqueId
          }))
      }
    ]
  },
  tableHeader: [
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "label",
      width: "30%"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "name",
      width: "20%"
    },
    {
      label: "",
      sortable: true,
      dynamicWidth: true,
      path: "member",
      width: "30%"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "createdTimestamp",
      width: "20%"
    }
  ],
  forms: { ...forms }
};

export default nsi;
