//  ,  access token   
axios.interceptors.request.use(
  config => {
    const token = store.getState().auth.accessToken;

    if (token != null) {
      return {
        ...config,
        headers: {
          ...config.headers,
          Authorization: `Bearer ${token}`
        }
      };
    }
    return config;
  },
  error => Promise.reject(error)
);

//  ,   HTTP    401     
axios.interceptors.response.use(
  response => response,
  error => {
    if (typeof error.response === "undefined") {
      return Promise.reject(error);
    }
    if (error.response.data.message === "invalid_token") {
      store.dispatch({
        type: SET_AUTH,
        payload: { isAuthenticated: false, accessToken: null }
      });
    }
    if (error.response.status === 401) {
      store.dispatch(setAuth(false, null));
    }

    // todo        
    //     
    if (error.response.status === 502) {
      customGlobalHistory.push("/error/  ");
    }

    //   dev     
    if (
      typeof error.response.data === "string" &&
      error.response.data.search("Proxy error: Could not proxy request") !== -1
    ) {
      customGlobalHistory.push("/error/  ");
    }
    return Promise.reject(error);
  }
);

