export const filtersReducer = (accumulator, currentValue) => {
  if (currentValue[1] !== "" && currentValue[1] !== null) {
    if (typeof currentValue[1] !== "object") {
      return `${accumulator}&${currentValue[0]}=${currentValue[1]}`;
    }
    if (Array.isArray(currentValue[1])) {
      let joo = accumulator;
      currentValue[1].forEach(item => {
        joo += `&${currentValue[0]}=${item}`;
      });
      return joo;
    }
    if (Object.keys(currentValue[1]).length > 0) {
      return `${accumulator}&${currentValue[0]}=${currentValue[1].value}`;
    }
  }
  return accumulator;
};

const sortsReducer = (accumulator, currentValue) =>
  `${accumulator}&s=${currentValue.path},${currentValue.value}`;

export function addFiltersToUrl(computedFilters, finalUrl) {
  const filtersToApply = Object.entries(computedFilters);
  let resultingUrl = finalUrl.includes("?") ? finalUrl : `${finalUrl}?`;
  if (filtersToApply.length > 0) {
    resultingUrl = filtersToApply.reduce(filtersReducer, resultingUrl);
  }
  return resultingUrl;
}

export const computeUrl = () => (dispatch, getState) => {
  const {
    sortable,
    pagination: { rowsPerPage, page },
    baseUrl
  } = getState().journalReducer;
  const { computedFilters } = getState().filtersReducer;

  //       
  let finalUrl = typeof baseUrl === "function" ? baseUrl() : baseUrl.slice();

  if (!finalUrl.includes("?")) {
    finalUrl += "?";
  }
  if (sortable.length > 0) {
    finalUrl = sortable.reduce(sortsReducer, finalUrl);
  }
  finalUrl = addFiltersToUrl(computedFilters, finalUrl);
  finalUrl += `&p=${page - 1}&l=${rowsPerPage}`;
  return Promise.resolve(
    dispatch({ type: SET_COMPUTED_URL, payload: finalUrl })
  );
};

