export const setActiveStep = (step, passProps) => ({
  type: SET_ACTIVE_STEP,
  payload: { activeStep: step, passProps }
});

export const incrementActiveStep = passProps => (dispatch, getState) => {
  const {
    stepperReducer: { activeStep }
  } = getState();
  dispatch(setActiveStep(activeStep + 1, passProps));
};

export const decrementActiveStep = passProps => (dispatch, getState) => {
  const {
    stepperReducer: { activeStep }
  } = getState();
  dispatch(setActiveStep(activeStep - 1, passProps));
};

export const popHeadModal = () => dispatch =>
  Promise.resolve(dispatch({ type: POP_HEAD_MODAL }));

export const pushHeadModal = modal => (dispatch, getState) => {
  if (
    getState().journalReducer.modalState.openedModals.some(
      x =>
        x.category === modal.category &&
        x.page === modal.page &&
        x.item.uniqueId === modal.item.uniqueId
    )
  ) {
    return dispatch(openSnackBar("    "));
  }
  return dispatch({ type: PUSH_MODAL, payload: modal });
};

export const popHeadModalThenPushModal = modal => dispatch =>
  dispatch(popHeadModal()).then(() => dispatch(pushHeadModal(modal)));

