export const constructRequest = (axiosConfig, handleResponse, handleError) => {
  axios(axiosConfig)
    .then(response => {
      if (handleResponse) {
        handleResponse(response);
      }
    })
    .catch(error => {
      if (handleError) {
        handleError(error);
      } else defaultErrorHandler(error);
    });
};

