import React from "react";
import * as PropTypes from "prop-types";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import LockIcon from "@material-ui/icons/LockOutlined";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { Redirect, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import Grid from "@material-ui/core/Grid";
import { hasIn } from "lodash";
import { makeStyles } from "@material-ui/styles";
import { authenticateUser } from "../actions";
import { dispatchUserAuthSetFunctions } from "../constants";
import { FormikMUI } from "./formikMUi/formikNewRealisation";
import { phoneMask } from "../constants/masks";
import { fullWidth } from "../constants/widths";
import Yup from "../constants/yup";

const getStyles = makeStyles(theme => ({
  layout: {
    width: "auto",
    display: "block", // Fix IE11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400
    }
  },
  paper: {
    marginTop: theme.spacing.unit * 6,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme
      .spacing.unit * 3}px`
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE11 issue.
    marginTop: theme.spacing.unit
  },
  submit: {
    marginTop: theme.spacing.unit * 3
  },
  icon: {
    height: "30px",
    marginTop: "-6px",
    marginBottom: "-6px",
    marginLeft: theme.spacing.unit
  },
  wrapper: {
    height: "100vh"
  }
}));

const getFirstOwnPropertyKey = o => Object.keys(o)[0];

const getFirstAllowedJournal = obj => {
  const firstCategoryKey = getFirstOwnPropertyKey(obj);
  const firstPageKey = Array.isArray(obj[firstCategoryKey])
    ? ""
    : getFirstOwnPropertyKey(obj[firstCategoryKey]);
  return `/journal/${firstCategoryKey}/${firstPageKey}`;
};

const formObj = {
  blocks: [
    {
      fields: [
        {
          name: "login",
          type: "formatted",
          label: "()",
          mask: phoneMask,
          widths: fullWidth
        },
        {
          name: "password",
          label: "",
          type: "password",
          widths: fullWidth
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    login: Yup.string().required(" "),
    password: Yup.string().required(" ")
  })
};

const SignIn = props => {
  const {
    isAuthenticated,
    allowedActions,
    errorMessage,
    onSubmitHandle,
    match
  } = props;

  const classes = getStyles();

  //       
  if (match) {
    const {
      params: { token }
    } = match;
    if (token) {
      dispatchUserAuthSetFunctions(token);
    }
  }

  if (isAuthenticated) {
    const { location } = props;
    let from = {};
    if (
      location &&
      location.state &&
      location.state.from &&
      location.state.from.pathname &&
      (location.state.from.pathname !== "/" ||
        location.state.from.pathname !== "")
    ) {
      const {
        state: {
          from: { pathname }
        }
      } = location;

      // todo           ,
      //             ,     
      //               .....
      from = hasIn(
        allowedActions,
        pathname.replace("/journal/", "").replace("/", ".")
      )
        ? location.state.from
        : { pathname: getFirstAllowedJournal(allowedActions) };
    } else {
      from = { pathname: getFirstAllowedJournal(allowedActions) };
    }
    return <Redirect to={from} />;
  }
  let submitRef = null;
  return (
    <React.Fragment>
      <CssBaseline />
      <Grid
        container
        alignItems="center"
        justify="center"
        className={classes.wrapper}
        direction="column"
      >
        <Grid item>
          <Grid
            container
            alignItems="center"
            justify="space-between"
            alignContent="center"
            direction="column"
          >
            <Grid>
              <img
                // todo  
                src="/logo_login.png"
                alt="logo"
                style={{
                  height: "100px",
                  color: "inherit"
                }}
              />
            </Grid>
            <Grid>
              <Typography
                // todo  
                align="center"
                variant="h6"
                style={{ width: "690px" }}
              >
                 
              </Typography>
            </Grid>
          </Grid>
        </Grid>

        <Grid item className={classes.layout}>
          <Paper className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockIcon />
            </Avatar>
            <Typography variant="h6">  </Typography>
            <FormikMUI
              style={{ paddingLeft: 0, paddingRight: 0 }}
              initialValues={{ login: "", password: "" }}
              formObj={formObj}
              onSubmit={(values, { setSubmitting }) => {
                onSubmitHandle(values);
              }}
              bindFormSubmit={ref => {
                submitRef = ref;
              }}
            />
            <Button
              fullWidth
              variant="contained"
              color="primary"
              onClick={() => {
                if (submitRef) {
                  submitRef();
                }
              }}
            >
              
            </Button>
            {<h3 style={{ color: "red" }}>{errorMessage}</h3>}
          </Paper>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated,
  errorMessage: state.auth.errorMessage,
  allowedActions: state.wrapperReducer.allowedActions
});

const mapDispatchToProps = dispatch => ({
  onSubmitHandle: ({ login, password }) =>
    dispatch(authenticateUser(login, password))
});

const Login = withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(SignIn)
);

SignIn.defaultProps = {
  errorMessage: ""
};

SignIn.propTypes = {
  onSubmitHandle: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool.isRequired,
  errorMessage: PropTypes.string,
  history: PropTypes.shape({}).isRequired,
  allowedActions: PropTypes.shape({}).isRequired
};

export default Login;
