import React from "react";
import * as PropTypes from "prop-types";
import { connect } from "react-redux";
import { NavLink, withRouter } from "react-router-dom";
import List from "@material-ui/core/List/List";
import ListItem from "@material-ui/core/ListItem/ListItem";
import Collapse from "@material-ui/core/Collapse/Collapse";
import ListItemIcon from "@material-ui/core/ListItemIcon/ListItemIcon";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import ListItemText from "@material-ui/core/ListItemText/ListItemText";
import Grid from "@material-ui/core/Grid/Grid";
import classNames from "classnames";
import { makeStyles } from "@material-ui/styles";
import { SET_EXPANDED_INDEX } from "../../constants";
import { INITIAL_PARAMETERS } from "../../constants/intialParameters";
import { RenderCollapsibleMenuItem } from "./RenderCollapsibleMenuItem";

const getStyles = makeStyles(theme => ({
  nested: {
    paddingLeft: theme.spacing.unit * 2
  },
  navlink: {
    textDecoration: "none"
  },
  navlinkActive: {
    borderLeft: `3px solid ${theme.palette.primary.main}`,
    display: "block",
    opacity: "0.9"
  },
  activeCategory: {
    backgroundColor: "rgba(0, 0, 0, 0.08)",
    color: "red"
  },
  firstLevelListItem: {
    paddingLeft: theme.spacing.unit,
    paddingRight: theme.spacing.unit
  },
  listIconRoot: {
    marginRight: 0
  },
  activeListItem: {
    color: theme.palette.primary
  }
}));

const getArrowSymbol = (index, expandedIndex, value) => {
  const isCollapsed = index === expandedIndex ? value : false;
  return isCollapsed ? <ExpandLess /> : <ExpandMore />;
};

/* function checkIfPageIsAllowed(page, functions) {
  return (
    !page.hidden &&
    page.requiresFunction &&
    page.requiresFunction.some(
      ({ functionName }) =>
        functions.includes(functionName) || functionName === "any"
    )
  );
}

const getAllowedMenuItems = (pages, functions) =>
  Object.keys(pages).filter(pageName =>
    checkIfPageIsAllowed(pages[pageName], functions)
  ); */

const CollapsableMenuContainer = ({
  onClick,
  expandedIndex,
  value,
  isMenuMinimized,
  match: {
    params: { category: activeCategory, page: activePage }
  },
  allowedActions
}) => {
  const classes = getStyles();
  return (
    <List disablePadding>
      {Object.keys(allowedActions)
        .filter(category => !INITIAL_PARAMETERS[category].hidden)
        .map((category, index) => {
          const CustomCategoryIcon = INITIAL_PARAMETERS[category].icon;
          if (!INITIAL_PARAMETERS[category].pages) {
            return (
              <NavLink
                key={category}
                to={`/journal/${category}/`}
                exact
                className={classes.navlink}
                activeClassName={classes.navlinkActive}
                aria-current="page"
              >
                <ListItem
                  className={classNames(classes.firstLevelListItem, {
                    [classes.activeCategory]: category === activeCategory
                  })}
                  divider
                  button
                >
                  <ListItemIcon
                    classes={{
                      root: classes.listIconRoot
                    }}
                  >
                    {CustomCategoryIcon ? (
                      <CustomCategoryIcon
                        color={
                          category === activeCategory ? "primary" : undefined
                        }
                      />
                    ) : (
                      <InboxIcon
                        color={
                          category === activeCategory ? "primary" : undefined
                        }
                      />
                    )}
                  </ListItemIcon>
                  {isMenuMinimized && (
                    <ListItemText
                      inset
                      primaryTypographyProps={{
                        color:
                          category === activeCategory ? "primary" : undefined
                      }}
                      primary={
                        <Grid container>
                          {INITIAL_PARAMETERS[category].label}
                        </Grid>
                      }
                    />
                  )}
                </ListItem>
              </NavLink>
            );
          }
          return (
            <React.Fragment key={category}>
              <ListItem
                className={classNames(classes.firstLevelListItem, {
                  [classes.activeCategory]: category === activeCategory
                })}
                divider
                button
                onClick={() => {
                  onClick(index);
                }}
              >
                <ListItemIcon
                  classes={{
                    root: classes.listIconRoot
                  }}
                >
                  {CustomCategoryIcon ? <CustomCategoryIcon /> : <InboxIcon />}
                </ListItemIcon>

                {isMenuMinimized && (
                  <ListItemText
                    inset
                    primary={
                      <Grid container>
                        {INITIAL_PARAMETERS[category].label}
                      </Grid>
                    }
                  />
                )}
                <ListItemIcon
                  classes={{
                    root: classes.listIconRoot
                  }}
                >
                  {getArrowSymbol(index, expandedIndex, value)}
                </ListItemIcon>
              </ListItem>

              <Collapse in={index === expandedIndex && value} unmountOnExit>
                <List disablePadding>
                  {Object.keys(allowedActions[category]).map(page => {
                    const CustomIcon =
                      INITIAL_PARAMETERS[category].pages[page].icon;
                    return (
                      <RenderCollapsibleMenuItem
                        category={category}
                        page={page}
                        classes={classes}
                        key={`${category}-${page}`}
                        customIcon={CustomIcon}
                        activeCategory={activeCategory}
                        activePage={activePage}
                        menuMinimized={isMenuMinimized}
                      />
                    );
                  })}
                </List>
              </Collapse>
            </React.Fragment>
          );
        })}
    </List>
  );
};

const mapStateToProps = state => ({
  expandedIndex: state.collapsableMenuReducer.expandedIndex,
  value: state.collapsableMenuReducer.value,
  functions: state.auth.functions,
  allowedActions: state.wrapperReducer.allowedActions
});

const mapDispatchToProps = dispatch => ({
  onClick: index =>
    dispatch({ type: SET_EXPANDED_INDEX, payload: { expandedIndex: index } })
});

CollapsableMenuContainer.propTypes = {
  onClick: PropTypes.func.isRequired,
  expandedIndex: PropTypes.number.isRequired,
  value: PropTypes.bool.isRequired,
  isMenuMinimized: PropTypes.bool.isRequired,
  functions: PropTypes.arrayOf(PropTypes.string).isRequired,
  allowedActions: PropTypes.shape({}).isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      category: PropTypes.string.isRequired
    })
  }).isRequired
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(CollapsableMenuContainer)
);
