import React from "react";
import { TextField } from "@material-ui/core";
import * as Proptypes from "prop-types";
import { checkIfFieldHasValidationError } from "../utils/checkIfFieldHasValidationError";

export const WrappedTextField = ({
  field,
  form: { touched, errors },
  multiline,
  ...props
}) => {
  const textFieldProps = checkIfFieldHasValidationError(
    field.name,
    touched,
    errors
  );
  return (
    <TextField
      {...field}
      {...props}
      {...textFieldProps}
      multiline={multiline}
      variant={multiline ? "outlined" : undefined}
      style={
        multiline
          ? {
              margin: "8px 0"
            }
          : undefined
      }
    />
  );
};

WrappedTextField.defaultProps = {
  multiline: false
};

WrappedTextField.propTypes = {
  field: Proptypes.shape({
    name: Proptypes.string.isRequired,
    onChange: Proptypes.func.isRequired
  }).isRequired,
  form: Proptypes.shape({
    errors: Proptypes.shape({}).isRequired,
    touched: Proptypes.shape({}).isRequired
  }).isRequired,
  multiline: Proptypes.bool
};
