import React from "react";
import Yup from "../../../yup";
import { computePropsForFormikMUI } from "../../../../components/modalWrapper/utils/computePropsForForMikMUI";
import {
  extractPayloadFromBaseSpringDateRestStructure,
  findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks
} from "../../../../utils/commonFormFunctions";
import { constructRequest } from "../../../../actions";
import { PrimaryButton } from "../../../../components/modalWrapper/buttons";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "../../../../components/completePromiseThenRenderComponent";
import { defaultErrorHandler } from "../../../../utils/defaultErrorHandler";
import { baseDictResponseRefactor, dispatchPopHeadModal } from "../../../index";
import { FormikMUI } from "../../../../components/formikMUi/formikNewRealisation";

const confObj = {
  fields: [
    {
      name: "nsiSlaves",
      label: "nsiSlaves",
      type: "fieldArray",
      fields: [
        {
          name: "uniqueId",
          label: "",
          type: "text",
          hidden: true
        },
        {
          name: "label",
          label: " ",
          type: "text",
          uniqueCheck: {
            url: "/api/nsiSlave/search/existByNsiMasterAndLabel",
            constructParams: ({ nsiMasterId }, value) => ({
              value,
              nsiMasterId
            })
          }
        },
        {
          name: "value",
          label: " ",
          type: "text",
          uniqueCheck: {
            url: "/api/nsiSlave/search/existByNsiMasterAndValue",
            constructParams: ({ nsiMasterId }, value) => ({
              nsiMasterId,
              value
            })
          }
        },
        {
          name: "isDeleted",
          label: "isDeleted",
          type: "checkbox",
          hidden: true
        }
      ]
    },
    {
      name: "nsiMasterId",
      label: "nsiMasterId",
      type: "text",
      hidden: true
    }
  ],
  validationSchema: Yup.object({
    nsiSlaves: Yup.array()
      .of(
        Yup.object({
          value: Yup.string().when("isDeleted", {
            is: true,
            then: Yup.string(),
            otherwise: Yup.string().required()
          }),
          label: Yup.string().when("isDeleted", {
            is: true,
            then: Yup.string(),
            otherwise: Yup.string().required()
          }),
          isDeleted: Yup.bool(),
          uniqueId: Yup.string()
        })
      )
      .min(1)
  })
};

const baseForm = {
  title: "",
  titleItem: "",
  url: "/api/nsiMaster",
  projection: "formNsiMaster",
  method: "post",
  headerText: "label",
  doBeforeSend: {
    select: ({ value }) => value
  },
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          name: "uniqueId",
          label: "uniqueId",
          type: "text",
          hidden: true
        },
        {
          name: "label",
          label: "",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          name: "name",
          label: "",
          type: "text",
          uniqueCheck: {
            url: "/api/nsiMaster/search/existsByName",
            constructParams: (v, value) => ({
              name: value
            })
          },
          widths: { xs: 4, sm: 4, md: 4, lg: 4, xl: 4 }
        },
        {
          name: "description",
          label: "",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "",
          name: "member",
          type: "select",
          dictUrl: "/api/structureMember/search/present",
          rebuildReceivedOptions: data => {
            const extractedData = extractPayloadFromBaseSpringDateRestStructure(
              data
            );
            return Array.isArray(extractedData)
              ? extractedData.map(({ uniqueId, fullName }) => ({
                  label: fullName,
                  value: uniqueId
                }))
              : [];
          },
          rebuildReceivedValue: data => ({
            label: data.fullName,
            value: data.uniqueId
          }),
          widths: { md: 12, lg: 12, xl: 12 }
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    name: Yup.string().required(),
    label: Yup.string().required()
  }),
  additionalTabs: [
    {
      tabTitle: " ",
      tabContent: {
        Component: ({
          modal: {
            item: { uniqueId }
          },
          modal,
          ...rest
        }) => (
          <CompletePromiseThenRenderComponent
            config={[
              {
                urlToDownload: `/api/nsiSlave/search/presentByNsiMaster?nsiMasterId=${uniqueId}`,
                whereToStore: "rawInitialValues",
                postLoadDoSomethingThenPassResultAsProp: {
                  func: data => ({
                    nsiSlaves: baseDictResponseRefactor(data),
                    nsiMasterId: uniqueId
                  }),
                  passResultAs: "initialValues"
                }
              },
              ...findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks(
                confObj
              )
            ]}
            PreloaderComponent={defaultPreloader}
            Component={FormikMUI}
            errorHandler={defaultErrorHandler}
            {...rest}
          />
        ),
        passProps: {
          formObj: confObj,
          variant: "table"
        },
        functionToExecuteThenPassResultAsProps: ({
          modal: {
            item: { uniqueId },
            mode
          }
        }) => ({
          onSubmit: ({ nsiSlaves }, { setSubmitting }) => {
            constructRequest(
              {
                method: "post",
                url: "/api/nsi/nsiSlaves",
                data: nsiSlaves.map(item => ({
                  ...item,
                  nsiMaster: uniqueId
                }))
              },
              () => {
                dispatchPopHeadModal();
                setSubmitting(false);
              },
              () => setSubmitting(false)
            );
          },
          readOnly: mode !== "edit"
        })
      },
      tabButtons: [
        {
          Btn: PrimaryButton,
          requiresFormSubmitRef: true,
          conditionallyRender: ({ modal: { mode } }) => mode === "edit"
        }
      ]
    }
  ]
};

const forms = {
  create: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      method: "post",
      url: "/api/nsiMaster",
      primaryActionUrl: "/api/nsi/nsiMaster"
    },
    mode: "create"
  }),
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" }),
  edit: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      method: "patch",
      url: "/api/nsiMaster",
      primaryActionUrl: "/api/nsi/nsiMaster"
    },
    mode: "edit"
  })
};

export default forms;
