import React, { useContext, useRef } from "react";
import { TableCell, Theme } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { TableCellProps } from "react-virtualized";
import { ReactSelectMUI } from "../reactSelectMUI";
import { changeType } from "./tableReducer";
import { TableContext } from "./ScheduleTable";
import { EventType } from "./types";

const getStyles = makeStyles((theme: Theme) => ({
  tableCell: {
    flex: 1,
    flexDirection: "column"
  },
  TableCellRoot: {
    borderBottom: "none"
  },
  flexContainer: {
    display: "flex",
    alignItems: "center",
    boxSizing: "border-box"
  }
}));

export const EventTypeCell = ({
  cellData: events,
  rowIndex
}: TableCellProps) => {
  const classes = getStyles();
  const {
    optionsMap: {
      type: { loaded, options }
    },
    dispatch
  } = useContext(TableContext);
  const selectContainer = useRef(null);
  return (
    <TableCell
      classes={{ root: classes.TableCellRoot }}
      variant="body"
      component="div"
    >
      <input ref={selectContainer} type="text" style={{ display: "none" }} />
      {events.map(
        ({ type: eventType, uniqueId }: EventType, eventIndex: number) => {
          return (
            <div style={{ width: "250px" }} key={uniqueId}>
              <ReactSelectMUI
                options={options}
                placeholder=""
                value={eventType}
                label=""
                handleChange={v =>
                  dispatch(changeType(rowIndex, eventIndex, v))
                }
                isDisabled={!loaded}
                menuPortalTarget={selectContainer.current}
              />
            </div>
          );
        }
      )}
    </TableCell>
  );
};
