import React from "react";
import * as PropTypes from "prop-types";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import { connect } from "react-redux";
import { Snackbar } from "@material-ui/core";
import { TOGGLE_SNACKBAR } from "../constants";

const CustomSnackbar = props => {
  const { message, open, closeHandler } = props;
  return (
    <Snackbar
      open={open}
      autoHideDuration={6000}
      onClose={closeHandler}
      message={message}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "left"
      }}
      action={[
        <IconButton
          key="close"
          aria-label="Close"
          color="inherit"
          // className={classes.close}
          onClick={closeHandler}
        >
          <CloseIcon />
        </IconButton>
      ]}
    />
  );
};

CustomSnackbar.propTypes = {
  message: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  closeHandler: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  open: state.wrapperReducer.isSnackBarOpened,
  message: state.wrapperReducer.message
});

const mapDispatchToProps = dispatch => ({
  closeHandler: () =>
    dispatch({ type: TOGGLE_SNACKBAR, payload: { isSnackBarOpened: false } })
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CustomSnackbar);
