import React, { createContext } from "react";
import { Paper, Theme, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import {
  Column,
  Table,
  TableCellRenderer,
  TableHeaderRenderer,
  CellMeasurerCache,
  CellMeasurer,
  TableCellProps,
  Index,
  AutoSizer
} from "react-virtualized";
import classNames from "classnames";
import { isEqual } from "lodash";
import { DayCell } from "./dayCell";
import { MonthWordCell } from "./monthWordCell";
import { HeaderCell } from "./headerRenderer";
import { EventTimeCell } from "./eventTimeCell";
import { collectionType, IOption, ScheduleTableProps } from "./types";
import { EventTypeCell } from "./EventTypeCell";
import { EventPerformersCell } from "./eventPerformersCell";
import { DutiesCell } from "./dutiesCell";
import { ActionType } from "./tableReducer";
import { ActionButtons } from "./actionButtons";

import "react-virtualized/styles.css";

const getStyles = makeStyles((theme: Theme) => ({
  mainContainer: {
    height: "100%"
  },
  headerColumn: {
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.fontSize.toFixed(15)
  },
  table: {
    fontFamily: theme.typography.fontFamily
  },
  flexContainer: {
    display: "flex",
    alignItems: "center",
    boxSizing: "border-box"
  },
  tableRow: {
    // cursor: "pointer"
    borderBottom: "1px solid rgba(224, 224, 224, 1)"
  },
  tableRowHover: {
    "&:hover": {
      backgroundColor: theme.palette.grey[200]
    }
  },
  tableCell: {
    flex: 1,
    flexDirection: "column"
  },
  TableCellRoot: {
    borderBottom: "none"
  },
  noClick: {
    cursor: "initial"
  }
}));

const noRowsRenderer = () => (
  <Typography
    variant="h6"
    style={{
      width: "100%",
      height: "100%",
      display: "flex",
      alignItems: "center",
      justifyContent: "center"
    }}
  >
      
  </Typography>
);

const cache = new CellMeasurerCache({
  defaultWidth: 100,
  defaultHeight: 80,
  minHeight: 70,
  minWidth: 75,
  fixedHeight: false,
  fixedWidth: true
});

const DayCellRenderer: TableCellRenderer = ({ cellData }: TableCellProps) => (
  <DayCell text={cellData} />
);

const HeaderRenderer: TableHeaderRenderer = props => <HeaderCell {...props} />;

const MonthWordCellRenderer: TableCellRenderer = ({
  cellData
}: TableCellProps) => <MonthWordCell cellData={cellData} />;

const EventTimeCellRender: TableCellRenderer = props => (
  <EventTimeCell {...props} />
);

const EventTypeCellRenderer: TableCellRenderer = ({
  cellData,
  dataKey,
  parent,
  rowIndex,
  columnIndex,
  ...rest
}: TableCellProps) => (
  <EventTypeCell
    cellData={cellData}
    dataKey={dataKey}
    parent={parent}
    rowIndex={rowIndex}
    columnIndex={columnIndex}
    {...rest}
  />
);

const DutiesCellRenderer: TableCellRenderer = (props: TableCellProps) => (
  <DutiesCell {...props} />
);

const ActionButtonsRenderer: TableCellRenderer = ({
  cellData,
  dataKey,
  parent,
  rowIndex,
  columnIndex,
  ...rest
}: TableCellProps) => (
  <ActionButtons
    cellData={cellData}
    dataKey={dataKey}
    parent={parent}
    rowIndex={rowIndex}
    columnIndex={columnIndex}
    {...rest}
  />
);

const EventPerformersCellRenderer: TableCellRenderer = ({
  cellData,
  columnIndex,
  dataKey,
  parent,
  rowIndex,
  ...rest
}: TableCellProps) => (
  <EventPerformersCell
    cellData={cellData}
    columnIndex={columnIndex}
    dataKey={dataKey}
    parent={parent}
    rowIndex={rowIndex}
    {...rest}
  />
);
//  <CellMeasurer cache={cache} parent={parent}>  </CellMeasurer>

export type DispatchType = (action: ActionType) => void;

export type optionsMapType = {
  [x: string]: {
    loaded: boolean;
    options: Array<IOption>;
  };
};

export type TableContextProps = {
  dispatch: DispatchType;
  optionsMap: optionsMapType;
};

const initialTableContext: TableContextProps = {
  optionsMap: {
    duties: {
      loaded: false,
      options: []
    },
    performers: {
      loaded: false,
      options: []
    },
    types: {
      loaded: false,
      options: []
    }
  },
  dispatch: () => {}
};

export const TableContext = createContext(initialTableContext);

interface ReactResizeDetectorDimensions {
  height: number;
  width: number;
}

export const ScheduleTable = ({
  collection,
  dispatch,
  omitDutiesColumn = false,
  optionsMap
}: ScheduleTableProps) => {
  const classes = getStyles();
  const getRowClassName = classNames(classes.tableRow, classes.flexContainer);

  let lastCollection: collectionType = [];
  let lastRenderedWidth = 0;
  let lastRenderedHeight = 0;

  const rowGetter = ({ index }: Index) => collection[index];
  return (
    <TableContext.Provider value={{ optionsMap, dispatch }}>
      <Paper square className={classes.mainContainer} elevation={1}>
        <AutoSizer>
          {({ width, height }) => {
            if (
              lastRenderedWidth !== width ||
              lastRenderedHeight !== height ||
              !isEqual(collection, lastCollection)
            ) {
              lastRenderedWidth = width;
              lastRenderedHeight = height;
              lastCollection = collection;
              cache.clearAll();
            }
            return (
              <Table
                // deferredMeasurementCache={cache}
                className={classes.table}
                headerClassName={classes.headerColumn}
                headerHeight={50}
                height={height}
                noRowsRenderer={noRowsRenderer}
                overscanRowCount={10}
                rowClassName={getRowClassName}
                rowHeight={80}
                // rowHeight={cache.rowHeight}
                rowGetter={rowGetter}
                rowCount={collection.length}
                width={width}
              >
                <Column
                  dataKey="day"
                  label=""
                  width={100}
                  cellRenderer={DayCellRenderer}
                  headerRenderer={HeaderRenderer}
                />
                <Column
                  width={200}
                  label=""
                  dataKey="mothWord"
                  cellRenderer={MonthWordCellRenderer}
                  headerRenderer={HeaderRenderer}
                />
                {!omitDutiesColumn && (
                  <Column
                    width={200}
                    label=""
                    dataKey="duties"
                    cellRenderer={DutiesCellRenderer}
                    headerRenderer={HeaderRenderer}
                  />
                )}
                <Column
                  dataKey="events"
                  label=""
                  width={170}
                  cellRenderer={EventTimeCellRender}
                  headerRenderer={HeaderRenderer}
                />
                <Column
                  dataKey="events"
                  label=""
                  width={200}
                  cellRenderer={EventTypeCellRenderer}
                  flexGrow={1}
                  headerRenderer={HeaderRenderer}
                />
                <Column
                  dataKey="events"
                  label=""
                  width={250}
                  cellRenderer={EventPerformersCellRenderer}
                  flexGrow={1}
                  headerRenderer={HeaderRenderer}
                />
                <Column
                  dataKey="events"
                  width={150}
                  cellRenderer={ActionButtonsRenderer}
                />
              </Table>
            );
          }}
        </AutoSizer>
      </Paper>
    </TableContext.Provider>
  );
};
