import React from "react";
import { Router, Route, Switch } from "react-router-dom";
import { Provider } from "react-redux";
// todo delete me    MUI  4- 
import "../initStylesMUI";
import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import { PersistGate } from "redux-persist/integration/react";
import { indigo } from "@material-ui/core/colors";
import Login from "./login";
import Wrapper from "./wrapper";
import PrivateRoute from "./PrivateRoute";
import NoMatch from "./no-match";
import { store, persistor } from "../store";
import customGlobalHistory from "../customGlobalHistory";

const theme = createMuiTheme({
  palette: {
    primary: indigo,
    // secondary: { main: "#484848" }
    secondary: { main: "#890706" }
  },
  typography: {
    useNextVariants: true
  }
});

const App = () => (
  <Provider store={store}>
    <PersistGate loading={null} persistor={persistor}>
      <ThemeProvider theme={theme}>
        <Router history={customGlobalHistory}>
          <Switch>
            <Route path="/" exact component={Login} />
            <PrivateRoute
              path={[
                "/journal/:category",
                "/journal/:category/:uniqueId([/\\d]+)",
                "/journal/:category/:page([a-z]+)",
                "/journal/:category/:page/:uniqueId([/\\d]+)"
              ]}
              exact
              component={Wrapper}
            />
            <Route path="/esia/:token" component={Login} exact />
            <Route path="/error/:errorMessage" component={NoMatch} />
            <Route component={NoMatch} />
          </Switch>
        </Router>
      </ThemeProvider>
    </PersistGate>
  </Provider>
);

export default App;
