export const baseDictResponseRefactor = data =>
  Object.values(data._embedded)[0];

export const baseDictRebuildReceivedOption = ({ label, uniqueId }) => ({
  label,
  value: uniqueId
});

export const baseDictRebuildReceivedOptions = data =>
  baseDictResponseRefactor(data).map(({ label, uniqueId }) => ({
    label,
    value: uniqueId
  }));

export const allowedActions = () =>
  store.getState().wrapperReducer.allowedActions;

export const getAuth = () => store.getState().auth;

export const getUserFunctions = () => {
  return store.getState().auth.functions;
};

export const computedUrl = () => store.getState().journalReducer.computedUrl;

export const disableConfirmDialogPrimaryBtn = value =>
  store.dispatch({
    type: DISABLE_PRIMARY_BTN_CONFIRM_DIALOG,
    payload: { primaryButtonDisabled: value }
  });

/* const closeConfirmDialog = () => store.dispatch({ type: HIDE_CONFIRM_DIALOG }); */

export const dispatchOpenSignDialog = props =>
  store.dispatch({ type: OPEN_SIGN_DIALOG, payload: props });

export const dispatchCloseSignDialog = () =>
  store.dispatch({ type: CLOSE_SIGN_DIALOG });

export const dispatchCloseConfirmDialogFormik = () =>
  store.dispatch(closeConfirmDialogFormik());

export const dispatchOpenSnackBar = message =>
  store.dispatch(openSnackBar(message));

/* const openConfirmDialog = payload =>
  store.dispatch({
    type: TOGGLE_CONFIRM_DIALOG,
    payload: { open: true, ...payload }
  }); */
export const dispatchOpenConfirmDialogFormik = payload =>
  store.dispatch(openConfirmDialogFormik(payload));

export const dispatchFetchJournalData = () => store.dispatch(fetchJournalData);

export const dispatchPopHeadModal = () => store.dispatch(popHeadModal());

export const dispatchPushModal = modal => store.dispatch(pushHeadModal(modal));

export const callDefaultErrorHandlerThenPopModal = er =>
  defaultErrorHandler(er).then(() => dispatchPopHeadModal());

export const dispatchPopHeadModalThenPushModal = modal =>
  store.dispatch(popHeadModalThenPushModal(modal));

export const getUserId = () => store.getState().auth.userId;
