import React, { useEffect, useState } from "react";
import axios from "axios";
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";
import Grid from "@material-ui/core/Grid/Grid";
import Typography from "@material-ui/core/Typography/Typography";
import Paper from "@material-ui/core/Paper/Paper";
import { connect } from "react-redux";
import { makeStyles } from "@material-ui/styles";
import * as PropTypes from "prop-types";
import { Button, DialogActions } from "@material-ui/core";
import { TOGGLE_SNACKBAR } from "../constants";
import { FormikMUI } from "./formikMUi/formikNewRealisation";
import { defaultErrorHandler } from "../utils/defaultErrorHandler";
import { baseRebuildReceivedInitialValues } from "./formikMUi/utils/initialValuesUtils";

const getStyles = makeStyles(theme => ({
  header: {
    padding: theme.spacing.unit
  },
  cntr: { width: "100%", height: "100%" },
  mainContainer: {
    padding: "8px",
    marginBottom: "8px",
    height: " 100%",
    width: "100%",
    overflowY: "auto"
  }
}));

const PrivateSysSettingsConfig = ({
  confObj: { initialValuesUrl, title },
  confObj,
  openSnackBar
}) => {
  const [state, setState] = useState({ isLoading: true });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [dirty, setDirty] = useState(true);
  const classes = getStyles();
  useEffect(() => {
    const source = axios.CancelToken.source();
    try {
      axios
        .get(initialValuesUrl, { cancelToken: source.token })
        .then(({ data }) => {
          const keys = [];
          confObj.blocks.forEach(({ fields }) =>
            fields.forEach(({ name }) => keys.push(name))
          );

          const downloaded = Object.values(data._embedded)[0].filter(
            settingObj => keys.includes(settingObj.name)
          );
          const pathLabelIdMap = {};
          const tempInitialValues = {};
          downloaded.forEach(
            ({ name, description, uniqueId, valueOfAttribute }) => {
              pathLabelIdMap[name] = { uniqueId, label: description };
              tempInitialValues[name] = valueOfAttribute;
            }
          );

          const tempForm = { ...confObj };

          tempForm.blocks.forEach(({ fields }) =>
            fields.forEach(field => {
              field.label =
                pathLabelIdMap[field.name] && pathLabelIdMap[field.name].label;
            })
          );

          setState({
            formObj: tempForm,
            initialValues: baseRebuildReceivedInitialValues(
              tempForm,
              tempInitialValues
            ),
            isLoading: false,
            pathLabelIdMap
          });
        });
    } catch (error) {
      if (!axios.isCancel(error)) {
        defaultErrorHandler(error);
      }
    }
    return () => {
      source.cancel("Cancelling in cleanup");
    };
  }, [confObj, initialValuesUrl]);

  const { isLoading, formObj, initialValues, pathLabelIdMap } = state;
  let refToFormSubmitFunc = null;
  return isLoading && !initialValues && !formObj ? (
    <Grid
      justify="center"
      alignContent="center"
      direction="column"
      alignItems="center"
      container
      className={classes.cntr}
    >
      <Typography variant="h6">    </Typography>
      <CircularProgress size={120} />
    </Grid>
  ) : (
    <Paper square className={classes.mainContainer} elevation={5}>
      <Typography variant="h6" className={classes.header}>
        {title.toUpperCase()}
      </Typography>
      <FormikMUI
        enableReinitialize
        formObj={formObj}
        initialValues={initialValues}
        bindFormSubmit={ref => {
          refToFormSubmitFunc = ref;
        }}
        bindDirty={setDirty}
        bindIsSubmitting={setIsSubmitting}
        onSubmit={(values, { setSubmitting }) => {
          const changedFields = [];

          formObj.blocks.forEach(({ fields }) => {
            fields.forEach(({ name, type }) => {
              const currentValue = values[name];
              if (initialValues[name] !== currentValue) {
                let resultValue;
                if (
                  formObj.doBeforeSend &&
                  (type === "file" || type === "select")
                ) {
                  resultValue = formObj.doBeforeSend[type]
                    ? formObj.doBeforeSend[type](values[name])
                    : values[name];
                } else {
                  resultValue = currentValue;
                }
                changedFields.push({
                  value: resultValue,
                  uniqueId: pathLabelIdMap[name].uniqueId
                });
              }
            });
          });

          axios
            .all(
              changedFields.map(({ value, uniqueId }) =>
                axios.patch(`${formObj.url}/${uniqueId}`, {
                  valueOfAttribute: value
                })
              )
            )
            .then(() => {
              setSubmitting(false);
              openSnackBar("   ");
            })
            .catch(er => {
              openSnackBar(
                "        "
              );
              setSubmitting(false);
            });
        }}
      />
      <DialogActions>
        <Button
          variant="contained"
          color="primary"
          disabled={!dirty || isSubmitting}
          onClick={() => {
            if (
              refToFormSubmitFunc &&
              typeof refToFormSubmitFunc === "function"
            ) {
              refToFormSubmitFunc();
            }
          }}
        >
          
        </Button>
      </DialogActions>
    </Paper>
  );
};

PrivateSysSettingsConfig.propTypes = {
  confObj: PropTypes.shape({
    title: PropTypes.string.isRequired,
    initialValuesUrl: PropTypes.string.isRequired
  }).isRequired,
  openSnackBar: PropTypes.func.isRequired
};

const mapStateToProps = () => ({});

const mapDispatchToProps = dispatch => ({
  openSnackBar: message =>
    dispatch({
      type: TOGGLE_SNACKBAR,
      payload: { message, isSnackBarOpened: true }
    })
});

export const SysSettingsConfig = connect(
  mapStateToProps,
  mapDispatchToProps
)(PrivateSysSettingsConfig);
