import { IMaskInput } from "react-imask";
import React from "react";

const IMaskedRegExpFilteredTextField = props => {
  const { mask, name, setFieldValue, ...rest } = props;
  return (
    <IMaskInput
      name={name}
      mask={mask}
      lazy
      unmask={false} // true|false|'typed'
      {...rest}
    />
  );
};

export default IMaskedRegExpFilteredTextField;
