import React from "react";
import uuidv4 from "uuid/v4";
import { Grid } from "@material-ui/core";
import * as PropTypes from "prop-types";
import { RenderField } from "./renderField";

export const RenderRow = ({ row, formObj }) => {
  const { attribute } = row;
  return (
    <>
      {attribute.map(field => {
        if (
          field === "" ||
          field === null ||
          typeof field.name === "undefined"
        ) {
          return (
            <Grid
              item
              key={uuidv4()}
              lg={12 / attribute.length}
              md={12 / attribute.length}
              xl={12 / attribute.length}
            />
          );
        }
        return (
          <Grid
            item
            key={field.name}
            lg={12 / attribute.length}
            md={12 / attribute.length}
            xl={12 / attribute.length}
          >
            <RenderField field={field} formObj={formObj} />
          </Grid>
        );
      })}
    </>
  );
};

RenderRow.propTypes = {
  row: PropTypes.shape({}).isRequired,
  formObj: PropTypes.shape({}).isRequired
};
