import React, { useContext } from "react";
import { makeStyles } from "@material-ui/styles";
import { TableCell } from "@material-ui/core";
import classNames from "classnames";
import { TableCellProps } from "react-virtualized";
import { TableContext } from "./ScheduleTable";
import { ReactSelectMUI } from "../reactSelectMUI";
import { changeDuties } from "./tableReducer";

const getStyles = makeStyles(theme => ({
  tableCell: {
    flex: 1,
    flexDirection: "column"
  },
  TableCellRoot: {
    borderBottom: "none"
  },
  flexContainer: {
    display: "flex",
    alignItems: "center",
    boxSizing: "border-box"
  }
}));

interface DutiesCellProps extends TableCellProps {}

export const DutiesCell = ({ rowIndex, cellData: duties }: DutiesCellProps) => {
  const classes = getStyles();
  const {
    optionsMap: {
      duties: { options = [], loaded = false }
    },
    dispatch
  } = useContext(TableContext);
  return (
    <TableCell
      component="div"
      variant="body"
      classes={{ root: classes.TableCellRoot }}
      className={classNames(classes.tableCell, classes.flexContainer)}
    >
      <div style={{ width: "250px" }}>
        <ReactSelectMUI
          options={options}
          placeholder=""
          value={duties}
          label=""
          handleChange={v => dispatch(changeDuties(rowIndex, v))}
          isDisabled={!loaded}
        />
      </div>
    </TableCell>
  );
};
