import React from "react";
import { connect } from "react-redux";
import * as PropTypes from "prop-types";
import { Button, Grid, Paper } from "@material-ui/core";
import { AutoSizer } from "react-virtualized";
import "react-virtualized/styles.css"; // only needs to be imported once
import { SET_SORT, TOGGLE_IS_COLLAPSED } from "../../constants";
import {
  computeUrlThenFetchJournalData,
  popHeadModal,
  pushHeadModal,
  setSortable
} from "../../actions";
import FilterPanel from "../filter-panel/index";
import ModalWrapper from "../modalWrapper";
import PaginatedTable from "../PaginatedTable";
import ShadowedOutlinedButton from "../ShadowedOutlinedButton";
import { INITIAL_PARAMETERS } from "../../constants/intialParameters";
import { AdditionalButtons } from "./additionalButtons";

const PrivateJournal = ({
  fetchedData,
  initialParameters: {
    additionalActionPanelButtons: { leftPanel = [], rightPanel = [] } = {},
    filtersPanel,
    tableHeader
  },
  sortable,
  toggleFiltersPanel,
  setSortableThenFetJournalData,
  category,
  page,
  allowedActions,
  popHeadModal,
  openedModals,
  pushModal,
  computeUrlThenFetchJournalDataW
}) => {
  const currentPageAllowedActions = Array.isArray(allowedActions[category])
    ? allowedActions[category]
    : allowedActions[category][page];
  return (
    <Grid
      direction="column"
      container
      wrap="nowrap"
      style={{ width: "100%", height: "calc(100vh -  80px)" }}
    >
      {/*   -  */}
      <Paper
        square
        style={{ padding: "8px", marginBottom: "8px" }}
        elevation={5}
      >
        <Grid container justify="space-between" direction="row">
          {/*    */}
          <Grid>
            <Grid container spacing={8}>
              <Grid item>
                <ShadowedOutlinedButton
                  variant="outlined"
                  onClick={() => {
                    toggleFiltersPanel();
                  }}
                >
                  
                </ShadowedOutlinedButton>
              </Grid>
              <AdditionalButtons buttons={leftPanel} />
            </Grid>
          </Grid>
          {/*    */}
          <Grid>
            <Grid container spacing={8}>
              <AdditionalButtons buttons={rightPanel} />
              {currentPageAllowedActions.includes("create") && (
                <Grid item>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() =>
                      pushModal({
                        category,
                        page,
                        mode: "create"
                      })
                    }
                  >
                    
                  </Button>
                </Grid>
              )}
              {currentPageAllowedActions.includes("wizard") && (
                <Grid item>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() =>
                      pushModal({
                        category,
                        page,
                        mode: "wizard"
                      })
                    }
                  >
                    
                  </Button>
                </Grid>
              )}
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      {/*   -  */}

      {/*   -  */}
      <FilterPanel
        onApplyFiltersClick={() => computeUrlThenFetchJournalDataW()}
        filters={filtersPanel.filters}
      />

      {/*   -  */}

      {/* //todo       TableWrapper */}

      <Paper
        square
        elevation={5}
        style={{ height: "100%", padding: "8px", flex: "0 1 auto" }}
      >
        <AutoSizer>
          {({ width, height }) => (
            <PaginatedTable
              fetchJournalData={() => computeUrlThenFetchJournalDataW()}
              data={
                Object.keys(fetchedData).length > 1
                  ? // eslint-disable-next-line no-underscore-dangle
                    Object.values(fetchedData._embedded)[0]
                  : []
              }
              count={
                fetchedData &&
                fetchedData.page &&
                fetchedData.page.totalElements
                  ? fetchedData.page.totalElements
                  : 0
              }
              columns={tableHeader.map(
                ({ label, path, width: columnWidth }) => ({
                  name: path,
                  header: label,
                  width:
                    columnWidth ||
                    (tableHeader.length < 5
                      ? `${100 / tableHeader.length}%`
                      : "25%"),
                  cellProps: { style: { paddingRight: 0 } }
                })
              )}
              width={width}
              height={height - 50}
              style={{ backgroundColor: "white" }}
              onCellClick={(column, item) =>
                pushModal({
                  category,
                  page,
                  mode: "view",
                  item
                })
              }
              onHeaderClick={({ name }) => setSortableThenFetJournalData(name)}
              orderBy={sortable.length > 0 ? sortable[0].path : undefined}
              orderDirection={
                sortable.length > 0 ? sortable[0].value : undefined
              }
              cellProps={(column, data) => {
                if (Object.entries(data).length === 0) {
                  return {
                    style: {
                      fontSize: "15px",
                      fontWeight: "bold",
                      overflow: "hidden"
                    }
                  };
                }
                return undefined;
              }}
            />
          )}
        </AutoSizer>
      </Paper>
      {openedModals.map((modal, index) => (
        <ModalWrapper
          key={modal.key}
          modal={modal}
          config={
            INITIAL_PARAMETERS[modal.category].pages
              ? INITIAL_PARAMETERS[modal.category].pages[modal.page].forms[
                  modal.mode
                ]
              : INITIAL_PARAMETERS[modal.category].page.forms[modal.mode]
          }
          shouldWrapInsideTabs={modal.shouldWrapInsideTabs}
          //     ,    ,      2-    
          hideModal={
            openedModals.length - 1 === index
              ? () => popHeadModal()
              : popHeadModal
          }
          fetchJournalData={computeUrlThenFetchJournalDataW}
        />
      ))}
    </Grid>
  );
};

PrivateJournal.defaultProps = {
  page: undefined
};

PrivateJournal.propTypes = {
  sortable: PropTypes.arrayOf(
    PropTypes.shape({
      path: PropTypes.string,
      value: PropTypes.string
    })
  ).isRequired,
  setSortableThenFetJournalData: PropTypes.func.isRequired,
  initialParameters: PropTypes.shape({}).isRequired,
  fetchedData: PropTypes.shape({}).isRequired,
  toggleFiltersPanel: PropTypes.func.isRequired,
  computedFilters: PropTypes.shape({}).isRequired,
  category: PropTypes.string.isRequired,
  page: PropTypes.string,
  computeUrlThenFetchJournalDataW: PropTypes.func.isRequired,
  allowedActions: PropTypes.shape({}).isRequired,
  pushModal: PropTypes.func.isRequired,
  openedModals: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  popHeadModal: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  computedFilters: state.filtersReducer.computedFilters,
  fetchedData: state.journalReducer.fetchedData,
  sortable: state.journalReducer.sortable,
  isCollapsed: state.filtersReducer.isCollapsed,
  functions: state.auth.functions,
  openedModals: state.journalReducer.modalState.openedModals,
  allowedActions: state.wrapperReducer.allowedActions
});

const mapDispatchToProps = dispatch => ({
  setSort: path => dispatch({ type: SET_SORT, payload: { path } }),
  toggleFiltersPanel: () => dispatch({ type: TOGGLE_IS_COLLAPSED }),
  setSortableThenFetJournalData: name =>
    dispatch(setSortable(name)).then(() =>
      dispatch(computeUrlThenFetchJournalData())
    ),
  pushModal: modal => dispatch(pushHeadModal(modal)),
  popHeadModal: () => dispatch(popHeadModal()),
  computeUrlThenFetchJournalDataW: () =>
    dispatch(computeUrlThenFetchJournalData())
});

export const Journal = connect(
  mapStateToProps,
  mapDispatchToProps
)(PrivateJournal);
