import React from "react";
import MaskedInput, { maskArray, MaskedInputProps } from "react-text-mask";
import * as PropTypes from "prop-types";
import { TextField } from "@material-ui/core";
import { BaseTextFieldProps } from "@material-ui/core/TextField";

interface TextMaskCustomProps extends MaskedInputProps {
  inputRef: Function;
}

function TextMaskCustom({
  mask,
  pipe,
  showMask,
  inputRef,
  ...other
}: TextMaskCustomProps) {
  return (
    <MaskedInput
      {...other}
      ref={ref => {
        inputRef(ref ? ref.inputElement : null);
      }}
      mask={mask}
      pipe={pipe}
      // placeholderChar={'\u2000'}
    />
  );
}

TextMaskCustom.propTypes = {
  inputRef: PropTypes.func.isRequired
};

export interface MaskedInputMUIProps extends BaseTextFieldProps {
  mask?: maskArray | ((value: string) => maskArray);
  keepCharPositions?: boolean;
  pipe?: (
    conformedValue: string,
    config: any
  ) => false | string | { value: string; indexesOfPipedChars: number[] };
  InputProps: any;
}

export const MaskedInputMUI = ({
  label = "",
  mask,
  pipe,
  keepCharPositions,
  InputProps,
  ...rest
}: any) => (
  <TextField
    label={label}
    InputProps={{
      ...InputProps,
      inputComponent: TextMaskCustom,
      inputProps: { mask, pipe, keepCharPositions }
    }}
    {...rest}
  />
);
