import { get, set } from "lodash";
import {
  baseDictRebuildReceivedOption,
  getUserFunctions
} from "../../../constants";

export const constructFieldInitialValue = (field, userFunctions) => {
  const { type, defaultValue } = field;
  if (defaultValue) {
    if (typeof defaultValue === "function") {
      return defaultValue(userFunctions);
    }
    return defaultValue;
  }
  switch (type) {
    case "text": {
      return "";
    }
    case "select": {
      return field.multiple ? [] : null;
    }
    case "checkbox": {
      return false;
    }
    case "date-range": {
      return [];
    }
    case "file": {
      return null;
    }
    case "fieldArray": {
      return [];
    }
    default: {
      return "";
    }
  }
};

export const createEmptyFieldArrayObj = fields => {
  const result = {};
  fields.forEach(field => {
    result[field.name] = constructFieldInitialValue(field);
  });
  return result;
};

export const constructInitialValuesForEmptyFormWithOutBlocks = confObj => {
  const initialValues = {};
  confObj.fields.forEach(field =>
    set(initialValues, field.name, constructFieldInitialValue(field))
  );
  return initialValues;
};

export const constructInitialValuesForEmptyForm = formObj => {
  const initialValues = {};
  const userFunctions = getUserFunctions();
  formObj.blocks.forEach(({ fields }) =>
    fields.forEach(field =>
      set(
        initialValues,
        field.name,
        constructFieldInitialValue(field, userFunctions)
      )
    )
  );
  return initialValues;
};

export const rebuildDownloadedInitialValue = (value, field, userFunctions) => {
  const { rebuildReceivedValue, type } = field;
  switch (type) {
    case "text": {
      if (value !== null && typeof value !== "undefined") {
        if (rebuildReceivedValue) {
          return rebuildReceivedValue(value);
        }
        return value;
      }
      return constructFieldInitialValue(field, userFunctions);
    }
    case "select": {
      if (typeof value !== "undefined") {
        if (value !== null && field.multiple !== true) {
          return rebuildReceivedValue
            ? rebuildReceivedValue(value)
            : baseDictRebuildReceivedOption(value);
        }
        if (Array.isArray(value) && field.multiple === true) {
          return value.map(el =>
            rebuildReceivedValue
              ? rebuildReceivedValue(el)
              : baseDictRebuildReceivedOption(el)
          );
        }
      }
      return constructFieldInitialValue(field, userFunctions);
    }
    case "checkbox": {
      if (value === true || value === false) {
        return value;
      }
      if (value === "true" || value === "false") {
        return value === "true";
      }
      return constructFieldInitialValue(field, userFunctions);
    }
    case "date-range": {
      return constructFieldInitialValue(field, userFunctions);
    }
    case "date": {
      if (value !== "") {
        if (rebuildReceivedValue) {
          return rebuildReceivedValue(value);
        }
        return value;
      }
      return constructFieldInitialValue(field, userFunctions);
    }
    case "file": {
      if (value !== null || typeof value !== "undefined") {
        if (rebuildReceivedValue) {
          return rebuildReceivedValue(value);
        }
        return value;
      }
      return constructFieldInitialValue(field, userFunctions);
    }

    default: {
      if (value === null || value === undefined) {
        return "";
      }
      return value;
      /* throw new Error(
        `     ${field.name}   ${type}`
      ); */
    }
  }
};

export const baseRebuildReceivedInitialValues = (
  formObj,
  receivedInitialValues
) => {
  const resultingInitialValues = {};
  const userFunctions = getUserFunctions();
  formObj.blocks.forEach(({ fields }) =>
    fields.forEach(field => {
      set(
        resultingInitialValues,
        field.name,
        rebuildDownloadedInitialValue(
          get(receivedInitialValues, field.name),
          field,
          userFunctions
        )
      );
    })
  );
  return resultingInitialValues;
};
