import { BusinessCenterSharp } from "@material-ui/icons";
import forms from "./form";
import { extractPayloadFromBaseSpringDateRestStructure } from "../../../../utils/commonFormFunctions";

const services = {
  navigationLabel: "",
  requiresFunction: [
    {
      functionName: "administration_service_modifier",
      allowedActions: ["create", "view", "edit", "delete"]
    },
    {
      functionName: "member_service_modifier",
      allowedActions: ["create", "view", "edit", "delete"]
    }
  ],
  url: "/api/journalStructureProcedures",
  icon: BusinessCenterSharp,
  filtersPanel: {
    filters: [
      {
        label: "   ",
        name: "rguId",
        type: "text"
      },
      {
        label: " ",
        type: "text",
        name: "fullName"
      },
      {
        label: "   ",
        name: "serviceRguId",
        type: "text"
      },
      {
        label: " ",
        name: "serviceFullName",
        type: "text"
      },
      {
        label: "",
        name: "memberId",
        type: "select",
        dictUrl: "/api/structureMember/search/present",
        rebuildReceivedOptions: data => {
          const extractedData = extractPayloadFromBaseSpringDateRestStructure(
            data
          );
          return Array.isArray(extractedData)
            ? extractedData.map(({ uniqueId, fullName }) => ({
                label: fullName,
                value: uniqueId
              }))
            : [];
        }
      }
    ]
  },
  tableHeader: [
    {
      label: "",
      sortable: true,
      path: "memberFullName"
    },
    {
      label: " ",
      sortable: true,
      path: "fullName"
    },
    {
      label: "   ",
      sortable: true,
      path: "rguId"
    },
    {
      label: " ",
      sortable: true,
      path: "serviceFullName"
    },
    {
      label: "   ",
      sortable: true,
      path: "serviceRguId"
    },
    {
      label: "",
      sortable: true,
      path: "isFunction",
      width: "15%"
    },
    {
      label: "     ",
      sortable: true,
      path: "synchronizeTimestamp",
      width: "20%"
    }
  ],
  forms: { ...forms }
};
export default services;
