import { SET_EXPANDED_INDEX, TOOGLE_MENU_COLLAPSED } from '../constants';

const initialState = {
  expandedIndex: -1,
  value: false,
  isMenuMinimized: false,
  currentlyActiveIndex: -1
};

export default function filtersReducer(state = initialState, action) {
  switch (action.type) {
    case SET_EXPANDED_INDEX:
      return {
        ...state,
        expandedIndex: action.payload.expandedIndex,
        value:
          state.expandedIndex !== action.payload.expandedIndex
            ? true
            : !state.value
      };
    case TOOGLE_MENU_COLLAPSED: {
      return {
        ...state,
        isMenuMinimized: !state.isMenuMinimized
      };
    }
    default:
      return state;
  }
}
