export const validateField = ({ value, field }) => {
  if (typeof field === "undefined") {
    return undefined;
  }
  const {
    required,
    type: { minlength, maxlength, regular, name: typeName }
  } = field;
  if (
    required === "true" &&
    (typeName === "dictionary" || typeName === "dictionary-nsi") &&
    value === null
  ) {
    return "    ";
  }
  if (required === "true" && typeName === "boolean" && value === false) {
    return "  ";
  }
  if (required === "true" && typeName === "attachment" && value === null) {
    return "  ";
  }
  if (required === "true" && value === "") {
    return " ";
  }

  if (value !== "" && minlength && value.length < minlength) {
    let suffix;
    if ("234".indexOf(minlength[minlength.length - 1]) !== -1) {
      suffix = "";
    } else if ("1".indexOf(minlength[minlength.length - 1]) !== -1) {
      suffix = "";
    } else {
      suffix = "";
    }
    return `   ${minlength} ${suffix}`;
  }

  // todo       
  if (value !== "" && maxlength && value.length > maxlength) {
    let suffix;
    if ("234".indexOf(maxlength[maxlength.length - 1]) !== -1) {
      suffix = "";
    } else if ("1".indexOf(maxlength[maxlength.length - 1]) !== -1) {
      suffix = "";
    } else {
      suffix = "";
    }
    return `   ${maxlength} ${suffix}`;
  }

  if (value !== "" && regular) {
    let reconstructedRegular = regular;
    if (regular.startsWith("/")) {
      reconstructedRegular = reconstructedRegular.replace(/^\//, "");
    }
    if (reconstructedRegular.endsWith("/")) {
      reconstructedRegular = reconstructedRegular.replace(/\/$/, "");
    }
    if (!RegExp(reconstructedRegular).test(value)) {
      return "  ";
    }
  }

  return undefined;
};
