import React from "react";
import createNumberMask from "text-mask-addons/dist/createNumberMask";
import Yup from "../../../yup";
import { computePropsForFormikMUI } from "../../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks } from "../../../../utils/commonFormFunctions";
import {
  callDefaultErrorHandlerThenPopModal,
  dispatchPopHeadModal
} from "../../../index";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "../../../../components/completePromiseThenRenderComponent";
import { FormikMUI } from "../../../../components/formikMUi/formikNewRealisation";
import { constructRequest } from "../../../../actions";
import { PrimaryButton } from "../../../../components/modalWrapper/buttons";
import {
  constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
  rebuildReceivedValueLabelFullNameValueUniqueId
} from "../../../rebuildRecieved";

const baseForm = {
  title: "",
  titleItem: "",
  headerText: "fullName",
  url: "/api/members/structureMember",
  doBeforeSend: {
    select: ({ value }) => value
  },
  projection: "formMember",
  method: "post",
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          name: "shortName",
          label: " ",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          name: "fullName",
          type: "text",
          label: " ",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          name: "rguId",
          label: "  ",
          type: "text",
          readOnly: true
        },
        {
          name: "synchronizeTimestamp",
          label: "   ",
          type: "text",
          readOnly: true
        },
        {
          name: "parentId",
          label: " ",
          type: "select",
          dictUrl: "/api/structureMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelFullNameValueUniqueId,
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        },
        {
          name: "supremeParentId",
          label: " ",
          readOnly: true,
          type: "select",
          dictUrl: "/api/structureMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelFullNameValueUniqueId,
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        }
      ]
    },
    {
      blockTitle: " ",
      fields: [
        {
          name: "memberInfo.orgHead",
          label: " ",
          type: "text"
        },
        {
          name: "memberInfo.orgPhone",
          label: "",
          type: "text"
        },
        {
          name: "memberInfo.web",
          label: "-",
          type: "text"
        },
        {
          name: "memberInfo.post",
          label: " ",
          type: "text",
          widths: { lg: 8, xl: 8 }
        },
        {
          name: "memberInfo.inn",
          label: "",
          type: "formatted",
          mask: createNumberMask({
            prefix: "",
            includeThousandsSeparator: false,
            allowLeadingZeroes: true,
            integerLimit: 10
          })
        },
        {
          name: "memberInfo.ogrn",
          label: "",
          type: "formatted",
          mask: createNumberMask({
            prefix: "",
            includeThousandsSeparator: false,
            allowLeadingZeroes: true,
            integerLimit: 13
          })
        },

        {
          name: "memberInfo.workTime",
          label: " ",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    shortName: Yup.string().required(),
    fullName: Yup.string().required(),
    rguId: Yup.string(),
    synchronizeTimestamp: Yup.string(),
    parentId: Yup.object().nullable(),
    supremeParentId: Yup.object().nullable(),
    memberInfo: Yup.object({
      orgHead: Yup.string(),
      orgPhone: Yup.string(),
      web: Yup.string(),
      post: Yup.string(),
      ogrn: Yup.string(),
      workTime: Yup.string(),
      inn: Yup.string()
    })
  })
};

const memberMvFormObj = {
  doBeforeSend: {
    select: ({ value }) => value,
    file: ({ value }) => value
  },
  fields: [
    {
      name: "mvList",
      label: "mvList",
      type: "fieldArray",
      fields: [
        {
          name: "uniqueId",
          label: "",
          type: "text",
          hidden: true
        },
        {
          name: "mvObjectId",
          label: "  ",
          type: "select",
          dictUrl: "/api/mv/objects",
          rebuildReceivedOptions: data =>
            data.map(({ label, uniqueId }) => ({
              label,
              value: uniqueId
            })),
          width: 2
        },
        {
          name: "isDeleted",
          label: "isDeleted",
          type: "checkbox",
          hidden: true
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    mvList: Yup.array()
      .of(
        Yup.object({
          mvObjectId: Yup.object().when("isDeleted", {
            is: true,
            then: Yup.object().nullable(),
            otherwise: Yup.object()
              .nullable()
              .required()
          }),
          isDeleted: Yup.bool(),
          uniqueId: Yup.string()
        })
      )
      .min(1)
  })
};

const baseViewEdit = {
  ...baseForm,
  url: "/api/structureMember",
  primaryActionUrl: "/api/members/structureMember",
  additionalTabs: [
    {
      tabTitle: "  ",
      tabContent: {
        Component: ({
          modal: {
            item: { uniqueId }
          },
          ...rest
        }) => (
          <CompletePromiseThenRenderComponent
            Component={FormikMUI}
            variant="table"
            config={[
              {
                urlToDownload: `/api/memberMv/structureMemberMv/${uniqueId}`,
                whereToStore: "rawInitialValues",
                postLoadDoSomethingThenPassResultAsProp: {
                  func: data => ({
                    mvList: data.map(
                      ({
                        mvObject: { label, uniqueId: mvUniqueId },
                        ...restO
                      }) => ({
                        mvObjectId: {
                          label,
                          value: mvUniqueId
                        },
                        ...restO
                      })
                    ),
                    procedureId: uniqueId
                  }),
                  passResultAs: "initialValues"
                }
              },
              ...findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks(
                memberMvFormObj
              )
            ]}
            formObj={memberMvFormObj}
            errorHandler={callDefaultErrorHandlerThenPopModal}
            PreloaderComponent={defaultPreloader}
            {...rest}
          />
        ),
        passProps: {
          enableReinitialize: true
        },
        functionToExecuteThenPassResultAsProps: ({
          modal: {
            item: { uniqueId },
            mode
          },
          firstFormInitialValues: { supremeParentId }
        }) => ({
          onSubmit: ({ mvList }, { setSubmitting }) => {
            constructRequest(
              {
                method: "post",
                url: `/api/memberMv/structureMemberMv/${uniqueId}`,
                data: mvList
                  .filter(item => {
                    if (item === null) {
                      return false;
                    }
                    const { uniqueId: listId, isDeleted } = item;
                    return !(listId === null && isDeleted === true);
                  })
                  .map(item => ({ ...item, mvObjectId: item.mvObjectId.value }))
              },
              () => {
                dispatchPopHeadModal();
                setSubmitting(false);
              },
              () => setSubmitting(false)
            );
          },
          readOnly: mode !== "edit" || supremeParentId !== null
        })
      },
      tabButtons: [
        {
          Btn: PrimaryButton,
          requiresFormSubmitRef: true,
          conditionallyRender: ({
            modal: { mode },
            firstFormInitialValues: { supremeParentId }
          }) => mode === "edit" && supremeParentId === null
        }
      ]
    }
  ]
};

const forms = {
  create: computePropsForFormikMUI({ formObj: baseForm, mode: "create" }),
  view: computePropsForFormikMUI({
    formObj: baseViewEdit,
    mode: "view"
  }),
  edit: computePropsForFormikMUI({
    formObj: {
      ...baseViewEdit,
      method: "patch"
    },
    mode: "edit"
  })
};

export default forms;
