import React from "react";
import { Field } from "formik";
import { get } from "lodash";
import locale from "date-fns/locale/ru";
import { WrappedTextField } from "./wrappedComponents/wrappedTextField";
import { WrappedReactSelect } from "./wrappedComponents/wrappedReactSelect";
import { WrappedMaskedInputMUI } from "./wrappedComponents/wrappedMaskedInputMUI";
import { WrappedCheckBoxMUI } from "./wrappedComponents/wrappedCheckBoxMUI";
import { WrappedReactDropZoneMUI } from "./wrappedComponents/wrappedReactDropZoneMUI";
import { fieldLevelUniqueCheck } from "./utils/isValueUnique";
import IntegrateDateRangePickerWithMUI from "../IntegrateDateRangePickerWithMUI";
import { defaultErrorHandler } from "../../utils/defaultErrorHandler";
import { IntegrateDatePickerWithMUI } from "../IntegrateDatePickerWithMUI";
import { getUserFunctions } from "../../constants";

const baseOnDrop = (files, field, setFieldValue) => {
  const data = new FormData();
  data.append("file", files[0]);
  field
    .doBeforeSetFieldValue(data)
    .then(({ data: fileId }) => {
      setFieldValue(field.name, {
        label: files[0].name,
        value: fileId
      });
    })
    .catch(error => {
      defaultErrorHandler(error);
    });
};

function createUniqueCheckFunction(initialValues, name, { uniqueCheck }) {
  return value => {
    if (value !== get(initialValues, name)) {
      const { constructParams, url } = uniqueCheck;
      const axiosConfig = {
        method: "get",
        url,
        params: constructParams(initialValues, value)
      };
      return fieldLevelUniqueCheck(axiosConfig, value);
    }
    return undefined;
  };
}

export const renderField = ({
  field,
  classes,
  readOnly,
  initialValues,
  setFieldValue,
  status
}) => {
  const { type, name, label, additionalProps } = field;

  switch (type) {
    case "text": {
      return (
        <Field
          name={name}
          label={label}
          component={WrappedTextField}
          validate={
            field.uniqueCheck
              ? createUniqueCheckFunction(initialValues, name, field)
              : undefined
          }
          className={classes.field}
          {...additionalProps}
          InputProps={{
            readOnly: readOnly || field.readOnly || false
          }}
        />
      );
    }
    case "select": {
      const fieldLevelReadOnly =
        typeof field.readOnly === "function"
          ? field.readOnly(getUserFunctions())
          : field.readOnly;
      return (
        <Field
          name={name}
          label={label}
          component={WrappedReactSelect}
          options={field.options}
          isMulti={field.multiple || false}
          isDisabled={readOnly || fieldLevelReadOnly || false}
          className={classes.field}
          placeholder={field.placeholder}
          customOnChange={field.customOnChange}
          {...additionalProps}
        />
      );
    }
    case "formatted": {
      return (
        <Field
          name={name}
          label={label}
          component={WrappedMaskedInputMUI}
          validate={
            field.uniqueCheck
              ? createUniqueCheckFunction(initialValues, name, field)
              : undefined
          }
          mask={field.mask}
          pipe={field.pipe}
          keepCharPositions={field.keepCharPositions}
          className={classes.field}
          {...additionalProps}
          InputProps={{
            readOnly: readOnly || field.readOnly || false
          }}
        />
      );
    }
    case "checkbox": {
      return (
        <Field
          name={name}
          label={label}
          component={WrappedCheckBoxMUI}
          className={classes.field}
          disabled={readOnly || field.readOnly || false}
          customOnChange={field.customOnChange}
          {...additionalProps}
        />
      );
    }
    case "password": {
      return (
        <Field
          name={name}
          label={label}
          component={WrappedTextField}
          className={classes.field}
          {...additionalProps}
          InputProps={{
            readOnly: readOnly || field.readOnly || false
          }}
          type="password"
        />
      );
    }
    case "date-range": {
      const pickerProps = {
        locale: { ...locale },
        moveRangeOnFirstSelection: false,
        showDateDisplay: false
      };
      return (
        <Field
          name={name}
          label={label}
          readOnly={readOnly || field.readOnly || false}
          className={classes.field}
          component={IntegrateDateRangePickerWithMUI}
          pickerProps={pickerProps}
        />
      );
    }
    case "date": {
      const pickerProps = {
        locale: { ...locale },
        moveRangeOnFirstSelection: false,
        showDateDisplay: false
      };
      return (
        <Field
          name={name}
          label={label}
          readOnly={readOnly || field.readOnly || false}
          className={classes.field}
          component={IntegrateDatePickerWithMUI}
          pickerProps={pickerProps}
        />
      );
    }
    case "file": {
      return (
        <Field
          name={name}
          label={label}
          onDrop={
            field.onDrop
              ? files => field.onDrop(files, field, setFieldValue, status)
              : files => baseOnDrop(files, field, setFieldValue)
          }
          component={WrappedReactDropZoneMUI}
          customUrlToDownLoadFile={
            field.customUrlToDownLoadFile
              ? field.customUrlToDownLoadFile
              : undefined
          }
          disabled={readOnly || field.readOnly || false}
          className={classes.field}
          accept={field.accept}
        />
      );
    }
    case "spaceFiller": {
      return <div className={classes.field} />;
    }
    default: {
      throw new Error(
        `  name: ${name}     : ${type}`
      );
    }
  }
};
