import {
  AppBar,
  Toolbar,
  IconButton,
  Button,
  Typography
} from "@material-ui/core";
import { Menu as MenuIcon, AccountCircle } from "@material-ui/icons";
import React from "react";
import { connect } from "react-redux";
import * as PropTypes from "prop-types";
import { makeStyles } from "@material-ui/styles";
import { PUSH_MODAL, TOOGLE_MENU_COLLAPSED } from "../../constants";
import { logout } from "../../actions";

const useStyles = makeStyles(theme => ({
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  menuButton: {
    marginLeft: 6,
    marginRight: 18
  },
  grow: {
    flexGrow: 1
  }
}));

const CustomAppBar = ({
  pushModal,
  logout,
  userId,
  surnameInitials,
  toggleLeftMenu
}) => {
  const classes = useStyles();
  return (
    <AppBar position="static" className={classes.appBar}>
      <Toolbar>
        <img
          src="/logo.png"
          alt="logo"
          style={{
            maxHeight: "50px",
            color: "inherit"
          }}
        />
        <IconButton
          onClick={toggleLeftMenu}
          className={classes.menuButton}
          color="inherit"
          aria-label="Menu"
        >
          <MenuIcon />
        </IconButton>
        <div className={classes.grow}>
          <Typography
            variant="subtitle1"
            color="inherit"
            style={{ width: "550px", lineHeight: "inherit" }}
          >
             
          </Typography>
        </div>
        <Button
          color="inherit"
          onClick={() => {
            pushModal({
              category: "serviceMan",
              mode: "profile",
              shouldWrapInsideTabs: false,
              item: { uniqueId: userId }
            });
          }}
        >
          <div
            style={{
              display: "flex",
              alignItems: "center"
            }}
          >
            <Typography
              variant="subtitle1"
              color="inherit"
              noWrap
              style={{ marginRight: "4px" }}
            >
              {surnameInitials}
            </Typography>
            <AccountCircle />
          </div>
        </Button>
        <Button variant="text" color="inherit" size="medium" onClick={logout}>
          
        </Button>
      </Toolbar>
    </AppBar>
  );
};

CustomAppBar.propTypes = {
  pushModal: PropTypes.func.isRequired,
  logout: PropTypes.func.isRequired,
  userId: PropTypes.number.isRequired,
  surnameInitials: PropTypes.string.isRequired,
  toggleLeftMenu: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  userId: state.auth.userId,
  surnameInitials: state.auth.surnameInitials
});

const mapDispatchToProps = dispatch => ({
  toggleLeftMenu: () => dispatch({ type: TOOGLE_MENU_COLLAPSED }),
  pushModal: modal => dispatch({ type: PUSH_MODAL, payload: modal }),
  logout: () => dispatch(logout())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CustomAppBar);
