import axios from "axios";

export const commonFilesUrl = "/api/file/download/commonFiles";

export const downloadFile = (url, fileName) =>
  axios({
    url,
    method: "GET",
    responseType: "blob" // important
  })
    .then(response => {
      const blobUrl = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = blobUrl;
      link.setAttribute("download", fileName);
      document.body.appendChild(link);
      link.click();
    })
    .catch(e => console.log(e));

export const claimAttachmentDownloadUrl = (claimUuid, fileName) =>
  `/api/claim/attachment/download/${claimUuid}/${fileName}`;

export const urlToDownLoadClaimAttachmentFromFtp = ({ value }) => {
  const { uuidClaim, label: fileName } = value[0];
  return claimAttachmentDownloadUrl(uuidClaim, fileName);
};
