import { TableCell } from "@material-ui/core";
import React, { useContext } from "react";
import { makeStyles } from "@material-ui/styles";
import { TableCellProps } from "react-virtualized";
import v4 from "uuid";
import { EventType } from "./types";
import { TableContext } from "./ScheduleTable";
import { ReactSelectMUI } from "../reactSelectMUI";
import { changePerformers } from "./tableReducer";

const getStyles = makeStyles({
  tableCell: {
    flex: 1,
    flexDirection: "column"
  },
  TableCellRoot: {
    borderBottom: "none"
  },
  flexContainer: {
    display: "flex",
    alignItems: "center",
    boxSizing: "border-box"
  }
});

export const EventPerformersCell = ({
  cellData: events,
  rowIndex
}: TableCellProps) => {
  const classes = getStyles();
  const {
    optionsMap: {
      performers: { loaded, options }
    },
    dispatch
  } = useContext(TableContext);
  return (
    <TableCell
      classes={{ root: classes.TableCellRoot }}
      component="div"
      variant="body"
    >
      {events.map(({ performers, uniqueId }: EventType, eventIndex: number) => {
        return (
          <div style={{ width: "250px" }} key={uniqueId || v4()}>
            <ReactSelectMUI
              options={options}
              isMulti
              placeholder=""
              value={performers}
              label=""
              handleChange={v =>
                dispatch(changePerformers(rowIndex, eventIndex, v))
              }
              isDisabled={!loaded}
            />
          </div>
        );
      })}
    </TableCell>
  );
};
