import React from "react";
import { connect } from "react-redux";
import * as PropTypes from "prop-types";
import { Dialog, withWidth } from "@material-ui/core";
import {
  MODAL_TAB_HAS_CHANGED,
  TOGGLE_SNACKBAR,
  TOGGLE_CONFIRM_DIALOG,
  HIDE_CONFIRM_DIALOG,
  DISABLE_PRIMARY_BTN_CONFIRM_DIALOG,
  PUSH_MODAL,
  REPLACE_HEAD_MODAL,
  POP_HEAD_MODAL
} from "../../constants";

const ModalWrapper = props => {
  const {
    width,
    config: {
      component: InnerComponent,
      functionsToExecute = [],
      forwardedProps,
      ...restConfigProps
    },
    hideModal,
    modal
  } = props;
  const additionalPassedProps = {};
  functionsToExecute.forEach(({ func, passResultAs }) => {
    additionalPassedProps[passResultAs] = func(modal);
  });
  // todo maxWidth={width === "xl" ? "lg" : width === "lg" ? "md" : width} - !!!,   
  return (
    <Dialog
      open
      disableEscapeKeyDown
      disableBackdropClick
      fullWidth
      maxWidth={width === "xl" ? "lg" : width === "lg" ? "md" : width}
    >
      <InnerComponent
        {...forwardedProps}
        closeHandler={hideModal}
        {...restConfigProps}
        {...additionalPassedProps}
        modal={modal}
      />
    </Dialog>
  );
};

ModalWrapper.defaultProps = {
  hideModal: undefined
};

ModalWrapper.propTypes = {
  modal: PropTypes.shape({
    mode: PropTypes.string,
    item: PropTypes.shape({
      uniqueId: PropTypes.number.isRequired,
      uuid: PropTypes.string
    }),
    category: PropTypes.string.isRequired,
    page: PropTypes.string
  }).isRequired,
  allowedActions: PropTypes.shape({}).isRequired,
  hideModal: PropTypes.func,
  config: PropTypes.shape({
    component: PropTypes.oneOfType([PropTypes.shape({}), PropTypes.func])
      .isRequired
  }).isRequired,
  width: PropTypes.string.isRequired
};

const mapDispatchToProps = dispatch => ({
  tabChanged: tabNumber =>
    dispatch({
      type: MODAL_TAB_HAS_CHANGED,
      payload: { openedTab: tabNumber }
    }),
  openSnackBar: message =>
    dispatch({
      type: TOGGLE_SNACKBAR,
      payload: { message, isSnackBarOpened: true }
    }),
  openConfirmDialog: payload =>
    dispatch({
      type: TOGGLE_CONFIRM_DIALOG,
      payload: { open: true, ...payload }
    }),
  closeConfirmDialog: () => dispatch({ type: HIDE_CONFIRM_DIALOG }),
  disableConfirmDialogPrimaryBtn: value =>
    dispatch({
      type: DISABLE_PRIMARY_BTN_CONFIRM_DIALOG,
      payload: { primaryButtonDisabled: value }
    }),
  pushModal: modal => dispatch({ type: PUSH_MODAL, payload: modal }),
  replaceHeadModal: modal =>
    dispatch({ type: REPLACE_HEAD_MODAL, payload: modal }),
  popHeadModal: () => dispatch({ type: POP_HEAD_MODAL })
});

const mapStateToProps = state => ({
  openedTab: state.journalReducer.openedTab,
  allowedActions: state.wrapperReducer.allowedActions
});

export default withWidth()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(ModalWrapper)
);
