import React, { useState } from "react";
import {
  DialogTitle,
  IconButton,
  DialogContent,
  DialogActions,
  Button,
  Tabs,
  Tab,
  Grid,
  Typography,
  LinearProgress
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import { makeStyles } from "@material-ui/styles";

const getStyles = makeStyles({
  DialogTitleRoot: {
    paddingBottom: "10px"
  },
  closeBtn: {
    position: "absolute",
    right: "24px",
    top: "15px"
  },
  tabTitle: {
    fontSize: "15px"
  }
});

export const RenderModalContent = ({
  title,
  closeHandler,
  tabs,
  firstFormInitialValues,
  optionsMap,
  actionButtons,
  additionalActionButtonsDependedOnProps,
  modal
}) => {
  const [activeTab, setActiveTab] = useState(0);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const arrayOfRefsToFormSubmitFunc = tabs.map(() => null);
  const classes = getStyles();
  const {
    tabContent: { functionToExecuteThenPassResultAsProps }
  } = tabs[activeTab];
  const propsDependedOnFirstFormInitialValuesOrModal = functionToExecuteThenPassResultAsProps
    ? functionToExecuteThenPassResultAsProps({
        firstFormInitialValues,
        modal
      })
    : {};
  return (
    <>
      <DialogTitle classes={{ root: classes.DialogTitleRoot }}>
        <Grid container justify="space-between" alignItems="center">
          <Typography variant="h6" style={{ width: "90%" }}>
            {`${
              typeof title === "function"
                ? title(firstFormInitialValues)
                : title
            }${
              firstFormInitialValues[
                tabs[0].tabContent.passProps.formObj.headerText
              ]
            }`.toUpperCase()}
          </Typography>
          <IconButton className={classes.closeBtn} onClick={closeHandler}>
            <CloseIcon />
          </IconButton>
        </Grid>
        <Tabs
          value={activeTab}
          onChange={(e, v) => setActiveTab(v)}
          indicatorColor="primary"
          textColor="primary"
          variant={tabs.length > 3 ? "scrollable" : "fullWidth"}
        >
          {tabs.map(
            ({
              tabTitle,
              conditionallyDisable,
              disabled,
              conditionallyRender
            }) => {
              if (
                conditionallyRender &&
                typeof conditionallyRender === "function" &&
                conditionallyRender({ firstFormInitialValues, modal })
              ) {
                return null;
              }
              const computedIsDisabled =
                disabled ||
                (conditionallyDisable &&
                  conditionallyDisable({ firstFormInitialValues, modal })) ||
                false;
              return (
                <Tab
                  fullWidth
                  label={tabTitle}
                  key={tabTitle}
                  disabled={computedIsDisabled}
                  classes={{ label: classes.tabTitle }}
                />
              );
            }
          )}
        </Tabs>
      </DialogTitle>
      <DialogContent>
        {tabs.map(
          (
            {
              tabContent: {
                Component,
                passProps,
                functionToExecuteThenPassResultAsProps
              },
              tabTitle,
              disabled
            },
            index
          ) => {
            if (disabled === true || activeTab !== index) {
              return null;
            }
            return (
              <div
                style={!(activeTab === index) ? { display: "none" } : undefined}
                key={tabTitle}
              >
                {index === 0 ? (
                  <Component
                    bindFormSubmit={refToFormSubmitFunc => {
                      arrayOfRefsToFormSubmitFunc[index] = refToFormSubmitFunc;
                    }}
                    bindIsSubmitting={setIsSubmitting}
                    {...passProps}
                    initialValues={firstFormInitialValues}
                    optionsMap={optionsMap}
                    {...propsDependedOnFirstFormInitialValuesOrModal}
                  />
                ) : (
                  <Component
                    bindFormSubmit={refToFormSubmitFunc => {
                      arrayOfRefsToFormSubmitFunc[index] = refToFormSubmitFunc;
                    }}
                    bindIsSubmitting={setIsSubmitting}
                    {...passProps}
                    firstFormInitialValues={firstFormInitialValues}
                    modal={modal}
                    {...propsDependedOnFirstFormInitialValuesOrModal}
                  />
                )}
              </div>
            );
          }
        )}
        {isSubmitting && <LinearProgress />}
      </DialogContent>
      <DialogActions
        style={modal.mode === "edit" ? { marginRight: "8px" } : undefined}
      >
        {/*       */}
        {actionButtons}
        {additionalActionButtonsDependedOnProps &&
          additionalActionButtonsDependedOnProps.map(
            ({ text, onClick, style, color, variant, conditionallyRender }) => {
              if (
                conditionallyRender &&
                !conditionallyRender({ firstFormInitialValues, modal })
              ) {
                return null;
              }
              return (
                <Button
                  key={text}
                  disabled={isSubmitting}
                  variant={variant}
                  onClick={() => {
                    onClick({ modal, firstFormInitialValues });
                  }}
                  style={style}
                  color={color}
                >
                  {text}
                </Button>
              );
            }
          )}
        {tabs.map(
          ({ tabButtons }, index) =>
            activeTab === index &&
            tabButtons && (
              <React.Fragment key={index}>
                {tabButtons.map(
                  (
                    { Btn, requiresFormSubmitRef, conditionallyRender },
                    bIndex
                  ) => {
                    if (
                      conditionallyRender &&
                      !conditionallyRender({ modal, firstFormInitialValues })
                    ) {
                      return null;
                    }
                    if (requiresFormSubmitRef) {
                      return (
                        <Btn
                          disabled={isSubmitting}
                          actionHandler={() => {
                            if (arrayOfRefsToFormSubmitFunc[activeTab])
                              arrayOfRefsToFormSubmitFunc[activeTab]();
                          }}
                          key={`${index}-${bIndex}`}
                        />
                      );
                    }
                    return (
                      <Btn key={`${index}-${bIndex}`} disabled={isSubmitting} />
                    );
                  }
                )}
              </React.Fragment>
            )
        )}
      </DialogActions>
    </>
  );
};
