import React from "react";
import Button from "@material-ui/core/Button";
import axios from "axios";
import { createNumberMask } from "text-mask-addons/dist/textMaskAddons";
import {
  dispatchCloseConfirmDialogFormik,
  disableConfirmDialogPrimaryBtn,
  dispatchFetchJournalData,
  dispatchOpenConfirmDialogFormik,
  dispatchOpenSnackBar
} from "../../../index";
import Yup from "../../../yup";

const additionalActionPanelButtons = [
  <Button
    variant="contained"
    color="secondary"
    style={{ backgroundColor: "#357a38" }}
    onClick={() =>
      dispatchOpenConfirmDialogFormik({
        title: "   ",
        formObj: {
          blocks: [
            {
              fields: [
                {
                  label: "   ",
                  name: "rguId",
                  type: "formatted",
                  mask: createNumberMask({
                    prefix: "",
                    includeThousandsSeparator: false,
                    allowLeadingZeroes: true
                  }),
                  widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
                }
              ]
            }
          ],
          validationSchema: Yup.object({
            rguId: Yup.number()
              .positive()
              .required()
          })
        },
        initialValues: {
          rguId: ""
        },
        primaryButtonText: "",
        primaryButtonHandler: (values, { setSubmitting }) => {
          const { rguId } = values;
          axios
            .post(`/api/members/syncMemberById/${rguId}`)
            .then(({ status }) => {
              disableConfirmDialogPrimaryBtn(true);
              if (status === 201 || status === 200) {
                dispatchOpenSnackBar(
                  `   "${rguId}   `
                );
              }
              //      204    
              if (status === 204 || status === 404) {
                dispatchOpenSnackBar(
                  `   "${rguId}"  `
                );
              }
              setSubmitting(false);
              dispatchCloseConfirmDialogFormik();
              dispatchFetchJournalData();
            })
            .catch(error => {
              setSubmitting(false);
              if (
                error.response.status === 204 ||
                error.response.status === 404 ||
                error.status === 204
              ) {
                dispatchOpenSnackBar(
                  `      "${rguId}"`
                );
              }
            });
        }
      })
    }
  >
    
  </Button>
];

export default additionalActionPanelButtons;
