import { v4 } from "uuid";
import {
  collectionType,
  dutiesType,
  EventType,
  IOption,
  RowType,
  TypeEnum
} from "./types";

const ADD_EVENT = "ADD_EVENT";
const REMOVE_EVENT = "REMOVE_EVENT";
const CHANGE_DUTIES = "CHANGE_DUTIES";
const CHANGE_FROM = "CHANGE_FROM";
const CHANGE_TO = "CHANGE_TO";
const CHANGE_TYPE = "CHANGE_TYPE";
const CHANGE_PERFORMERS = "CHANGE_PERFORMERS";
const INIT = "INIT";

export type ActionType = {
  type: any;
  payload?: any;
};

export const addEvent = (rowIndex: number) => ({
  type: ADD_EVENT,
  payload: {
    rowIndex,
    event: {
      uniqueId: v4(),
      from: "",
      to: "",
      type: null,
      performers: [],
      canEdit: true,
      typeEnum: TypeEnum.ministration
    }
  }
});

export const removeEvent = (rowIndex: number, eventIndex: number) => ({
  type: REMOVE_EVENT,
  payload: {
    rowIndex,
    eventIndex
  }
});

export const changeFrom = (
  rowIndex: number,
  eventIndex: number,
  from: string
): ActionType => ({
  type: CHANGE_FROM,
  payload: {
    rowIndex,
    eventIndex,
    from
  }
});

export const changeTo = (
  rowIndex: number,
  eventIndex: number,
  to: string
): ActionType => ({
  type: CHANGE_TO,
  payload: {
    rowIndex,
    eventIndex,
    to
  }
});

export const changeType = (
  rowIndex: number,
  eventIndex: number,
  type: IOption
): ActionType => ({
  type: CHANGE_TYPE,
  payload: {
    rowIndex,
    eventIndex,
    type
  }
});

export const changePerformers = (
  rowIndex: number,
  eventIndex: number,
  performers: Array<IOption>
): ActionType => ({
  type: CHANGE_PERFORMERS,
  payload: {
    rowIndex,
    eventIndex,
    performers
  }
});

export const changeDuties = (rowIndex: number, duties: dutiesType) => ({
  type: CHANGE_DUTIES,
  payload: {
    rowIndex,
    duties
  }
});

export const setCollection = (collection: collectionType) => ({
  type: INIT,
  payload: collection
});

const naturalOrder = (
  { from: fromA }: EventType,
  { from: fromB }: EventType
) => {
  if (fromA > fromB) {
    return 1;
  }
  if (fromA < fromB) {
    return -1;
  }
  // a    b
  return 0;
};

//  touched: {
//     [x: string]: boolean;
//   };
export type StateType = {
  collection: Array<RowType>;
};

export function tableReducer(state: StateType, { type, payload }: ActionType) {
  switch (type) {
    case ADD_EVENT: {
      const { rowIndex, event } = payload;
      const newCollection = [...state.collection];
      newCollection[rowIndex] = {
        ...newCollection[rowIndex],
        events: [...newCollection[rowIndex].events, event].sort(naturalOrder)
      };
      return { ...state, collection: newCollection };
    }
    case CHANGE_DUTIES: {
      const newCollection = [...state.collection];
      const { rowIndex, duties } = payload;
      newCollection[rowIndex].duties = duties;
      return {
        ...state,
        collection: newCollection
      };
    }
    case CHANGE_FROM: {
      const newCollection = [...state.collection];
      const { rowIndex, eventIndex, from } = payload;
      const newRow = { ...newCollection[rowIndex] };
      const newEvents = [...newCollection[rowIndex].events];
      newEvents[eventIndex] = { ...newEvents[eventIndex], from };
      newRow.events = /^\d{2}:\d{2}$/.test(from)
        ? newEvents.sort(naturalOrder)
        : newEvents;
      newCollection[rowIndex] = newRow;
      return {
        ...state,
        collection: newCollection
      };
    }
    case CHANGE_TO: {
      const newCollection = [...state.collection];
      const { rowIndex, eventIndex, to } = payload;
      const newRow = { ...newCollection[rowIndex] };
      const newEvents = [...newCollection[rowIndex].events];
      newEvents[eventIndex] = { ...newEvents[eventIndex], to };
      newRow.events = newEvents;
      newCollection[rowIndex] = newRow;
      return {
        ...state,
        collection: newCollection
      };
    }
    case REMOVE_EVENT: {
      const newCollection = [...state.collection];
      const { rowIndex, eventIndex } = payload;
      const newRow = newCollection[rowIndex];
      newRow.events = newRow.events.filter((v, index) => index !== eventIndex);
      return {
        ...state,
        collection: newCollection
      };
    }
    case CHANGE_TYPE: {
      const newCollection = [...state.collection];
      const { rowIndex, eventIndex, type: eventType } = payload;
      const newRow = newCollection[rowIndex];
      newRow.events[eventIndex] = {
        ...newRow.events[eventIndex],
        type: eventType
      };
      return {
        ...state,
        collection: newCollection
      };
    }
    case CHANGE_PERFORMERS: {
      const newCollection = [...state.collection];
      const { rowIndex, eventIndex, performers } = payload;
      const newRow = newCollection[rowIndex];
      newRow.events[eventIndex] = {
        ...newRow.events[eventIndex],
        performers
      };
      return {
        ...state,
        collection: newCollection
      };
    }
    case INIT: {
      return { ...state, collection: payload };
    }
    default:
      return state;
  }
}
