const phone = "+{7}(000)000-00-00";

const date = "00.00.0000";

const daterange = "00.00.0000-00.00.0000";

const email = /^\S*@?\S*$/;

// const postalCode = /^[1-6]\d{0,5}$/;

const getMask = mask => {
  let result;
  const config = { phone, email, date, daterange };
  if (typeof config[mask] !== "undefined") {
    result = config[mask];
  }
  if (mask.startsWith("_") || mask.startsWith("#")) {
    result = mask.substr(1);
  }
  return result;
};

export default getMask;
