import { extractPayloadFromBaseSpringDateRestStructure } from "../../utils/commonFormFunctions";

export const rebuildReceivedValueLabelFullNameValueUniqueId = ({
  uniqueId,
  fullName
}) => ({
  label: fullName,
  value: uniqueId
});

export const rebuildReceivedValueLabelLabelValueUniqueId = ({
  uniqueId,
  label,
  enum: enumName
}) => ({
  label,
  value: uniqueId,
  enum: enumName
});

export const rebuildReceivedValueLabelLabelValueLabel = ({ label }) => ({
  label,
  value: label
});

export const rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue = data =>
  data.map(rebuildReceivedValueLabelLabelValueUniqueId);

export const constructRebuildReceivedOptions = functionToCallForEach => data =>
  extractPayloadFromBaseSpringDateRestStructure(data).map(
    functionToCallForEach
  );

export const constructRebuildReceivedOptionsLabelFullNameValueUniqueId = data =>
  extractPayloadFromBaseSpringDateRestStructure(data).map(
    rebuildReceivedValueLabelFullNameValueUniqueId
  );

export const constructRebuildReceivedOptionsLabelLabelValueUniqueId = data =>
  rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue(
    extractPayloadFromBaseSpringDateRestStructure(data)
  );

export const constructRebuildReceivedOptionsLabelLabelValueLabel = data =>
  extractPayloadFromBaseSpringDateRestStructure(data).map(
    rebuildReceivedValueLabelLabelValueLabel
  );
