import React from "react";
import { Grid, Typography } from "@material-ui/core";
import * as PropTypes from "prop-types";
import { Form } from "formik";
import { RenderBlock } from "./elementRenders/renderBlock";
import defineItemVisibility from "./utils/dependenciesUt";
import ReconstructDescription from "./utils/reconstructDescription";

export const RenderXmlDescribedForm = ({ formObj, values }) => (
  <Form>
    <Grid container spacing={32} direction="column">
      <Grid item>
        <Typography variant="h6">{formObj.info.name}</Typography>
      </Grid>
      <Grid item>
        <Typography component="div">
          <ReconstructDescription htmlInput={formObj.info.description} />
        </Typography>
      </Grid>
      {formObj.statement.blocks.block.map(
        block =>
          defineItemVisibility(block, formObj, values) && (
            <RenderBlock
              key={block.name}
              block={block}
              formObj={formObj}
              values={values}
            />
          )
      )}
    </Grid>
  </Form>
);

RenderXmlDescribedForm.propTypes = {
  formObj: PropTypes.shape({}).isRequired,
  values: PropTypes.shape({}).isRequired
};
