import { get, set } from "lodash";
import React from "react";
import { AutoSizer } from "react-virtualized";
import MuiTable from "mui-virtualized-table";
import { constructRequest } from "./constructRequest";
import {
  cancelButton,
  deleteButton,
  openEditButton,
  PrimaryButton
} from "../buttons";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "../../completePromiseThenRenderComponent";
import { findFieldsWithDictThenConstructArrayToDownload } from "../../../utils/commonFormFunctions";
import { getInitialValues } from "./constructConfigArrayForLoaderComponent";
import { FormikMUI } from "../../formikMUi/formikNewRealisation";
import {
  allowedActions,
  callDefaultErrorHandlerThenPopModal,
  disableConfirmDialogPrimaryBtn,
  dispatchFetchJournalData,
  dispatchOpenSnackBar,
  dispatchPopHeadModal
} from "../../../constants";
import { store } from "../../../store";
import { closeConfirmDialog, openConfirmDialog } from "../../../actions";
import { RenderModalContent } from "../renderModalContent";
import { RenderModalCreateForm } from "../renderModalContentWithOutTabs";
import { constructInitialValuesForEmptyForm } from "../../formikMUi/utils/initialValuesUtils";

export const rebuildValues = (values, form) => {
  let keysToRefactor = [];
  form.blocks.forEach(({ fields }) => {
    keysToRefactor = [
      ...keysToRefactor,
      ...fields.filter(
        field =>
          (field.type === "select" && get(values, field.name) !== null) ||
          (field.type === "file" && get(values, field.name) !== null)
      )
    ];
  });
  const reconstructedValues = { ...values };
  keysToRefactor.forEach(({ name, dictUrl, type }) => {
    const fieldValue = get(values, name);
    if (Array.isArray(fieldValue)) {
      set(
        reconstructedValues,
        name,
        fieldValue.map(
          form.doBeforeSend && form.doBeforeSend[type]
            ? value => form.doBeforeSend[type](value)
            : ({ value }) => `${dictUrl.split("/search")[0]}/${value}`
        )
      );
    } else {
      set(
        reconstructedValues,
        name,
        form.doBeforeSend && form.doBeforeSend[type]
          ? form.doBeforeSend[type](fieldValue)
          : `${dictUrl}/${fieldValue.value}`
      );
    }
  });

  return reconstructedValues;
};

const historyTableConfigObj = [
  { dataKey: "createdTimestamp", label: " ", relativeWidth: 1 },
  { dataKey: "initUser", label: "", relativeWidth: 1 },
  { dataKey: "text", label: "", relativeWidth: 2 }
];

const HistoryTable = ({ data, ...props }) => {
  return (
    <AutoSizer>
      {({ width, height }) => (
        <MuiTable
          data={data}
          columns={historyTableConfigObj.map(
            ({ label, dataKey, relativeWidth }) => ({
              name: dataKey,
              header: label,
              width: `${(100 / 4) * relativeWidth}%`,
              cellProps: { style: { paddingRight: 0 } }
            })
          )}
          isCellHovered={(column, rowData, hoveredColumn, hoveredRowData) =>
            rowData.uniqueId && rowData.uniqueId === hoveredRowData.uniqueId
          }
          {...props}
          includeHeaders
          fixedRowCount={1}
          width={width}
          height={height}
        />
      )}
    </AutoSizer>
  );
};

const constructHistoryTab = () => ({
  tabTitle: "",
  tabContent: {
    Component: ({ modal }) => (
      <div style={{ minHeight: "40vh", width: "100%" }}>
        <CompletePromiseThenRenderComponent
          config={[
            {
              whereToStore: "data",
              urlToDownload: `/api/commonHistory/search/allPresentByUuid?uuid=${
                modal.item.uuid
              }&projection=tab`,
              rebuildReceivedValue: ({ _embedded }) =>
                Object.values(_embedded)[0]
            }
          ]}
          Component={HistoryTable}
          PreloaderComponent={defaultPreloader}
          errorHandler={callDefaultErrorHandlerThenPopModal}
        />
      </div>
    )
  }
});

const constructTabsArray = ({
  formObj,
  passProps,
  functionToExecuteThenPassResultAsProps,
  tabButtons,
  historyTab
}) => {
  const { additionalTabs = [] } = formObj;
  return [
    {
      tabTitle: " ",
      tabContent: {
        Component: FormikMUI,
        passProps: { formObj, ...passProps },
        functionToExecuteThenPassResultAsProps
      },
      tabButtons
    },
    ...additionalTabs,
    historyTab
  ];
};

// todo ---    ,          
//       ""  -          
export const computePropsForFormikMUI = ({ formObj, mode }) => {
  const resultingConfigObj = {
    component: CompletePromiseThenRenderComponent,
    forwardedProps: {
      Component: RenderModalContent,
      tabs: constructTabsArray({
        formObj,
        passProps: {
          readOnly: mode === "view"
        },
        functionToExecuteThenPassResultAsProps: ({ modal }) => {
          return {
            onSubmit: (values, { setSubmitting }) => {
              const urlId = formObj.customId
                ? modal.item[formObj.customId]
                : modal.item.uniqueId;

              constructRequest(
                {
                  url: formObj.primaryActionUrl
                    ? `${formObj.primaryActionUrl}/${urlId}`
                    : `${formObj.url}/${urlId}`,
                  method: formObj.method,
                  data: rebuildValues(values, formObj)
                },
                ({ status }) => {
                  if (status === 200 || status === 201) {
                    dispatchOpenSnackBar(
                      ` "${formObj.titleItem}"  `
                    );
                  }
                  setSubmitting(false);
                  dispatchPopHeadModal();
                  dispatchFetchJournalData();
                },
                dispatchOpenSnackBar,
                dispatchPopHeadModal,
                setSubmitting
              );
            }
          };
        },
        tabButtons:
          mode === "edit"
            ? [
                {
                  Btn: PrimaryButton,
                  requiresFormSubmitRef: true
                }
              ]
            : [],
        historyTab: constructHistoryTab()
      }),
      //    -   (      1 ....)
      title:
        typeof formObj.title === "function"
          ? firstFormInitialValues =>
              `  ${formObj.title(firstFormInitialValues)}: `
          : `  ${formObj.title}: `,
      actionButtons: [cancelButton(() => dispatchPopHeadModal())],
      additionalActionButtonsDependedOnProps:
        formObj.additionalActionButtonsDependedOnProps,
      PreloaderComponent: defaultPreloader,
      errorHandler: er => callDefaultErrorHandlerThenPopModal(er)
    },
    functionsToExecute: []
  };
  if (mode === "create") {
    resultingConfigObj.forwardedProps = {
      Component: RenderModalCreateForm,
      formObj,
      title: `  ${formObj.title}`,
      actionButtons: [cancelButton(() => dispatchPopHeadModal())],
      readOnly: false,
      PreloaderComponent: defaultPreloader,
      errorHandler: er => callDefaultErrorHandlerThenPopModal(er)
    };

    resultingConfigObj.forwardedProps.config = findFieldsWithDictThenConstructArrayToDownload(
      formObj
    );
    resultingConfigObj.forwardedProps.initialValues = constructInitialValuesForEmptyForm(
      formObj
    );

    resultingConfigObj.forwardedProps.onSubmit = (values, { setSubmitting }) =>
      constructRequest(
        {
          url: formObj.primaryActionUrl
            ? formObj.primaryActionUrl
            : formObj.url,
          method: formObj.method,
          data: rebuildValues(values, formObj)
        },
        ({ status }) => {
          if (status === 201) {
            dispatchOpenSnackBar(
              ` "${formObj.titleItem}"  `
            );
          }
          setSubmitting(false);
          dispatchPopHeadModal();
          dispatchFetchJournalData();
        },
        dispatchOpenSnackBar,
        dispatchPopHeadModal,
        setSubmitting
      );
  }
  if (mode === "view") {
    resultingConfigObj.functionsToExecute = [
      ...resultingConfigObj.functionsToExecute,
      {
        func: modal => {
          const { category, page } = modal;
          const {
            forwardedProps: { actionButtons }
          } = resultingConfigObj;

          let resActionBtns = [...actionButtons];
          const allowedActionsVar = allowedActions();
          if (
            (allowedActionsVar[category] &&
              allowedActionsVar[category].includes &&
              allowedActionsVar[category].includes("delete")) ||
            (allowedActionsVar[category] &&
              allowedActionsVar[category][page] &&
              allowedActionsVar[category][page].includes("delete"))
          ) {
            resActionBtns = [
              ...actionButtons,
              deleteButton(() => {
                store.dispatch(
                  openConfirmDialog({
                    mainText: `  "${
                      formObj.titleItem ? formObj.titleItem : formObj.title
                    }": ${modal.item.label ||
                      modal.item.shortName ||
                      modal.item.surnameInitials ||
                      modal.item.uuid}?`,
                    primaryButtonHandler: () => {
                      disableConfirmDialogPrimaryBtn(true);
                      constructRequest(
                        {
                          url: `${formObj.url}/${modal.item.uniqueId}`,
                          method: "delete"
                        },
                        ({ status }) => {
                          disableConfirmDialogPrimaryBtn(true);
                          if (status === 204) {
                            dispatchOpenSnackBar(
                              ` "${
                                formObj.titleItem
                                  ? formObj.titleItem
                                  : formObj.title
                              }"  `
                            );
                          }
                          store.dispatch(closeConfirmDialog());
                          dispatchPopHeadModal();
                          dispatchFetchJournalData();
                        },
                        dispatchOpenSnackBar,
                        dispatchPopHeadModal
                      );
                    }
                  })
                );
              })
            ];
          }

          if (
            (allowedActionsVar[category].includes &&
              allowedActionsVar[category].includes("delete")) ||
            (allowedActionsVar[category] &&
              allowedActionsVar[category][page] &&
              allowedActionsVar[category][page].includes("edit"))
          ) {
            resActionBtns = [
              ...resActionBtns,
              openEditButton({
                modal: { ...modal, mode: "edit" }
              })
            ];
          }

          return resActionBtns;
        },
        passResultAs: "actionButtons"
      }
    ];
  }
  if (mode === "view" || mode === "edit") {
    /*            ""   ,
         ..           */
    // resultingConfigObj.forwardedProps.tabs = constructTabsArray(formObj);
    resultingConfigObj.functionsToExecute = [
      ...resultingConfigObj.functionsToExecute,
      {
        func: modal => [
          ...findFieldsWithDictThenConstructArrayToDownload(formObj),
          getInitialValues(modal, formObj)
        ],
        passResultAs: "config"
      }
    ];
  }
  if (mode === "edit") {
    resultingConfigObj.forwardedProps.title = `  ${
      formObj.title
    }: `;
  }

  if (mode === "profile") {
    resultingConfigObj.forwardedProps = {
      Component: RenderModalCreateForm,
      formObj,
      title: `   ${formObj.title}: `,
      actionButtons: [cancelButton(() => dispatchPopHeadModal())],
      readOnly: false,
      PreloaderComponent: defaultPreloader,
      errorHandler: er => callDefaultErrorHandlerThenPopModal(er)
    };
    resultingConfigObj.functionsToExecute = [
      ...resultingConfigObj.functionsToExecute,
      {
        func: modal => [
          ...findFieldsWithDictThenConstructArrayToDownload(formObj),
          {
            ...getInitialValues(modal, formObj, "initialValues")
          }
        ],
        passResultAs: "config"
      },
      {
        func: modal => (values, { setSubmitting }) =>
          constructRequest(
            {
              url: formObj.primaryActionUrl
                ? `${formObj.primaryActionUrl}/${modal.item.uniqueId}`
                : `${formObj.url}/${modal.item.uniqueId}`,
              method: formObj.method,
              data: rebuildValues(values, formObj)
            },
            ({ status }) => {
              if (status === 201) {
                dispatchOpenSnackBar(`   `);
              }
              setSubmitting(false);
              dispatchPopHeadModal();
            },
            dispatchOpenSnackBar,
            dispatchPopHeadModal,
            setSubmitting
          ),
        passResultAs: "onSubmit"
      }
    ];

    resultingConfigObj.forwardedProps.renderSubmitButton = true;
    resultingConfigObj.forwardedProps.isInitialValid = true;
  }
  return resultingConfigObj;
};
