import { Domain } from "@material-ui/icons";
import additionalActionPanelButtons from "./additionalButtons";
import forms from "./form";
import { baseDictResponseRefactor } from "../../../index";
import { extractPayloadFromBaseSpringDateRestStructure } from "../../../../utils/commonFormFunctions";

const members = {
  navigationLabel: "",
  requiresFunction: [
    {
      functionName: "administration_member_modifier",
      allowedActions: ["create", "view", "edit", "delete"]
    },
    {
      functionName: "member_member_modifier",
      allowedActions: ["create", "view", "edit", "delete"]
    }
  ],
  url: "/api/journalMembers",
  icon: Domain,
  additionalActionPanelButtons,
  filtersPanel: {
    filters: [
      {
        label: "",
        name: "uniqueId",
        type: "select",
        dictUrl: "/api/structureMember/search/present",
        rebuildReceivedOptions: data => {
          const extractedData = extractPayloadFromBaseSpringDateRestStructure(
            data
          );
          return Array.isArray(extractedData)
            ? extractedData.map(({ uniqueId, fullName }) => ({
                label: fullName,
                value: uniqueId
              }))
            : [];
        }
      },
      { label: "  ", name: "rguId", type: "text" },
      {
        label: " ",
        name: "supremeParentId",
        type: "select",
        dictUrl: "/api/structureMember/search/present",
        rebuildReceivedOptions: data =>
          baseDictResponseRefactor(data).map(({ fullName, uniqueId }) => ({
            label: fullName,
            value: uniqueId
          }))
      }
    ]
  },
  tableHeader: [
    {
      label: "",
      sortable: true,
      path: "fullName",
      width: "40%"
    },
    {
      label: "  ",
      sortable: true,
      path: "rguId",
      width: "15%"
    },
    {
      label: " ",
      sortable: true,
      path: "supremeParentFullName",
      width: "45%"
    },
    {
      label: "     ",
      sortable: true,
      path: "synchronizeTimestamp",
      width: "20%"
    }
  ],
  forms: { ...forms }
};
export default members;
