import { Button, Grid } from "@material-ui/core";
import React from "react";
import { connect } from "react-redux";

export const PrivateAdditionalButtons = ({ buttons, computedFilters }) => {
  return buttons.map(({ text, onClick, component: Component, ...rest }) => {
    const onClickHandler = () => onClick({ computedFilters });
    return (
      <Grid item key={text}>
        {Component ? (
          <Component onClick={onClickHandler} {...rest}>
            {text}
          </Component>
        ) : (
          <Button onClick={onClickHandler} {...rest}>
            {text}
          </Button>
        )}
      </Grid>
    );
  });
};

const mapStateToProps = state => ({
  computedFilters: state.filtersReducer.computedFilters
});

export const AdditionalButtons = connect(mapStateToProps)(
  PrivateAdditionalButtons
);
