import { emailMask } from "text-mask-addons/dist/textMaskAddons";
import Yup from "../../yup";
import { computePropsForFormikMUI } from "../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { phoneMask } from "../../masks";
import { fullWidth, halfWith } from "../../widths";
import {
  constructRebuildReceivedOptionsLabelLabelValueUniqueId,
  rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue,
  rebuildReceivedValueLabelLabelValueUniqueId
} from "../../rebuildRecieved";
import { saveFileInCommonFiles } from "../../baseFieldProps";

const baseForm = {
  title: "",
  titleItem: "",
  headerText: "fullname",
  url: "/api/sysUsers",
  projection: "formSysUser",
  primaryActionUrl: "/api/user/sysUser",
  method: "post",
  doBeforeSend: { select: ({ value }) => value },
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "photo",
          type: "file",
          doBeforeSetFieldValue: saveFileInCommonFiles,
          accept: "image/*",
          widths: fullWidth
        },
        { name: "accost", type: "text", label: "", widths: halfWith },
        {
          name: "sanCode",
          label: "",
          type: "select",
          dictUrl: "/api/sysDictSan/search/present",
          widths: halfWith
        },
        {
          name: "firstname",
          type: "text",
          label: ""
        },
        {
          name: "surname",
          type: "text",
          label: ""
        },
        {
          name: "middlename",
          type: "text",
          label: ""
        },
        {
          name: "fullname",
          type: "text",
          label: "",
          hidden: true
        },
        {
          name: "birthday",
          label: " ",
          type: "date",
          widths: halfWith
        },
        {
          name: "hirotonyday",
          label: " ",
          type: "date",
          widths: halfWith
        },
        {
          name: "member",
          label: " ",
          type: "select",
          dictUrl: "/api/strMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: fullWidth
        },
        {
          name: "specializationCode",
          label: "",
          type: "select",
          dictUrl: "/api/sysDictSpecialization/search/present",
          widths: halfWith
        }
      ]
    },
    {
      blockTitle: " ",
      fields: [
        {
          name: "phone",
          label: "\\  ",
          type: "formatted",
          mask: phoneMask,
          uniqueCheck: {
            url: "/api/sysAuthen/search/existsByName",
            constructParams: (v, value) => ({
              name: value
            })
          }
        },
        {
          name: "password",
          type: "password",
          label: ""
        },
        {
          name: "isActiveLogin",
          label: "  ",
          type: "checkbox",
          defaultValue: true
        },
        {
          name: "role",
          label: "",
          type: "select",
          dictUrl: "/api/user/role",
          rebuildReceivedOptions: rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        }
      ]
    },
    {
      blockTitle: " ",
      fields: [
        {
          name: "email",
          label: "E-mail",
          type: "formatted",
          mask: emailMask
        },
        {
          name: "isSendEmail",
          label: " email  ",
          type: "checkbox"
        },
        {
          name: "isSendMessage",
          label: " push ",
          type: "checkbox"
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    surname: Yup.string().required(),
    firstname: Yup.string().required(),
    middlename: Yup.string(),
    // fullName: Yup.string(),
    accost: Yup.string(),
    phone: Yup.string().required(),
    email: Yup.string(),
    password: Yup.string().required(),
    san: Yup.object().nullable(),
    role: Yup.object()
      .nullable()
      .required(),
    isActiveLogin: Yup.bool(),
    specializationCode: Yup.object().nullable(),
    member: Yup.object().nullable()
  })
};

const profile = {
  title: "",
  titleItem: "",
  headerText: "fullname",
  url: "/api/sysUsers",
  projection: "formSysUser",
  primaryActionUrl: "/api/user/sysUser",
  method: "patch",
  doBeforeSend: { select: ({ value }) => value },
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "photo",
          type: "file",
          doBeforeSetFieldValue: saveFileInCommonFiles,
          accept: "image/*",
          widths: fullWidth
        },
        {
          name: "firstname",
          type: "text",
          label: ""
        },
        {
          name: "surname",
          type: "text",
          label: ""
        },
        {
          name: "middlename",
          type: "text",
          label: ""
        },
        { name: "accost", type: "text", label: "" },
        {
          name: "fullname",
          type: "text",
          label: "",
          hidden: true
        },
        {
          name: "birthday",
          label: " ",
          type: "date",
          widths: { md: 2, lg: 2, xl: 2 }
        },
        {
          name: "sanCode",
          label: "",
          type: "select",
          dictUrl: "/api/sysDictSan/search/present",
          widths: halfWith,
          readOnly: true
        },
        {
          name: "hirotonyday",
          label: " ",
          type: "date"
        },
        {
          name: "member",
          label: " ",
          type: "select",
          dictUrl: "/api/strMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: fullWidth,
          readOnly: true
        },
        {
          name: "specializationCode",
          label: "",
          type: "select",
          dictUrl: "/api/sysDictSpecialization/search/present",
          widths: halfWith,
          readOnly: true
        }
      ]
    },
    {
      blockTitle: " ",
      fields: [
        {
          name: "phone",
          label: "\\  ",
          type: "formatted",
          mask: phoneMask,
          uniqueCheck: {
            url: "/api/sysAuthen/search/existsByName",
            constructParams: (v, value) => ({
              name: value
            })
          }
        },
        {
          name: "password",
          type: "password",
          label: ""
        },
        {
          name: "role",
          label: "",
          type: "select",
          dictUrl: "/api/user/role",
          rebuildReceivedOptions: rebuildReceivedOptionsLabelAsLabelUniqueIdAsValue,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          readOnly: true,
          widths: halfWith
        }
      ]
    },
    {
      blockTitle: " ",
      fields: [
        {
          name: "email",
          label: "E-mail",
          type: "formatted",
          mask: emailMask
        },
        {
          name: "isSendEmail",
          label: " email  ",
          type: "checkbox"
        },
        {
          name: "isSendMessage",
          label: " push ",
          type: "checkbox"
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    surname: Yup.string().required(),
    firstname: Yup.string().required(),
    middlename: Yup.string(),
    // fullName: Yup.string(),
    accost: Yup.string(),
    phone: Yup.string().required(),
    email: Yup.string(),
    password: Yup.string().required(),
    san: Yup.object().nullable(),
    role: Yup.object()
      .nullable()
      .required(),
    specializationCode: Yup.object().nullable(),
    member: Yup.object().nullable()
  })
};

const forms = {
  create: computePropsForFormikMUI({ formObj: baseForm, mode: "create" }),
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" }),
  edit: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      method: "patch",
      doBeforeSend: { select: ({ value }) => value },
      validationSchema: Yup.object({
        surname: Yup.string().required(),
        firstname: Yup.string().required(),
        middlename: Yup.string(),
        // fullName: Yup.string(),
        accost: Yup.string(),
        phone: Yup.string().required(),
        email: Yup.string(),
        password: Yup.string(),
        san: Yup.object().nullable(),
        role: Yup.object()
          .nullable()
          .required(),
        isActiveLogin: Yup.bool(),
        specializationCode: Yup.object().nullable(),
        member: Yup.object().nullable()
      })
    },
    mode: "edit"
  }),
  profile: computePropsForFormikMUI({ formObj: profile, mode: "profile" })
};
export default forms;
