import { NavLink } from "react-router-dom";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import InboxIcon from "@material-ui/core/SvgIcon/SvgIcon";
import ListItemText from "@material-ui/core/ListItemText";
import * as PropTypes from "prop-types";
import React from "react";
import { INITIAL_PARAMETERS } from "../../constants/intialParameters";

export function RenderCollapsibleMenuItem({
  category,
  page,
  classes,
  customIcon: CustomIcon,
  activeCategory,
  activePage,
  menuMinimized
}) {
  return (
    <NavLink
      to={`/journal/${category}/${page}`}
      exact
      className={classes.navlink}
      activeClassName={classes.navlinkActive}
      aria-current="page"
    >
      <ListItem button className={classes.nested} disableGutters>
        <ListItemIcon
          classes={{
            root: classes.listIconRoot
          }}
        >
          {CustomIcon ? (
            <CustomIcon
              color={
                activeCategory === category && activePage === page
                  ? "primary"
                  : undefined
              }
            />
          ) : (
            <InboxIcon
              color={
                activeCategory === category && activePage === page
                  ? "primary"
                  : undefined
              }
            />
          )}
        </ListItemIcon>
        {menuMinimized && (
          <ListItemText
            primaryTypographyProps={{
              color:
                activeCategory === category && activePage === page
                  ? "primary"
                  : undefined
            }}
          >
            {INITIAL_PARAMETERS[category].pages[page].navigationLabel}
          </ListItemText>
        )}
      </ListItem>
    </NavLink>
  );
}

RenderCollapsibleMenuItem.defaultProps = {
  customIcon: undefined,
  activePage: undefined
};

RenderCollapsibleMenuItem.propTypes = {
  category: PropTypes.string.isRequired,
  page: PropTypes.string.isRequired,
  classes: PropTypes.shape({}).isRequired,
  customIcon: PropTypes.func,
  activeCategory: PropTypes.string.isRequired,
  activePage: PropTypes.string,
  menuMinimized: PropTypes.bool.isRequired
};
