import axios from "axios";
import { computePropsForFormikMUI } from "../../../../components/modalWrapper/utils/computePropsForForMikMUI";
import Yup from "../../../yup";
import { extractPayloadFromBaseSpringDateRestStructure } from "../../../../utils/commonFormFunctions";
import {
  constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
  rebuildReceivedValueLabelFullNameValueUniqueId
} from "../../../rebuildRecieved";

const baseForm = {
  title: " ",
  titleItem: " ",
  headerText: "label",
  url: "/api/mvObject",
  projection: "formMvObject",
  method: "post",
  doBeforeSend: {
    file: ({ value }) => value,
    select: ({ value }) => value
  },
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "label",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "",
          name: "description",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "",
          name: "direction",
          type: "select",
          dictUrl: "/api/mvDirection/search/present",
          readOnly: true
        },
        {
          label: "",
          name: "version",
          type: "text"
        },
        {
          label: " ",
          name: "mainCode",
          type: "text"
        },
        {
          label: "",
          name: "mvType",
          type: "select",
          dictUrl: "/api/mvType/search/present",
          rebuildReceivedOptions: data => {
            const extractedData = extractPayloadFromBaseSpringDateRestStructure(
              data
            );
            return Array.isArray(extractedData)
              ? extractedData.map(({ code, label }) => ({
                  label,
                  value: code
                }))
              : [];
          },
          rebuildReceivedValue: data => ({
            label: data.label,
            value: data.code
          })
        },
        {
          label: " ",
          name: "provider",
          type: "select",
          dictUrl: "/api/mvProvider/search/present",
          widths: { md: 12, lg: 12, xl: 12 }
        },
        {
          label: " ",
          name: "directionUrl",
          type: "text"
        },
        {
          label: "",
          name: "member",
          type: "select",
          dictUrl: "/api/structureMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelFullNameValueUniqueId,
          widths: { md: 12, lg: 12, xl: 12 }
        },
        {
          label: " ",
          name: "isInternal",
          type: "checkbox",
          hidden: true
        },
        {
          label: "XML   ",
          name: "xmlIn",
          type: "file",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        },
        {
          label: "XML   ",
          name: "xmlOut",
          type: "file",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        },
        {
          label: " -",
          name: "needEs",
          type: "checkbox"
        },
        {
          label: "",
          name: "isBlock",
          type: "checkbox"
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    direction: Yup.object()
      .nullable()
      .required(),
    isInternal: Yup.bool(),
    version: Yup.string().when("isInternal", {
      is: true,
      then: Yup.string(),
      otherwise: Yup.string().required()
    }),
    mainCode: Yup.string().required(),
    mvType: Yup.object().when("isInternal", {
      is: true,
      then: Yup.object().nullable(),
      otherwise: Yup.object()
        .nullable()
        .required()
    }),
    provider: Yup.object().when("isInternal", {
      is: true,
      then: Yup.object().nullable(),
      otherwise: Yup.object()
        .nullable()
        .required()
    })
  })
};

const forms = {
  create: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      url: "/api/mv/structureMv",
      blocks: [
        {
          blockTitle: " ",
          fields: [
            {
              label: "",
              name: "label",
              type: "text",
              additionalProps: {
                multiline: true,
                fullWidth: true,
                rowsMax: 6
              }
            },
            {
              label: "",
              name: "description",
              type: "text",
              additionalProps: {
                multiline: true,
                fullWidth: true,
                rowsMax: 6
              },
              widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
            },
            {
              label: "  ()",
              name: "member",
              type: "select",
              dictUrl: "/api/structureMember/search/present",
              rebuildReceivedOptions: data => {
                const extractedData = extractPayloadFromBaseSpringDateRestStructure(
                  data
                );
                return Array.isArray(extractedData)
                  ? extractedData.map(({ uniqueId, fullName }) => ({
                      label: fullName,
                      value: uniqueId
                    }))
                  : [];
              },
              rebuildReceivedValue: data => ({
                label: data.fullName,
                value: data.uniqueId
              }),
              widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
            },
            {
              label: " ",
              name: "mainCode",
              type: "text",
              uniqueCheck: {
                url: "/api/mvObject/search/existsByMainCode",
                constructParams: (v, value) => ({
                  mainCode: value
                })
              },
              widths: { xs: 8, sm: 8, md: 8, lg: 8, xl: 8 }
            },
            {
              label: "",
              name: "isBlock",
              type: "checkbox"
            },
            {
              label: "XML   ",
              name: "xmlIn",
              type: "file",
              doBeforeSetFieldValue: value =>
                axios.post("/api/file/save", value, {
                  "Content-Type": "multipart/form-data"
                }),
              widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
            },
            {
              label: "XML   ",
              name: "xmlOut",
              type: "file",
              doBeforeSetFieldValue: value =>
                axios.post("/api/file/save", value, {
                  "Content-Type": "multipart/form-data"
                }),
              widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
            }
          ]
        }
      ],
      validationSchema: Yup.object({
        label: Yup.string().required(),
        mainCode: Yup.string().required(),
        member: Yup.object()
          .nullable()
          .required(),
        xmlIn: Yup.object()
          .nullable()
          .required(),
        xmlOut: Yup.object()
          .nullable()
          .required()
      })
    },
    mode: "create"
  }),
  view: computePropsForFormikMUI({ formObj: baseForm, mode: "view" }),
  edit: computePropsForFormikMUI({
    formObj: {
      ...baseForm,
      url: "/api/mvObject",
      method: "patch",
      primaryActionUrl: "/api/mv/structureMv"
    },
    mode: "edit"
  })
};

export default forms;
