import React, { useState } from "react";
import {
  Button,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  LinearProgress,
  Typography
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import { makeStyles } from "@material-ui/styles";
import { FormikMUI } from "../formikMUi/formikNewRealisation";

const getStyles = makeStyles({
  DialogTitleRoot: {
    paddingBottom: "10px"
  },
  closeBtn: {
    position: "absolute",
    right: "24px",
    top: "15px"
  },
  tabTitle: {
    fontSize: "15px"
  }
});

export const RenderModalCreateForm = ({
  initialValues,
  formObj,
  title,
  optionsMap,
  closeHandler,
  onSubmit,
  actionButtons
}) => {
  let refToFormSubmitFunc;
  const [isSubmitting, setIsSubmitting] = useState(false);
  const classes = getStyles();
  return (
    <>
      <DialogTitle classes={{ root: classes.DialogTitleRoot }}>
        <Grid container justify="space-between" alignItems="center">
          <Typography variant="h6">
            {`${typeof title === "function" ? title(initialValues) : title}${
              initialValues[formObj.headerText]
            }`.toUpperCase()}
          </Typography>
          <IconButton className={classes.closeBtn} onClick={closeHandler}>
            <CloseIcon />
          </IconButton>
        </Grid>
      </DialogTitle>
      <DialogContent>
        <FormikMUI
          onSubmit={onSubmit}
          formObj={formObj}
          bindFormSubmit={ref => {
            refToFormSubmitFunc = ref;
          }}
          bindIsSubmitting={setIsSubmitting}
          initialValues={initialValues}
          optionsMap={optionsMap}
        />
        {isSubmitting && <LinearProgress />}
      </DialogContent>
      <DialogActions>
        {/*       */}
        {actionButtons}
        <Button
          color="primary"
          variant="contained"
          disabled={isSubmitting}
          onClick={() => refToFormSubmitFunc()}
        >
          
        </Button>
      </DialogActions>
    </>
  );
};
