import {
  RESET_COMPUTED_FILTERS,
  SET_COMPUTED_FILTERS,
  TOGGLE_IS_COLLAPSED,
  SET_FILTERS_OPTIONS_MAP
} from "../constants";

const initialState = { computedFilters: {}, isCollapsed: true, optionsMap: {} };

export type IFiltersInitialState = {
  readonly computedFilters: object;
  readonly isCollapsed: boolean;
  readonly optionsMap: object;
}

export default function filtersReducer(
  state: IFiltersInitialState = initialState,
  action:any
) {
  switch (action.type) {
    case TOGGLE_IS_COLLAPSED: {
      return {
        ...state,
        isCollapsed:
          action.payload && action.payload.isCollapsed
            ? action.payload.isCollapsed
            : !state.isCollapsed
      };
    }
    case RESET_COMPUTED_FILTERS: {
      return {
        ...state,
        computedFilters: {},
        isCollapsed: true
      };
    }
    case SET_COMPUTED_FILTERS: {
      return {
        ...state,
        computedFilters: { ...action.payload }
      };
    }
    case SET_FILTERS_OPTIONS_MAP: {
      return {
        ...state,
        optionsMap: action.payload
      };
    }
    default:
      return state;
  }
}
