import React from "react";
import { Grid, Divider, Typography } from "@material-ui/core";
import { get } from "lodash";
import { connect } from "formik";
import * as PropTypes from "prop-types";
import uuidv4 from "uuid/v4";
import { renderField } from "./renderField";
import { calcFieldWidth } from "./utils/calcFieldWidth";

const PrivateRenderBlock = ({
  block: { blockTitle, fields, conditionallyRender },
  classes,
  bIndex,
  formik: {
    status: { registeredFields, readOnly },
    status,
    initialValues,
    setFieldValue,
    values
  }
}) => {
  if (conditionallyRender && !conditionallyRender(values)) {
    return null;
  }

  return (
    <React.Fragment>
      <Grid
        container
        alignItems="center"
        style={
          bIndex === 0
            ? { marginTop: "8px", marginBottom: "8px" }
            : { marginTop: "20px", marginBottom: "8px" }
        }
      >
        {blockTitle ? (
          <React.Fragment>
            <Typography variant="subtitle2" style={{ fontSize: "15px" }}>
              {blockTitle.toUpperCase()}
            </Typography>
            <Divider className={classes.divider} />
          </React.Fragment>
        ) : null}
      </Grid>
      <Grid container spacing={8}>
        {fields.map(field =>
          get(registeredFields, field.name) ? (
            <Grid item key={field.name || uuidv4()} {...calcFieldWidth(field)}>
              {renderField({
                field,
                classes,
                readOnly,
                initialValues,
                setFieldValue,
                status
              })}
            </Grid>
          ) : null
        )}
      </Grid>
    </React.Fragment>
  );
};

PrivateRenderBlock.propTypes = {
  block: PropTypes.shape({}).isRequired,
  classes: PropTypes.shape({}).isRequired,
  bIndex: PropTypes.number.isRequired,
  formik: PropTypes.shape({
    status: PropTypes.shape({
      registeredFields: PropTypes.shape({}).isRequired,
      readOnly: PropTypes.bool.isRequired
    }),
    initialValues: PropTypes.shape({}).isRequired,
    setFieldValue: PropTypes.func.isRequired
  }).isRequired
};

export const RenderBlock = connect(PrivateRenderBlock);
