import {
  TOGGLE_SNACKBAR,
  TOGGLE_CONFIRM_DIALOG,
  HIDE_CONFIRM_DIALOG,
  DISABLE_PRIMARY_BTN_CONFIRM_DIALOG,
  OPEN_CONFIRM_DIALOG_FORMIK,
  CLOSE_CONFIRM_DIALOG_FORMIK,
  SET_ALLOWED_ACTIONS,
  OPEN_SCHEDULE_CREATION_MASTER,
  CLOSE_SCHEDULE_CREATION_MASTER
} from "../constants";

const confirmDialog = {
  open: false,
  title: " ",
  mainText: "",
  primaryButtonText: "",
  secondaryButtonText: "",
  primaryButtonHandler: () => {}
};
const confirmDialogFormik = {
  open: false,
  title: " ",
  primaryButtonText: "",
  secondaryButtonText: "",
  primaryButtonHandler: () => {},
  formObj: {
    blocks: []
  }
};

const initialState = {
  isSnackBarOpened: false,
  message: "",
  confirmDialog,
  confirmDialogFormik,
  allowedActions: {},
  scheduleCreationMasterIsOpen: false
};

export default function wrapperReducer(state = initialState, action) {
  switch (action.type) {
    case TOGGLE_SNACKBAR: {
      return {
        ...state,
        isSnackBarOpened: action.payload.isSnackBarOpened,
        message: action.payload.message ? action.payload.message : ""
      };
    }
    case TOGGLE_CONFIRM_DIALOG: {
      const {
        open,
        title,
        mainContent,
        primaryButtonHandler,
        mainText,
        primaryButtonText,
        secondaryButtonText,
        primaryButtonDisabled
      } = action.payload;
      return {
        ...state,
        confirmDialog: {
          ...state.confirmDialog,
          title,
          open,
          mainContent,
          primaryButtonHandler,
          mainText,
          primaryButtonText:
            primaryButtonText || confirmDialog.primaryButtonText,
          secondaryButtonText:
            secondaryButtonText || confirmDialog.secondaryButtonText,
          primaryButtonDisabled: !!primaryButtonDisabled
        }
      };
    }
    case DISABLE_PRIMARY_BTN_CONFIRM_DIALOG: {
      const { primaryButtonDisabled } = action.payload;
      return {
        ...state,
        confirmDialog: {
          ...state.confirmDialog,
          primaryButtonDisabled
        }
      };
    }
    case HIDE_CONFIRM_DIALOG: {
      return {
        ...state,
        confirmDialog: { ...confirmDialog }
      };
    }
    case OPEN_CONFIRM_DIALOG_FORMIK: {
      const { open, formObj, ...rest } = action.payload;
      return {
        ...state,
        confirmDialogFormik: {
          ...confirmDialogFormik,
          open,
          formObj,
          ...rest
        }
      };
    }
    case CLOSE_CONFIRM_DIALOG_FORMIK: {
      return {
        ...state,
        confirmDialogFormik
      };
    }
    case SET_ALLOWED_ACTIONS: {
      return {
        ...state,
        allowedActions: action.payload
      };
    }
    case OPEN_SCHEDULE_CREATION_MASTER: {
      return {
        ...state,
        scheduleCreationMasterIsOpen: true
      };
    }
    case CLOSE_SCHEDULE_CREATION_MASTER: {
      return { ...state, scheduleCreationMasterIsOpen: false };
    }
    default:
      return state;
  }
}
