import { SwapVert } from "@material-ui/icons";
import { baseDictResponseRefactor } from "../../../index";
import forms from "./forms";

const logMv = {
  navigationLabel: " ",
  requiresFunction: [
    {
      functionName: "administration_mv_log_view",
      allowedActions: ["view"]
    },
    { functionName: "member_mv_log_view", allowedActions: ["view"] }
  ],
  doBeforeSend: {
    select: ({ value }) => value
  },
  url: "/api/journalMvActionLogs",
  icon: SwapVert,
  filtersPanel: {
    filters: [
      {
        label: "  ",
        name: "mvName",
        type: "text"
      },
      {
        label: "",
        name: "status",
        type: "select",
        dictUrl: "/api/mvActionStatuses/search/present",
        rebuildReceivedOptions: data =>
          baseDictResponseRefactor(data).map(({ label }) => ({
            label,
            value: label
          }))
      },
      {
        label: " ",
        name: "reqTimestamp",
        type: "date-range"
      },
      {
        label: " ",
        name: "outTimestamp",
        type: "date-range"
      },
      {
        label: "",
        name: "provider",
        type: "select",
        dictUrl: "/api/mvActionLogs/getProviderList",
        rebuildReceivedOptions: data =>
          data.map(({ label }) => ({ label, value: label }))
      },
      {
        label: "",
        name: "consumer",
        type: "select",
        dictUrl: "/api/mvActionLogs/getConsumerList",
        rebuildReceivedOptions: data =>
          data.map(({ label }) => ({ label, value: label }))
      },
      {
        label: " ",
        name: "serviceUuid",
        type: "text"
      },
      {
        label: " ",
        name: "uuidIn",
        type: "text"
      },
      {
        label: " ",
        name: "uuidOut",
        type: "text"
      },
      {
        label: " ",
        name: "errorCode",
        type: "text"
      },
      {
        label: " ",
        name: "errorText",
        type: "text"
      }
    ]
  },
  tableHeader: [
    {
      label: "  ",
      sortable: true,
      dynamicWidth: true,
      path: "mvName",
      width: "30%"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "reqTimestamp",
      width: "15%"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "outTimestamp",
      width: "15%"
    },
    {
      label: "",
      sortable: true,
      dynamicWidth: true,
      path: "provider",
      width: "30%"
    },
    {
      label: "",
      sortable: true,
      dynamicWidth: true,
      path: "consumer",
      width: "30%"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "serviceUuid"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "uuidIn"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "uuidOut"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "errorCode",
      width: "15%"
    },
    {
      label: " ",
      sortable: true,
      dynamicWidth: true,
      path: "errorText"
    }
  ],
  forms: { ...forms }
};

export default logMv;
