import React, { Suspense, useEffect, useReducer, useState } from "react";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/styles";
import { startOfWeek, endOfWeek, startOfMonth, endOfMonth } from "date-fns";
import { ru } from "date-fns/locale";
import {
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  IconButton,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  Theme
} from "@material-ui/core";
import {
  AddOutlined,
  FileCopyOutlined,
  PrintOutlined,
  CalendarViewDayOutlined,
  CalendarToday,
  SearchOutlined,
  ExpandMore
} from "@material-ui/icons";
import { connect } from "react-redux";
import axios from "axios";
import { FormikActions, FormikValues } from "formik";
import { defaultPreloader } from "../completePromiseThenRenderComponent";
import { ReactDateRangeInline } from "../IntegrateDatePickerWithMUI/inlineReactDateRange";
import { ReactSelectMUI } from "../reactSelectMUI";
import { DispatchType, ScheduleTable } from "./ScheduleTable";
import { openScheduleCreationMaster } from "../../actions";
import { openConfirmDialogFormik } from "../../actions/typedActions";
import { defaultErrorHandler } from "../../utils/defaultErrorHandler";
import { ActionType, setCollection, tableReducer } from "./tableReducer";
import { russianDate } from "./formatters";

const getStyles = makeStyles((theme: Theme) => ({
  header: {
    padding: theme.spacing.unit
  },
  mainContainer: {
    padding: theme.spacing.unit,
    marginBottom: theme.spacing.unit,
    height: " 100%",
    width: "100%",
    overflowY: "auto",
    display: "flex",
    flexWrap: "nowrap",
    flexDirection: "column"
  },
  actionPanel: {
    marginBottom: theme.spacing.unit
  },
  actionBtn: {}
}));

const ExpandMoreRendered = <ExpandMore />;

function constructParamsFromTo(selectedRange: SelectedRangeT, date: Date) {
  if (selectedRange === "week") {
    return `?from=${russianDate(
      startOfWeek(date, { locale: ru })
    )}&to=${russianDate(
      endOfWeek(date, {
        locale: ru
      })
    )}`;
  }
  return `?from=${russianDate(startOfMonth(date))}&to=${russianDate(
    endOfMonth(date)
  )}`;
}

const mapSurinameInitialsAsLabelUniqueIdAsValue = ({
  surnameInitials,
  uniqueId
}: {
  surnameInitials: string;
  uniqueId: number;
}) => ({ label: surnameInitials, value: uniqueId });

type confObjType = {
  title: string;
};

type PrivateScheduleJournalProps = {
  confObj: confObjType;
  openSnackBar: Function;
  openCreationMaster: Function;
};

type SelectedRangeT = "week" | "month";

const mapLabelAsLabelUniqueIdAsValue = ({
  label,
  uniqueId
}: {
  label: string;
  uniqueId: number;
}) => ({
  label,
  value: uniqueId
});

const PrivateScheduleJournal = ({
  confObj: { title },
  openSnackBar,
  openCreationMaster
}: PrivateScheduleJournalProps) => {
  const classes = getStyles();
  const [date, setDate] = useState(new Date());
  const [showAllEvents, setShowAllEvents] = useState(false);
  const [ministration, setMinistration] = useState(null);
  const [user, setUser] = useState(null);
  const [selectedRange, setSelectedRange] = useState<SelectedRangeT>("week");
  const [types, setTypes] = useState([]);
  const [priests, setPriests] = useState([]);
  const [{ collection }, dispatch] = useReducer(tableReducer, {
    collection: []
  });
  //    
  useEffect(() => {
    const source = axios.CancelToken.source();
    axios
      .all([
        axios.get("/api/ministration/type", { cancelToken: source.token }),
        axios.get("/api/ministration/priest", { cancelToken: source.token })
      ])
      .then(
        axios.spread((typesResponse, priestsResponse) => {
          setTypes(typesResponse.data.map(mapLabelAsLabelUniqueIdAsValue));
          setPriests(
            priestsResponse.data.map(mapSurinameInitialsAsLabelUniqueIdAsValue)
          );
        })
      )
      .catch(error => {
        if (!axios.isCancel(error)) {
          defaultErrorHandler(error);
        }
      });
    return () => {
      source.cancel("Cancelling in cleanup");
    };
  }, []);

  //      , ,  ,    
  useEffect(() => {
    const source = axios.CancelToken.source();
    const url = `/api/events/log${constructParamsFromTo(selectedRange, date)}`;
    axios
      .get(url, { cancelToken: source.token })
      .then(({ data }) => {
        if (data.length === 0) {
          openCreationMaster();
        }
        dispatch(setCollection(data));
      })
      .catch(error => {
        if (!axios.isCancel(error)) {
          defaultErrorHandler(error);
        }
      });
    return () => {
      source.cancel("Cancelling in cleanup");
    };
  }, [date, openCreationMaster, selectedRange]);

  return (
    <Paper square className={classes.mainContainer} elevation={5}>
      <Typography variant="h6" className={classes.header}>
        {title.toUpperCase()}
      </Typography>
      <Suspense fallback={defaultPreloader}>
        <ExpansionPanel className={classes.actionPanel}>
          <ExpansionPanelSummary expandIcon={ExpandMoreRendered}>
             
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Grid container wrap="nowrap">
              <Grid item>
                <Grid container direction="column" spacing={24}>
                  <Grid item>
                    <Grid container spacing={24}>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                          onClick={() => openSnackBar("joj")}
                        >
                          <AddOutlined />
                          
                        </Button>
                      </Grid>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                        >
                          <AddOutlined />
                          
                        </Button>
                      </Grid>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                        >
                          <AddOutlined />
                          
                        </Button>
                      </Grid>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                        >
                          <FileCopyOutlined />
                          
                        </Button>
                      </Grid>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                        >
                          <PrintOutlined />
                          
                        </Button>
                      </Grid>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                          color={
                            selectedRange === "week" ? "primary" : undefined
                          }
                          onClick={() => {
                            setSelectedRange("week");
                          }}
                        >
                          <CalendarViewDayOutlined />
                          
                        </Button>
                      </Grid>
                      <Grid item>
                        <Button
                          variant="outlined"
                          className={classes.actionBtn}
                          color={
                            selectedRange === "month" ? "primary" : undefined
                          }
                          onClick={() => {
                            setSelectedRange("month");
                          }}
                        >
                          <CalendarToday />
                          
                        </Button>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid item>
                    <Grid container spacing={24}>
                      <Grid item xl={4} lg={5}>
                        <Grid container spacing={8} wrap="nowrap">
                          <Grid item xl={11} lg={11}>
                            <ReactSelectMUI
                              value={ministration}
                              options={types}
                              label=""
                              handleChange={setMinistration}
                            />
                          </Grid>
                          <Grid item>
                            <IconButton>
                              <SearchOutlined />
                            </IconButton>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xl={4} lg={5}>
                        <Grid container spacing={8} wrap="nowrap">
                          <Grid item xl={11} lg={11}>
                            <ReactSelectMUI
                              value={user}
                              options={priests}
                              label=""
                              handleChange={setUser}
                            />
                          </Grid>
                          <Grid item>
                            <IconButton>
                              <SearchOutlined />
                            </IconButton>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item>
                        <FormControlLabel
                          label="  "
                          onChange={(event1, checked) =>
                            setShowAllEvents(checked)
                          }
                          control={
                            <Checkbox
                              indeterminate={showAllEvents === null}
                              color="primary"
                            />
                          }
                        />
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item>
                <ReactDateRangeInline date={date} onChange={setDate} />
              </Grid>
            </Grid>
          </ExpansionPanelDetails>
        </ExpansionPanel>
      </Suspense>
      <ScheduleTable
        collection={collection}
        dispatch={dispatch}
        optionsMap={{
          duties: { options: priests, loaded: true },
          performers: { options: priests, loaded: true },
          type: { options: types, loaded: true }
        }}
      />
    </Paper>
  );
};

const mapDispatchToProps = (dispatch: DispatchType) => ({
  openSnackBar: (message: string) => {
    const action: ActionType = openConfirmDialogFormik({
      title: "  ",
      formObj: {
        blocks: [
          {
            blockTitle: "asd",
            fields: []
          }
        ]
      },
      primaryButtonText: "",
      primaryButtonHandler: (
        values: FormikValues,
        { setSubmitting }: FormikActions<FormikValues>
      ) => {}
    });
    return dispatch(action);
  },
  openCreationMaster: () => dispatch(openScheduleCreationMaster())
});

export const ScheduleJournal = connect(
  undefined,
  mapDispatchToProps
)(PrivateScheduleJournal);
