import { emailMask } from "text-mask-addons";
import Yup from "../../yup";
import { fullWidth, halfWith } from "../../widths";
import { SysSettingsConfig } from "../../../components/sysSettingsConfig";
import { baseNumberProps, baseTextAreaProps } from "../../baseFieldProps";
import { saveFileInCommonFiles } from "../../baseFieldProps";

const rebuildReceivedValue = v => {
  if (v) {
    return JSON.parse(v);
  }
  return null;
};

const settings = {
  requiresFunction: [
    { functionName: "administration_global_modifier", allowedActions: ["edit"] }
  ],
  title: " ",
  passProps: {
    confObj: {
      title: " ",
      url: "/api/sysSystemConfig",
      method: "patch",
      doBeforeSend: {
        file: v => JSON.stringify(v)
      },
      initialValuesUrl: "/api/sysSystemConfig/search/present",
      blocks: [
        {
          blockTitle: "  ",
          fields: [
            {
              name: "sync_server_host",
              label: "",
              type: "text"
            },
            {
              name: "sync_frequency_cron",
              label: "",
              type: "text"
            }
          ]
        },
        {
          blockTitle: "  ",
          fields: [
            {
              name: "mail_server_username",
              label: "",
              type: "text"
            },
            {
              name: "mail_server_password",
              label: "",
              type: "password"
            },
            {
              name: "mail_server_port",
              label: "",
              ...baseNumberProps({ allowLeadingZeroes: true })
            },
            {
              name: "mail_server_host",
              label: "",
              type: "text"
            },
            {
              name: "mail_delivery_from_personal",
              label: "",
              type: "text"
            },
            {
              name: "mail_delivery_from_address",
              label: "",
              type: "formatted",
              mask: emailMask
            },

            {
              name: "mail_server_ssl",
              label: "",
              type: "checkbox"
            },
            {
              name: "mail_server_auth",
              label: "",
              type: "checkbox"
            }
          ]
        },
        {
          blockTitle: "   ",
          fields: [
            {
              name: "notification_frequency_event",
              label: "",
              ...baseNumberProps()
            },
            {
              name: "notification_frequency_raw_claim",
              label: "",
              ...baseNumberProps()
            }
          ]
        },
        {
          blockTitle: "",
          fields: [
            {
              name: "questioning_baptism",
              label: "",
              type: "file",
              rebuildReceivedValue,
              doBeforeSetFieldValue: saveFileInCommonFiles,
              widths: fullWidth
            },
            {
              name: "questioning_wedding",
              label: "",
              type: "file",
              rebuildReceivedValue,
              doBeforeSetFieldValue: saveFileInCommonFiles,
              widths: fullWidth
            }
          ]
        },
        {
          blockTitle: "  ",
          fields: [
            {
              name: "conversation_frequency_recording_person",
              label: "",
              ...baseNumberProps(),
              widths: halfWith
            }
          ]
        },
        {
          blockTitle: "  ",
          fields: [
            {
              name: "claim_not_less_days",
              label: "",
              ...baseNumberProps(),
              widths: halfWith
            },
            {
              name: "claim_not_more_days",
              label: "",
              ...baseNumberProps(),
              widths: halfWith
            },
            {
              name: "claim_auto_remove",
              label: "",
              ...baseNumberProps(),
              widths: fullWidth
            },
            {
              name: "claim_auto_transfer_to_free",
              label: "",
              ...baseNumberProps(),
              widths: fullWidth
            }
          ]
        },
        {
          blockTitle: "  ",
          fields: [
            {
              name: "sms_server_host",
              label: "",
              type: "text"
            },
            {
              name: "sms_database_login",
              label: "",
              type: "text"
            },
            {
              name: "sms_database_password",
              label: "",
              type: "text"
            }
          ]
        },
        {
          blockTitle: "",
          fields: [
            {
              name: "other_count_lock",
              label: "",
              ...baseNumberProps()
            },
            {
              name: "other_disclaimer_text",
              label: "",
              ...baseTextAreaProps
            },
            {
              name: "other_disclaimer_enable",
              label: "",
              type: "checkbox"
            }
          ]
        }
      ],
      validationSchema: Yup.object({
        sync_server_host: Yup.string().required(),
        sync_frequency_cron: Yup.string().required(),
        mail_server_username: Yup.string().required(),
        mail_server_password: Yup.string().required(),
        mail_server_port: Yup.string().required(),
        mail_server_host: Yup.string().required(),
        mail_delivery_from_personal: Yup.string().required(),
        mail_delivery_from_address: Yup.string().required(),
        mail_server_ssl: Yup.string().required(),
        mail_server_auth: Yup.string().required(),
        notification_frequency_event: Yup.string().required(),
        notification_frequency_raw_claim: Yup.string().required(),
        questioning_baptism: Yup.string().required(),
        questioning_wedding: Yup.string().required(),
        conversation_frequency_recording_person: Yup.string().required(),
        claim_not_less_days: Yup.string().required(),
        claim_not_more_days: Yup.string().required(),
        claim_auto_remove: Yup.string().required(),
        claim_auto_transfer_to_free: Yup.string().required(),
        sms_server_host: Yup.string().required(),
        sms_database_login: Yup.string().required(),
        sms_database_password: Yup.string().required(),
        other_count_lock: Yup.string().required(),
        other_disclaimer_text: Yup.string().required(),
        other_disclaimer_enable: Yup.string().required()
      })
    }
  },
  Component: SysSettingsConfig
};
export default settings;
