import React from "react";
import * as PropTypes from "prop-types";
import { Journal } from "../journal";
import { DisplayErrorMessage } from "../no-match";

export function GetJournalMainContent(props) {
  const { initialParameters, page, category, allowedActions } = props;
  if (
    !initialParameters ||
    !allowedActions[category] ||
    !(Array.isArray(allowedActions[category])
      ? true
      : allowedActions[category][page])
  ) {
    return (
      <div style={{ height: "calc(100vh -  80px)" }}>
        <DisplayErrorMessage errorMessage="   " />
      </div>
    );
  }

  if (initialParameters.Component) {
    const { Component, passProps = {} } = initialParameters;
    return <Component {...passProps} />;
  }

  return (
    <Journal
      category={category}
      page={page}
      initialParameters={initialParameters}
    />
  );
}

GetJournalMainContent.defaultProps = {
  page: undefined
};

GetJournalMainContent.propTypes = {
  page: PropTypes.string,
  category: PropTypes.string.isRequired,
  allowedActions: PropTypes.shape({}).isRequired,
  initialParameters: PropTypes.shape({}).isRequired
};
