export const findAttributeByPredicate = (
  {
    statement: {
      blocks: { block }
    }
  },
  predicate
) => {
  let foundAtr;
  block.forEach(({ attributes: { group } }) => {
    if (foundAtr) {
      return "";
    }
    group.forEach(({ attribute }) => {
      if (foundAtr) {
        return "";
      }
      foundAtr = attribute.find(field => {
        if (typeof predicate === "function") {
          return predicate(field);
        }
        throw new Error("     ");
      });
      return undefined;
    });
    return undefined;
  });
  return foundAtr;
};

export const findAllAttributesByPredicate = (
  {
    statement: {
      blocks: { block }
    }
  },
  predicate
) => {
  if (typeof predicate === "function") {
    const foundAttrs = [];
    block.forEach(({ attributes: { group } }) => {
      group.forEach(({ attribute }) => {
        attribute.forEach(field => {
          if (predicate(field)) {
            foundAttrs.push(field);
          }
        });
      });
    });
    return foundAttrs;
  }
  throw new Error("     ");
};

export const findByNamePredicate = value => ({ name }) => name === value;

export const findBySuperAttributeNamePredicate = value => ({
  type: { superAttributeName }
}) => superAttributeName === value;
