import React from "react";
import MuiTable from "mui-virtualized-table";
import { connect } from "react-redux";
import * as PropTypes from "prop-types";
import { makeStyles } from "@material-ui/styles";
import { setPage, setRowsPerPage } from "../actions";
import "./tableOverride.css";

const getStyles = makeStyles(theme => ({
  primaryColor: {
    color: theme.palette.primary.main
  }
}));

const PaginatedTable = ({
  data,
  defaultPagination,
  fetchJournalData,
  count = 0,
  setRowsPerPage,
  rowsPerPage,
  page,
  setPage,
  ...rest
}) => {
  const classes = getStyles();
  return (
    <MuiTable
      data={data}
      pagination={{
        classes: {
          root: classes.primaryColor,
          selectIcon: classes.primaryColor
        },
        backIconButtonProps: {
          color: "primary"
        },
        nextIconButtonProps: {
          color: "primary"
        },
        count,
        rowsPerPage,
        page: page - 1, // material-ui's <TablePagination /> is 0-based
        // rowsPerPageOptions: [5, 10, 25, 100, 1000],
        onChangePage: (e, pageLocal) => {
          setPage(pageLocal + 1);
          fetchJournalData();
        },
        onChangeRowsPerPage: e => {
          setPage(1);
          setRowsPerPage(e.target.value);
          fetchJournalData();
        },
        labelDisplayedRows: ({ from, to, count }) =>
          `${from}-${to}  ${count}`,
        labelRowsPerPage: "  "
      }}
      isCellHovered={(column, rowData, hoveredColumn, hoveredRowData) =>
        rowData.uniqueId && rowData.uniqueId === hoveredRowData.uniqueId
      }
      {...rest}
      fixedRowCount={1}
      includeHeaders
    />
  );
};

PaginatedTable.defaultProps = {};

PaginatedTable.propTypes = {
  setPage: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
  setRowsPerPage: PropTypes.func.isRequired,
  count: PropTypes.number.isRequired
};

const mapDispatchToProps = dispatch => ({
  setRowsPerPage: rowsPerPage => dispatch(setRowsPerPage(rowsPerPage)),
  setPage: page => dispatch(setPage(page))
});

const mapStateToProps = state => ({
  rowsPerPage: state.journalReducer.pagination.rowsPerPage,
  page: state.journalReducer.pagination.page
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PaginatedTable);
