import React, { useContext } from "react";
import { TableCell, Typography } from "@material-ui/core";
import classNames from "classnames";
import { makeStyles } from "@material-ui/styles";
import { TableCellProps } from "react-virtualized";
import { EventType, TypeEnum } from "./types";
import { MaskedInputMUI } from "../maskedInputMUI";
import { time24HMask } from "../../constants/masks";
import { TableContext } from "./ScheduleTable";
import { changeFrom, changeTo } from "./tableReducer";

const getStyles = makeStyles(theme => ({
  tableCell: {
    flex: 1,
    flexDirection: "column"
  },
  TableCellRoot: {
    borderBottom: "none"
  },
  flexContainer: {
    display: "flex",
    alignItems: "center",
    boxSizing: "border-box"
  },
  redHighLight: {
    backgroundColor: "#890706"
  },
  greenHighLight: {
    backgroundColor: "#277b1f"
  },
  blueHighLight: {
    backgroundColor: "#74bcff"
  },
  cont: {
    display: "flex",
    flexWrap: "nowrap"
  },
  sign: {
    padding: theme.spacing.unit
  }
}));

export const EventTimeCell = ({
  cellData: events,
  rowIndex
}: TableCellProps) => {
  const classes = getStyles();
  const { dispatch } = useContext(TableContext);
  return (
    <TableCell
      component="div"
      variant="body"
      classes={{ root: classes.TableCellRoot }}
      className={classNames(classes.tableCell, classes.flexContainer)}
    >
      {events.map(
        (
          { from, to, type, typeEnum, isFree, uniqueId }: EventType,
          eventIndex: number
        ) => {
          const fromOnChange = (
            e: React.ChangeEvent<HTMLInputElement>
          ): void => {
            if (e.currentTarget && e.currentTarget.value) {
              dispatch(changeFrom(rowIndex, eventIndex, e.currentTarget.value));
            }
          };
          const toOnChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
            if (e.currentTarget && e.currentTarget.value) {
              dispatch(changeTo(rowIndex, eventIndex, e.currentTarget.value));
            }
          };
          return (
            <div
              key={uniqueId}
              className={classNames(
                classes.cont,
                {
                  [classes.redHighLight]:
                    typeEnum && TypeEnum.slot === typeEnum && !isFree
                },
                {
                  [classes.greenHighLight]:
                    typeEnum && TypeEnum.slot === typeEnum && !!isFree
                },
                {
                  [classes.blueHighLight]:
                    typeEnum && TypeEnum.workItem === typeEnum
                }
              )}
            >
              <MaskedInputMUI
                value={from}
                mask={time24HMask}
                keepCharPositions
                onChange={fromOnChange}
              />
              <Typography className={classes.sign}>-</Typography>
              <MaskedInputMUI
                value={to}
                mask={time24HMask}
                keepCharPositions
                onChange={toOnChange}
              />
            </div>
          );
        }
      )}
    </TableCell>
  );
};
