import axios from "axios";
import { dispatchOpenSnackBar } from "../../../constants";

export const fieldLevelUniqueCheck = (axiosConfig, value) =>
  axios(axiosConfig)
    .then(({ data }) => {
      if (data === true) {
        return ` "${value}"   `;
      }
      return undefined;
    })
    .catch(er => {
      console.log(er);
      dispatchOpenSnackBar("  ");
      return undefined;
    });
