import axios from "axios";
import React from "react";
import { computePropsForFormikMUI } from "../../../../components/modalWrapper/utils/computePropsForForMikMUI";
import Yup from "../../../yup";
import { findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks } from "../../../../utils/commonFormFunctions";
import {
  baseDictResponseRefactor,
  dispatchOpenSnackBar,
  dispatchPopHeadModal
} from "../../../index";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "../../../../components/completePromiseThenRenderComponent";
import { defaultErrorHandler } from "../../../../utils/defaultErrorHandler";
import { constructRequest } from "../../../../actions";
import { PrimaryButton } from "../../../../components/modalWrapper/buttons";
import { FormikMUI } from "../../../../components/formikMUi/formikNewRealisation";
import {
  constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
  rebuildReceivedValueLabelFullNameValueUniqueId
} from "../../../rebuildRecieved";

const baseForm = {
  title: "",
  titleItem: "",
  headerText: "fullName",
  url: "/api/procedures/structureProcedures",
  projection: "formStructureProcedure",
  doBeforeSend: {
    file: ({ value }) => value,
    select: ({ value }) => value
  },
  method: "post",
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: " ",
          name: "fullName",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "   ",
          name: "rguId",
          type: "text"
        },
        {
          label: "   ",
          name: "serviceId",
          readOnly: true,
          type: "text"
        },
        {
          label: " ",
          name: "serviceShortName",
          type: "text",
          additionalProps: {
            multiline: true,
            fullWidth: true,
            rowsMax: 6
          }
        },
        {
          label: "",
          name: "organizationId",
          type: "select",
          dictUrl: "/api/structureMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelFullNameValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelFullNameValueUniqueId,
          widths: { lg: 12, xl: 12 }
        },
        {
          label: " ",
          name: "synchronizeTimestamp",
          readOnly: true,
          type: "text"
        },
        {
          label: "   ",
          name: "claimFile",
          type: "file",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        },
        {
          label: "   ",
          name: "bpmnFile",
          type: "file",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        },
        {
          label: "",
          name: "isFunction",
          type: "checkbox",
          widths: { lg: 6, xl: 6 }
        },
        {
          label: " ",
          name: "isPaid",
          type: "checkbox",
          widths: { lg: 6, xl: 6 },
          customOnChange: ({ value, status, setStatus }) => {
            if (value === true) {
              setStatus({
                registeredFields: {
                  ...status.registeredFields,
                  gisgmpId: true,
                  gisgmpMemberUrn: true
                }
              });
            } else {
              setStatus({
                registeredFields: {
                  ...status.registeredFields,
                  gisgmpId: false,
                  gisgmpMemberUrn: false
                }
              });
            }
          }
        },
        {
          label: "     ",
          name: "gisgmpId",
          type: "text",
          widths: { lg: 6, xl: 6 },
          customInitialRegister: ({ isPaid }) => isPaid === true
        },
        {
          label: "     ",
          name: "gisgmpMemberUrn",
          type: "text",
          widths: { lg: 6, xl: 6 },
          customInitialRegister: ({ isPaid }) => isPaid === true
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    fullName: Yup.string().required(),
    rguId: Yup.string().required(),
    isFunction: Yup.bool(),
    serviceId: Yup.string(),
    serviceShortName: Yup.string(),
    organizationId: Yup.object()
      .nullable()
      .required(),
    synchronizeTimestamp: Yup.string(),
    claimFile: Yup.object()
      .nullable()
      .required(),
    bpmnFile: Yup.object()
      .nullable()
      .required(),
    gisgmpId: Yup.string().when("isPaid", {
      is: true,
      then: Yup.string().required(),
      otherwise: Yup.string()
    }),
    gisgmpMemberUrn: Yup.string().when("isPaid", {
      is: true,
      then: Yup.string().required(),
      otherwise: Yup.string()
    })
  })
};

const confObj = {
  doBeforeSend: {
    select: ({ value }) => value,
    file: ({ value }) => value
  },
  fields: [
    {
      name: "mvList",
      label: "mvList",
      type: "fieldArray",
      fields: [
        {
          name: "uniqueId",
          label: "",
          type: "text",
          hidden: true
        },
        {
          name: "mvObjectId",
          label: "  ",
          type: "select",
          dictUrl: "/api/mv/objects",
          rebuildReceivedOptions: data =>
            data.map(({ label, uniqueId }) => ({ label, value: uniqueId })),
          width: 2
        },
        {
          name: "autoDelivered",
          label: " ",
          type: "select",
          options: [
            { label: "", value: true },
            { label: "", value: false }
          ],
          placeholder: " "
        },
        {
          name: "claimMvXml",
          label: " ",
          doBeforeSetFieldValue: value =>
            axios.post("/api/file/save", value, {
              "Content-Type": "multipart/form-data"
            }),
          type: "file"
        },
        {
          name: "isDeleted",
          label: "isDeleted",
          type: "checkbox",
          hidden: true
        }
      ]
    },
    {
      name: "procedureId",
      label: "procedureId",
      type: "text",
      hidden: true
    }
  ],
  validationSchema: Yup.object({
    mvList: Yup.array()
      .of(
        Yup.object({
          mvObjectId: Yup.object().when("isDeleted", {
            is: true,
            then: Yup.object().nullable(),
            otherwise: Yup.object()
              .nullable()
              .required()
          }),
          autoDelivered: Yup.object().when("isDeleted", {
            is: true,
            then: Yup.object().nullable(),
            otherwise: Yup.object()
              .nullable()
              .required()
          }),
          claimMvXml: Yup.object()
            .nullable()
            .when("autoDelivered", (autoDelivered, schema) =>
              autoDelivered !== null && autoDelivered.value
                ? schema.required()
                : schema
            ),
          isDeleted: Yup.bool(),
          uniqueId: Yup.string()
        })
      )
      .min(1)
  })
};

const baseViewEdit = {
  ...baseForm,
  url: "/api/structureProcedure",
  primaryActionUrl: "/api/procedures/structureProcedures",
  additionalTabs: [
    {
      tabTitle: "  ",
      tabContent: {
        Component: ({
          modal: {
            item: { uniqueId }
          },
          ...rest
        }) => (
          <CompletePromiseThenRenderComponent
            config={[
              {
                urlToDownload: `/api/procedureMv/search/presentByProcedure?procedureId=${uniqueId}&projection=procedureMvList`,
                whereToStore: "rawInitialValues",
                postLoadDoSomethingThenPassResultAsProp: {
                  func: data => ({
                    mvList: baseDictResponseRefactor(data).map(
                      ({
                        mvObject: { label, uniqueId: mvUniqueId },
                        autoDelivered,
                        ...restO
                      }) => ({
                        mvObjectId: {
                          label,
                          value: mvUniqueId
                        },
                        autoDelivered:
                          autoDelivered === true
                            ? { label: "", value: true }
                            : { label: "", value: false },
                        ...restO
                      })
                    ),
                    procedureId: uniqueId
                  }),
                  passResultAs: "initialValues"
                }
              },
              ...findFieldsWithDictThenConstructArrayToDownloadWithOutBlocks(
                confObj
              )
            ]}
            PreloaderComponent={defaultPreloader}
            Component={FormikMUI}
            errorHandler={defaultErrorHandler}
            {...rest}
          />
        ),
        passProps: {
          formObj: confObj,
          variant: "table"
        },
        functionToExecuteThenPassResultAsProps: ({
          modal: {
            item: { uniqueId },
            mode
          }
        }) => ({
          onSubmit: ({ procedureId, mvList }, { setSubmitting }) => {
            constructRequest(
              {
                method: "post",
                url: "/api/procedureMv/structureProcedureMvList",
                data: mvList
                  .filter(
                    item => !(item.isDeleted === true && item.uniqueId === "")
                  )
                  .map(item => ({
                    ...item,
                    autoDelivered: item.autoDelivered.value,
                    mvObjectId: item.mvObjectId.value,
                    claimMvXml:
                      item.claimMvXml !== null ? item.claimMvXml.value : null,
                    procedureId
                  }))
              },
              () => {
                dispatchPopHeadModal();
                dispatchOpenSnackBar("   ");
                setSubmitting(false);
              },
              () => setSubmitting(false)
            );
          },
          readOnly: mode !== "edit"
        })
      },
      tabButtons: [
        {
          Btn: PrimaryButton,
          requiresFormSubmitRef: true,
          conditionallyRender: ({ modal: { mode } }) => mode === "edit"
        }
      ]
    }
  ]
};

const forms = {
  create: computePropsForFormikMUI({ formObj: baseForm, mode: "create" }),
  view: computePropsForFormikMUI({
    formObj: baseViewEdit,
    mode: "view"
  }),
  edit: computePropsForFormikMUI({
    formObj: { ...baseViewEdit, method: "patch" },
    mode: "edit"
  })
};

export default forms;
