import React, { useEffect, useRef } from "react";
import { FieldArray, Form } from "formik";
import {
  Fab,
  Grid,
  IconButton,
  List,
  ListItem,
  ListItemSecondaryAction,
  Typography
} from "@material-ui/core";
import classnames from "classnames";
import { get } from "lodash";
import { Add as AddIcon, Delete } from "@material-ui/icons";
import * as PropTypes from "prop-types";
import { makeStyles } from "@material-ui/styles";
import uuidV4 from "uuid/v4";
import { renderField } from "../../renderField";
import { createEmptyFieldArrayObj } from "../../utils/initialValuesUtils";

const useStyles = makeStyles(theme => ({
  isDeleted: {
    color: "#a94442",
    backgroundColor: "#f2dede",
    borderColor: "#ebccd1",
    border: "1px solid transparent",
    borderRadius: "4px"
  },
  listSubItem: {
    padding: `${theme.spacing.unit}px`,
    width: "100%"
  },
  headerItem: {
    width: "100%"
  },
  listItem: {
    position: "relative"
  },
  lastItem: {
    paddingTop: `${theme.spacing.unit * 2}px`,
    // position: "sticky",
    display: "flex",
    justifyContent: "center",
    // bottom: "0",
    // zIndex: 2,
    // backgroundColor: "white",
    paddingBottom: "0"
  },
  removeBtn: {
    position: "absolute",
    right: "0"
  },
  divider: {
    marginLeft: "8px",
    height: "1px",
    backgroundColor: "white",
    flex: "1  1 auto"
  },
  field: {
    width: "100%"
  },
  fabContainer: {
    marginBottom: `-${theme.spacing.unit * 3}px`
  },
  container: {
    position: "relative",
    minWidth: "20vw",
    minHeight: "20vh"
  },
  header: {
    position: "sticky",
    zIndex: "2",
    top: "0",
    borderBottom: "2px solid rgba(0, 0, 0, 0.12)",
    backgroundColor: "#eeeeee",
    paddingTop: "0",
    paddingBottom: "0",
    marginBottom: `${theme.spacing.unit}px`
  },
  deleteBtn: {
    right: "-8px"
  },
  secondaryAction: { paddingRight: "0", paddingLeft: "0" }
}));

let previousValuesLength = 0;

export const RenderTableForm = ({
  confObj,
  values,
  readOnly,
  initialValues,
  setFieldValue
}) => {
  const fistFieldArray = confObj.fields[0];
  const emptyFieldArrayObj = createEmptyFieldArrayObj(fistFieldArray.fields);
  const classes = useStyles();
  let arrayHelpersRef;
  const addBtnRef = useRef(null);
  let maxCountOfColumns = 0;
  fistFieldArray.fields.forEach(({ width = 1, hidden }) => {
    if (hidden !== true) {
      maxCountOfColumns += width;
    }
  });
  const widths = [];
  const minColumnWidth = 100 / maxCountOfColumns;
  fistFieldArray.fields.forEach(({ width = 1 }) => {
    widths.push(minColumnWidth * width);
  });
  useEffect(() => {
    //       -  
    if (addBtnRef.current !== null) {
      if (
        values &&
        values[fistFieldArray.name] &&
        values[fistFieldArray.name].length !== previousValuesLength
      ) {
        previousValuesLength = values[fistFieldArray.name].length;
        addBtnRef.current.scrollIntoView({ behavior: "smooth" });
      }
    }
  }, [fistFieldArray.name, values]);
  return (
    <Form>
      <div className={classes.container}>
        <List>
          <ListItem className={classes.header} disableGutters>
            {fistFieldArray.fields.map(
              ({ label, hidden = false }, index) =>
                !hidden && (
                  <span
                    className={classnames(
                      classes.listSubItem,
                      classes.headerItem
                    )}
                    key={label || uuidV4()}
                    style={{ width: `${widths[index]}%` }}
                  >
                    <Typography
                      variant="subtitle1"
                      style={{
                        fontSize: "15px"
                      }}
                    >
                      {label}
                    </Typography>
                  </span>
                )
            )}
          </ListItem>
          <FieldArray
            name={fistFieldArray.name}
            render={arrayHelpers => {
              arrayHelpersRef = arrayHelpers;
              return (
                <>
                  {(values &&
                    values[fistFieldArray.name] &&
                    values[fistFieldArray.name].length > 0 &&
                    values[fistFieldArray.name].map((row, index) => (
                      <ListItem
                        key={index}
                        className={classnames(classes.listItem, {
                          [classes.isDeleted]: row.isDeleted === true
                        })}
                        disableGutters
                      >
                        {fistFieldArray.fields.map((field, fieldIndex) => {
                          if (field.component) {
                            const { component: CustomComponent } = field;
                            return (
                              <span
                                key={field.name || uuidV4() + index}
                                className={classes.listSubItem}
                                style={{ width: `${widths[fieldIndex]}%` }}
                              >
                                <CustomComponent
                                  value={values[fistFieldArray.name][index]}
                                />
                              </span>
                            );
                          }
                          if (field.hidden === true) {
                            return null;
                          }
                          return (
                            <span
                              key={field.name || uuidV4() + index}
                              className={classes.listSubItem}
                              style={{ width: `${widths[fieldIndex]}%` }}
                            >
                              {renderField({
                                field: {
                                  ...field,
                                  label: "",
                                  name: `${fistFieldArray.name}.[${index}].${
                                    field.name
                                  }`
                                },
                                classes,
                                readOnly,
                                initialValues,
                                setFieldValue
                              })}
                            </span>
                          );
                        })}
                        {!row.isDeleted && !readOnly && (
                          <ListItemSecondaryAction>
                            <IconButton
                              className={classes.deleteBtn}
                              type="button"
                              onClick={() =>
                                arrayHelpers.replace(index, {
                                  ...get(
                                    values,
                                    `${fistFieldArray.name}.[${index}]`
                                  ),
                                  isDeleted: true
                                })
                              }
                            >
                              <Delete />
                            </IconButton>
                          </ListItemSecondaryAction>
                        )}
                      </ListItem>
                    ))) || (
                    <ListItem>
                      <Grid container justify="center">
                        <Typography variant="body1">
                           
                        </Typography>
                      </Grid>
                    </ListItem>
                  )}
                </>
              );
            }}
          />
        </List>
        {!readOnly && (
          <Grid container justify="center">
            <div ref={addBtnRef}>
              <Fab
                size="small"
                color="primary"
                aria-label="Add"
                onClick={() => arrayHelpersRef.push(emptyFieldArrayObj)}
              >
                <AddIcon />
              </Fab>
            </div>
          </Grid>
        )}
      </div>
    </Form>
  );
};

RenderTableForm.defaultProps = {
  readOnly: false
};

RenderTableForm.propTypes = {
  confObj: PropTypes.shape({}).isRequired,
  values: PropTypes.shape({}).isRequired,
  readOnly: PropTypes.bool,
  initialValues: PropTypes.shape({}).isRequired,
  setFieldValue: PropTypes.func.isRequired
};
