import React from "react";
import { Formik } from "formik";
import { set, isEqual } from "lodash";
import * as PropTypes from "prop-types";
import { RenderTableForm } from "./variants/table/renderTableForm";
import { RenderBlockForm } from "./variants/block/blockForm";
import { RenderXmlDescribedForm } from "./variants/xmlToForm/renderXmlDescribedForm";
import { ValidationErrorFocus } from "./validationErrorFocus";

const initRegisteredFields = (formObj, variant, initialValues) => {
  const result = {};
  if (variant === "block") {
    formObj.blocks.forEach(({ fields }) =>
      fields.forEach(({ name, hidden, customInitialRegister }) => {
        const check = customInitialRegister
          ? customInitialRegister(initialValues)
          : true;
        set(result, name, hidden !== true && check);
      })
    );
  }
  if (variant === "table") {
    formObj.fields.forEach(({ name, hidden }) => {
      set(result, name, hidden !== true);
    });
  }
  return result;
};

export const FormikMUI = ({
  formObj,
  onSubmit,
  bindFormSubmit,
  initialValues,
  optionsMap,
  readOnly,
  variant,
  enableReinitialize,
  additionalStatusProps,
  bindIsSubmitting,
  bindDirty,
  enableStatusReinitialize,
  style
}) => {
  const registeredFields = initRegisteredFields(
    formObj,
    variant,
    initialValues
  );
  return (
    <Formik
      enableReinitialize={enableReinitialize}
      onSubmit={onSubmit}
      validateOnChange={false}
      validateOnBlur={!readOnly}
      validationSchema={formObj.validationSchema}
      initialValues={initialValues}
      initialStatus={{
        registeredFields,
        optionsMap,
        readOnly,
        ...additionalStatusProps
      }}
      render={({
        handleSubmit,
        submitForm,
        setFieldValue,
        values,
        isSubmitting,
        setStatus,
        status,
        dirty
      }) => {
        if (
          enableStatusReinitialize &&
          (!isEqual(optionsMap, status.optionsMap) ||
            readOnly !== status.readOnly)
        ) {
          setStatus({ ...status, optionsMap, readOnly });
        }
        if (bindFormSubmit) {
          // bind the submission handler remotely
          bindFormSubmit(submitForm);
        }
        if (bindIsSubmitting) {
          bindIsSubmitting(isSubmitting);
        }
        if (bindDirty) {
          bindDirty(dirty);
        }
        return (
          <>
            {variant === "block" ? (
              <RenderBlockForm
                formObj={formObj}
                handleSubmit={handleSubmit}
                style={style}
              />
            ) : null}
            {variant === "table" ? (
              <RenderTableForm
                confObj={formObj}
                handleSubmit={handleSubmit}
                values={values}
                initialValues={initialValues}
                readOnly={readOnly}
                setFieldValue={setFieldValue}
              />
            ) : null}
            {variant === "xmlToForm" ? (
              <RenderXmlDescribedForm
                formObj={formObj}
                values={values}
                initialValues={initialValues}
              />
            ) : null}
            <ValidationErrorFocus />
          </>
        );
      }}
    />
  );
};

FormikMUI.defaultProps = {
  bindFormSubmit: undefined,
  optionsMap: {},
  readOnly: false,
  variant: "block",
  enableReinitialize: false,
  onSubmit: undefined,
  additionalStatusProps: undefined,
  bindIsSubmitting: undefined,
  bindDirty: undefined,
  enableStatusReinitialize: false,
  style: undefined
};

FormikMUI.propTypes = {
  formObj: PropTypes.shape({}).isRequired,
  onSubmit(props, propName, componentName) {
    if (props.readOnly !== true && typeof props[propName] !== "function") {
      return new Error(
        `prop \`${propName}\` should Be supplied if props[readOnly] is true to` +
          ` \`${componentName}\`. Validation failed.`
      );
    }
    return undefined;
  },
  bindDirty: PropTypes.func,
  bindFormSubmit: PropTypes.func,
  initialValues: PropTypes.shape({}).isRequired,
  optionsMap: PropTypes.shape({}),
  readOnly: PropTypes.bool,
  variant: PropTypes.oneOf(["block", "table", "xmlToForm"]),
  enableReinitialize: PropTypes.bool,
  additionalStatusProps: PropTypes.shape({}),
  bindIsSubmitting: PropTypes.func,
  enableStatusReinitialize: PropTypes.bool,
  style: PropTypes.shape({})
};
