import React from "react";
import Dropzone from "react-dropzone";
import {
  Typography,
  Grid,
  IconButton,
  FormHelperText
} from "@material-ui/core";
import {
  InsertDriveFileOutlined,
  Done,
  VerticalAlignBottomOutlined
} from "@material-ui/icons";
import * as Proptypes from "prop-types";
import { checkIfFieldHasValidationError } from "../utils/checkIfFieldHasValidationError";
import { commonFilesUrl, downloadFile } from "../utils/downLoad";

const baseStyle = {
  height: "100%",
  borderWidth: 1,
  borderColor: "#1c375a66",
  borderStyle: "dashed",
  borderRadius: 5,
  padding: "16px"
};
const activeStyle = {
  borderColor: "#1676f380",
  backgroundColor: "#eee",
  background: "#1676f31a",
  transition: "background-color 0.1s, border-color 0.1s"
};
const rejectStyle = {
  borderColor: "#c66",
  backgroundColor: "#eee"
};

/* const separator = {
  borderTop: "1px solid #1a39601a"
}; */

const textStyle = {
  marginLeft: "8px"
};

export const WrappedReactDropZoneMUI = ({
  field: { value, name },
  form: { errors, touched, setFieldValue },
  disabled,
  label: controlLabel,
  onDrop,
  customUrlToDownLoadFile,
  accept
}) => {
  const { label: fileName, value: fileId } =
    value !== null ? value : { label: "", value: "" };
  const { error, helperText } = checkIfFieldHasValidationError(
    name,
    touched,
    errors
  );
  return (
    <Dropzone
      onDrop={onDrop}
      disabled={disabled}
      multiple={false}
      maxSize={10485760}
      accept={accept}
    >
      {({
        getRootProps,
        getInputProps,
        isDragAccept,
        isDragReject,
        acceptedFiles,
        rejectedFiles
      }) => {
        let styles = { ...baseStyle };
        styles = isDragAccept ? { ...styles, ...activeStyle } : styles;
        styles = isDragReject ? { ...styles, ...rejectStyle } : styles;

        return (
          <div style={{ marginTop: "4px", position: "relative" }}>
            <div
              style={{
                position: "absolute",
                left: "8px",
                top: "-8px",
                backgroundColor: "white"
              }}
            >
              <Typography>{controlLabel}</Typography>
            </div>
            <Grid
              container
              alignItems="flex-start"
              justify="flex-start"
              direction="column"
              {...getRootProps()}
              style={styles}
            >
              <Grid container alignItems="center">
                <input {...getInputProps()} />
                {(value === null || (value && value.value === null)) &&
                  !disabled &&
                  !isDragReject && (
                    <>
                      <InsertDriveFileOutlined />
                      <Typography color="textPrimary" style={textStyle}>
                         
                      </Typography>
                    </>
                  )}
                {(value === null || (value && value.value === null)) &&
                  disabled && (
                    <Typography color="textPrimary" style={textStyle}>
                       
                    </Typography>
                  )}
                {isDragReject && (
                  <Typography align="center" color="error">
                       
                  </Typography>
                )}
                {fileName && (
                  <Grid container>
                    <Grid
                      key={fileName}
                      container
                      justify="space-between"
                      alignItems="center"
                    >
                      <Grid item>
                        <Grid container alignItems="center">
                          <Done />
                          <Typography style={textStyle}>{fileName}</Typography>
                          <IconButton
                            onClick={event => {
                              if (event) {
                                event.stopPropagation();
                              }
                              downloadFile(
                                customUrlToDownLoadFile
                                  ? customUrlToDownLoadFile(value)
                                  : `${commonFilesUrl}/${fileId}`,
                                fileName
                              );
                            }}
                            style={{
                              position: "absolute",
                              right: "0"
                            }}
                          >
                            <VerticalAlignBottomOutlined />
                          </IconButton>
                        </Grid>
                      </Grid>
                      {/* <Grid item>
                        <Close />
                      </Grid> */}
                    </Grid>
                  </Grid>
                )}
              </Grid>
            </Grid>
            <FormHelperText error={error}>{helperText}</FormHelperText>
          </div>
        );
      }}
    </Dropzone>
  );
};

WrappedReactDropZoneMUI.defaultProps = {
  disabled: false,
  label: "",
  downloadFile: undefined,
  customUrlToDownLoadFile: undefined,
  accept: undefined
};

WrappedReactDropZoneMUI.propTypes = {
  field: Proptypes.shape({
    name: Proptypes.string.isRequired,
    onChange: Proptypes.func.isRequired
  }).isRequired,
  form: Proptypes.shape({
    errors: Proptypes.shape({}).isRequired,
    touched: Proptypes.shape({}).isRequired,
    setFieldValue: Proptypes.func
  }).isRequired,
  disabled: Proptypes.bool,
  label: Proptypes.string,
  onDrop: Proptypes.func.isRequired,
  downloadFile: Proptypes.func,
  customUrlToDownLoadFile: Proptypes.func,
  accept: Proptypes.oneOfType([
    Proptypes.string,
    Proptypes.arrayOf(Proptypes.string)
  ])
};
