export const time24HMask = value => {
  if (value.charAt(0) === "2") {
    return [/[0-2]/, /[0-3]/, ":", /[0-5]/, /[0-9]/];
  }
  return [/[0-2]/, /[0-9]/, ":", /[0-5]/, /[0-9]/];
};
