import * as React from "react";
import { connect } from "react-redux";
import { Dispatch } from "redux";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Typography
} from "@material-ui/core";
import { closeScheduleCreationMaster } from "../../../actions";
import ShadowedOutlinedButton from "../../ShadowedOutlinedButton";

type PrivateScheduleCreationMasterProps = {
  closeHandler: Function;
  username: string;
};

const PrivateScheduleCreationMaster = ({
  closeHandler,
  username
}: PrivateScheduleCreationMasterProps) => {
  const description = ` ${username},        1 .         `;
  return (
    <Dialog
      open
      disableEscapeKeyDown
      disableBackdropClick
      fullWidth
      maxWidth="lg"
    >
      <DialogTitle>  </DialogTitle>
      <DialogContent>
        <Typography variant="body1">{description}</Typography>
      </DialogContent>
      <DialogActions>
        <ShadowedOutlinedButton onClick={closeHandler}>
          
        </ShadowedOutlinedButton>
        <Button variant="contained" color="primary">
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};

type stateProps = {
  auth: {
    surnameInitials: string;
  };
};

const mapStateToProps = (state: stateProps) => ({
  username: state.auth.surnameInitials
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
  closeHandler: () => dispatch(closeScheduleCreationMaster())
});
export const ScheduleCreationMaster = connect(
  mapStateToProps,
  mapDispatchToProps
)(PrivateScheduleCreationMaster);
