function makeArrayFromObject(obj) {
  if (!(obj instanceof Array)) {
    return [obj];
  }
  return obj;
}

// item - ,      "dependencies", ..   
export default function defineItemVisibility(item, formObj, values) {
  const hasVisual = Object.keys(item).indexOf("visual") !== -1;
  const visual = hasVisual ? item.visual === "true" : true;
  let dependenciesMet = true;
  if (item.dependencies) {
    dependenciesMet = checkDependencies(item.dependencies, formObj, values);
  }
  return visual && dependenciesMet;
}

function checkDependencies(dependencies, initialObj, currentValues) {
  const results = [];

  if (dependencies.dependencies) {
    dependencies.dependencies.forEach(dep =>
      results.push(checkDependencies(dep, initialObj, currentValues))
    );
  }

  if (dependencies.dependency) {
    dependencies.dependency.forEach(dep =>
      results.push(checkDependency(dep, initialObj, currentValues))
    );
  }

  switch (dependencies.condition) {
    case "or":
      return results.some(result => result);
    case "not":
      return results.every(result => !result);
    case "and":
    default:
      return results.every(result => result);
  }
}

function getValuesFromObjectByKeyReccursion(obj, key, predicate, results) {
  const keys = Object.keys(obj);
  keys.forEach(tKey => {
    if (tKey === key) {
      const tempArr = makeArrayFromObject(obj[tKey]);
      tempArr.forEach(elem => {
        if (predicate) {
          if (predicate(elem)) {
            results.push(elem);
          }
        } else {
          results.push(elem);
        }
      });
    }
    if (obj[tKey] instanceof Object) {
      getValuesFromObjectByKeyReccursion(obj[tKey], key, predicate, results);
    }
  });
}

function getValuesFromObjectByKey(obj, key, predicate) {
  const results = [];
  getValuesFromObjectByKeyReccursion(obj, key, predicate, results);
  return results;
}

function findDependencyByName(depName, initialObj) {
  const depRefs = getValuesFromObjectByKey(
    initialObj,
    "dependencies",
    dependencies => dependencies.name && dependencies.name === depName
  );

  if (!depRefs) {
    console.log("    ", depName, initialObj);
  }

  return depRefs[0];
}

function getCurrentValue(values, valueName) {
  if (Object.keys(values).indexOf(valueName) !== -1) {
    if (typeof values[valueName] === "string" || !values[valueName]) {
      return values[valueName];
    }

    if (values[valueName].value) {
      return values[valueName].value;
    }
  }

  console.log("  !", valueName, values);
  return "";
}

function getCurrentValuesFromMultyItems(itemName, currentValues) {
  const results = getValuesFromObjectByKey(currentValues, itemName);
  return results.map(result =>
    result && result.value ? result.value : result
  );
}

function checkDependency(dependency, initialObj, currentValues) {
  // Reference to a 'dependencies'
  if (dependency.dependencyRef) {
    const dep = findDependencyByName(dependency.dependencyRef, initialObj);
    return checkDependencies(dep, initialObj, currentValues);
  }
  if (dependency.field) {
    const { field, value } = dependency;
    // Dependency on multy-items
    if (field.multy === true) {
      const itemValues = getCurrentValuesFromMultyItems(
        field.name,
        currentValues
      );

      switch (field.condition) {
        case "and":
          return value.every(val => itemValues.indexOf(val) !== -1);
        case "not":
          return value.every(val => itemValues.indexOf(val) === -1);
        default:
          return value.some(val => itemValues.indexOf(val) !== -1);
      }
    } else {
      // Simple dependency
      const currentValue = getCurrentValue(currentValues, field.name);

      switch (field.condition) {
        case "and":
          return value.every(val => val === currentValue);
        case "not":
          return value.every(val => val !== currentValue);
        default:
          return value.some(val => val === currentValue);
      }
    }
  }

  console.log(
    "  !!!",
    dependency,
    initialObj,
    currentValues
  );
}
