import React from "react";
import CompletePromiseThenRenderComponent, {
  defaultPreloader
} from "../../../components/completePromiseThenRenderComponent";
import { FormikMUI } from "../../../components/formikMUi/formikNewRealisation";
import { defaultErrorHandler } from "../../../utils/defaultErrorHandler";
import { baseTextAreaProps, baseTimeFieldProps } from "../../baseFieldProps";
import { constructRequest, openSnackBar } from "../../../actions";
import { dispatchPopHeadModal } from "../../index";
import Yup from "../../yup";

const hramTabFormObj = {
  blocks: [
    {
      blockTitle: "",
      fields: [
        {
          label: "",
          name: "photo",
          type: "file"
        },
        {
          label: " ",
          name: "description",
          ...baseTextAreaProps
        },
        {
          label: "-",
          name: "webUrl",
          type: "text"
        },
        {
          label: "",
          name: "contacts",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "history",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "religion",
          ...baseTextAreaProps
        },
        {
          label: "   ",
          name: "startWorkTime",
          ...baseTimeFieldProps
        },
        {
          label: "   ",
          name: "finishWorkTime",
          ...baseTimeFieldProps
        },
        {
          label: "     ",
          name: "visiblePriests",
          type: "checkbox"
        }
      ]
    },
    {
      blockTitle: " ",
      fields: [{ label: "", name: "description", ...baseTextAreaProps }]
    }
  ],
  validationSchema: Yup.object({
    photo: Yup.object().nullable(),
    description: Yup.string().required(),
    webUrl: Yup.string(),
    contacts: Yup.string(),
    history: Yup.string(),
    religion: Yup.string(),
    startWorkTime: Yup.string().required(),
    finishWorkTime: Yup.string().required(),
    visiblePriests: Yup.bool()
  }).nullable()
};

export const HramTab = ({
  firstFormInitialValues: { uniqueId },
  modal: { mode },
  ...rest
}) => (
  <CompletePromiseThenRenderComponent
    config={[
      {
        urlToDownload: `/api/strHram/search/byMember?member=${uniqueId}&projection=formMemberHram`,
        whereToStore: "initialValues"
      }
    ]}
    Component={FormikMUI}
    readOnly={mode !== "edit"}
    onSubmit={(values, { setSubmitting }) => {
      constructRequest(
        {
          url: `/api/hram/${uniqueId}`,
          method: "patch",
          data: values
        },
        () => {
          openSnackBar("  ");
          setSubmitting(false);
          dispatchPopHeadModal();
        }
      );
    }}
    formObj={hramTabFormObj}
    errorHandler={defaultErrorHandler}
    PreloaderComponent={defaultPreloader}
    {...rest}
  />
);
