import * as PropTypes from "prop-types";
import React from "react";
import { Button } from "@material-ui/core";
import ShadowedOutlinedButton from "../../ShadowedOutlinedButton";
import { popHeadModalThenPushModal } from "../../../actions";
import { store } from "../../../store";

export const cancelButton = (actionHandler = () => {}, text = "") => (
  <ShadowedOutlinedButton
    key={text}
    variant="outlined"
    color="primary"
    onClick={() => actionHandler()}
  >
    {text}
  </ShadowedOutlinedButton>
);

export const PrimaryButton = ({ actionHandler, text, disabled }) => {
  return (
    <Button
      variant="contained"
      color="primary"
      disabled={disabled}
      onClick={() => actionHandler()}
      key={text}
    >
      {text}
    </Button>
  );
};

PrimaryButton.propTypes = {
  actionHandler: PropTypes.func.isRequired,
  text: PropTypes.string,
  disabled: PropTypes.bool
};
PrimaryButton.defaultProps = {
  text: "",
  disabled: false
};

export const deleteButton = (actionHandler, text = "") => (
  <Button
    variant="contained"
    color="secondary"
    onClick={() => actionHandler()}
    key={text}
  >
    {text}
  </Button>
);

export const openEditButton = ({ text = "", modal }) => (
  <Button
    key={text}
    variant="contained"
    color="primary"
    onClick={() => store.dispatch(popHeadModalThenPushModal(modal))}
  >
    {text}
  </Button>
);

openEditButton.propTypes = {
  modal: PropTypes.shape({}).isRequired,
  text: PropTypes.string
};
openEditButton.defaultProps = {
  text: ""
};
