import React, { useEffect, useReducer, useState } from "react";
import {
  Paper,
  Stepper,
  Button,
  StepContent,
  StepLabel,
  Step,
  LinearProgress
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import * as PropTypes from "prop-types";
import { defaultPreloader } from "../completePromiseThenRenderComponent";
import {
  decreaseActiveStep,
  increaseActiveStep,
  init,
  reducer,
  setIsLoading,
  setStepPassProps
} from "./reducer";
import { createAxiosCancelToken, downLoadStuff } from "./until";

const getStyles = makeStyles(theme => ({
  button: {
    marginTop: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  actionsContainer: {
    marginLeft: theme.spacing.unit * 2
  },
  resetContainer: {
    padding: theme.spacing.unit * 3
  }
}));

export const StepperMUI = ({ stepsConfig }) => {
  const [state, dispatch] = useReducer(reducer, init(stepsConfig));

  const { activeStep, stepsPassProps, isLoading } = state;

  useEffect(() => {
    const source = createAxiosCancelToken();
    if (
      (stepsConfig[activeStep] && stepsConfig[activeStep].config) ||
      (stepsPassProps[activeStep] && stepsPassProps[activeStep].config)
    ) {
      if (!isLoading) {
        dispatch(setIsLoading());
      }
      downLoadStuff(
        stepsConfig[activeStep].config || stepsPassProps[activeStep].config,
        source
      ).then(downLoaded => {
        dispatch(setStepPassProps(activeStep, { ...downLoaded }));
      });
    }
    return () => {
      source.cancel("Cancelling in cleanup");
    };
  }, [activeStep, isLoading, stepsConfig, stepsPassProps]);

  const classes = getStyles();

  //       
  let submitRef = null;
  const [isDisabled, setIsDisabled] = useState(false);
  return (
    <Paper>
      <Stepper activeStep={activeStep} orientation="vertical">
        {stepsConfig.map(
          (
            {
              label,
              Component,
              additionalProps,
              requiresFormSubmitRef,
              stepButtons: StepButtons
            },
            index
          ) => {
            const { onSubmit, ...restAdditionalProps } = additionalProps || {};
            const currentStepPassProps = stepsPassProps[index];
            const wrappedSubmit = onSubmit
              ? onSubmit({
                  passProps: currentStepPassProps,
                  dispatch
                })
              : () => {};
            return (
              <Step active={activeStep >= index} key={label}>
                <StepLabel>{label}</StepLabel>
                <StepContent active={activeStep >= index}>
                  {isLoading ? (
                    defaultPreloader({})
                  ) : (
                    <>
                      <Component
                        onSubmit={wrappedSubmit}
                        {...restAdditionalProps}
                        bindFormSubmit={ref => {
                          submitRef = ref;
                        }}
                        enableReinitialize
                        {...currentStepPassProps}
                        readOnly={activeStep !== index}
                      />
                      {isDisabled && activeStep === index && <LinearProgress />}
                      {activeStep === index && (
                        <div className={classes.actionsContainer}>
                          <Button
                            disabled={activeStep === 0}
                            onClick={() => decreaseActiveStep(dispatch)}
                            className={classes.button}
                            variant="contained"
                          >
                            
                          </Button>
                          {activeStep !== stepsConfig.length - 1 && (
                            <Button
                              variant="contained"
                              color="primary"
                              onClick={() => {
                                if (requiresFormSubmitRef === true) {
                                  //     
                                  if (typeof submitRef === "function") {
                                    submitRef();
                                  }
                                } else {
                                  dispatch(increaseActiveStep());
                                }
                              }}
                              className={classes.button}
                            >
                              
                            </Button>
                          )}
                          {StepButtons && (
                            <StepButtons
                              {...currentStepPassProps}
                              isDisabled={isDisabled}
                              setIsDisabled={setIsDisabled}
                            />
                          )}
                        </div>
                      )}
                    </>
                  )}
                </StepContent>
              </Step>
            );
          }
        )}
      </Stepper>
    </Paper>
  );
};

StepperMUI.propTypes = {
  stepsConfig: PropTypes.arrayOf(
    PropTypes.shape({
      Component: PropTypes.func.isRequired,
      label: PropTypes.string
    })
  ).isRequired
};
