import { FormikActions, FormikValues } from "formik";
import { DispatchType } from "./ScheduleTable";

export interface IOption {
  label: string;
  value: any;
  [any: string]: any;
}

export enum TypeEnum {
  ministration = "ministration",
  slot = "slot",
  workItem = "workItem"
}

export interface EventType {
  //   uuid v4    union
  uniqueId: number | string;
  from: string;
  to: string;
  type: IOption;
  performers: Array<IOption>;
  canEdit: boolean;
  typeEnum?: TypeEnum;
  isFree?: boolean;
}

export type dutiesType = Array<IOption>;

export type RowType = {
  day: string;
  mothWord: IOption;
  duties: IOption | null;
  events: Array<EventType>;
  canEdit?: boolean;
};

export type collectionType = Array<RowType>;

export type OtionsMapT = {
  [fieldName: string]: {
    loaded: boolean;
    options: Array<IOption>;
  };
};

export interface ScheduleTableProps {
  collection: collectionType;
  dispatch: DispatchType;
  omitDutiesColumn?: boolean;
  optionsMap: OtionsMapT;
}

export type FormikOnSubmit = (
  values: FormikValues,
  formikActions: FormikActions<FormikValues>
) => void;
