import React, { useEffect, useRef, useState } from "react";
import * as PropTypes from "prop-types";
import {
  Popper,
  Fade,
  Paper,
  Button,
  Grid,
  TextField,
  ClickAwayListener
} from "@material-ui/core";
import { Calendar } from "react-date-range";
import { format, parse } from "date-fns";
import "react-date-range/dist/styles.css"; // main style file
import "react-date-range/dist/theme/default.css";
import { makeStyles } from "@material-ui/styles";
import { checkIfFieldHasValidationError } from "../formikMUi/utils/checkIfFieldHasValidationError";

const getStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing.unit
  },
  buttonPanel: {
    borderTop: "solid 1px #eff2f7"
  },
  popper: {
    zIndex: 2000
  }
}));

const formatDateDisplay = date => {
  if (date) {
    return format(date, "DD.MM.YYYY");
  }
  return "";
};

/* const formatDateValue = date => {
  if (date) {
    return format(date, "YYYY-MM-DD");
  }
  return "";
}; */

export const IntegrateDatePickerWithMUI = props => {
  const classes = getStyles();
  const [open, setOpen] = useState(false);
  const [date, setDate] = useState(new Date());
  const {
    pickerProps,
    label,
    form: { touched, errors, setFieldValue },
    field: { name, value },
    readOnly
  } = props;

  const errorHelperTextProps = checkIfFieldHasValidationError(
    name,
    touched,
    errors
  );

  const containerRef = useRef(null);

  useEffect(() => {
    if (value) {
      setDate(parse(value, "DD.MM.YYYY", new Date()));
    }
  }, [value]);

  return (
    <div ref={containerRef}>
      <TextField
        value={value}
        label={label}
        onClick={() => {
          if (readOnly === false) {
            setOpen(true);
          }
        }}
        {...errorHelperTextProps}
        inputProps={{ readOnly }}
        fullWidth
      />
      <Popper
        open={open}
        anchorEl={containerRef.current}
        placement="bottom-start"
        transition
        className={classes.popper}
      >
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={350}>
            <ClickAwayListener
              onClickAway={() => {
                setOpen(false);
              }}
            >
              <Paper>
                <Calendar date={date} onChange={setDate} {...pickerProps} />
                <Grid
                  container
                  justify="flex-end"
                  className={classes.buttonPanel}
                >
                  <Grid item>
                    <Button
                      variant="outlined"
                      className={classes.button}
                      onClick={() => setOpen(false)}
                    >
                      
                    </Button>
                  </Grid>
                  <Grid item>
                    <Button
                      color="primary"
                      variant="outlined"
                      className={classes.button}
                      onClick={() => {
                        setFieldValue(name, formatDateDisplay(date));
                        setOpen(false);
                      }}
                    >
                      
                    </Button>
                  </Grid>
                </Grid>
              </Paper>
            </ClickAwayListener>
          </Fade>
        )}
      </Popper>
    </div>
  );
};

IntegrateDatePickerWithMUI.defaultProps = {
  readOnly: false,
  label: ""
};

IntegrateDatePickerWithMUI.propTypes = {
  pickerProps: PropTypes.shape({}).isRequired,
  field: PropTypes.shape({
    name: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired
  }).isRequired,
  form: PropTypes.shape({ errors: PropTypes.shape({}).isRequired }).isRequired,
  readOnly: PropTypes.bool,
  label: PropTypes.string
};
