import { computePropsForFormikMUI } from "../../../components/modalWrapper/utils/computePropsForForMikMUI";
import { baseTextAreaProps, baseTimeFieldProps } from "../../baseFieldProps";
import Yup from "../../yup";
import { halfWith, fullWidth } from "../../widths";
import {
  constructRebuildReceivedOptionsLabelLabelValueUniqueId,
  rebuildReceivedValueLabelLabelValueUniqueId
} from "../../rebuildRecieved";
import { HramTab } from "./hramTab";
import { PrimaryButton } from "../../../components/modalWrapper/buttons";

const createForm = {
  title: "",
  headerText: "label",
  url: "/api/strMember",
  primaryActionUrl: "/api/member/structureMembers",
  projection: "formMember",
  method: "post",
  doBeforeSend: {
    select: ({ value }) => value,
    file: ({ value }) => value
  },
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "label",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "address",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "type",
          type: "select",
          dictUrl: "/api/strType/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: "",
          name: "parentId",
          type: "select",
          dictUrl: "/api/strMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: fullWidth
        },
        {
          label: "",
          name: "town",
          type: "select",
          dictUrl: "/api/strDictTown/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: "",
          name: "metro",
          type: "select",
          dictUrl: "/api/strDictMetro/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: "",
          name: "coordination",
          type: "text"
        }
      ]
    },
    {
      blockTitle: " ",
      conditionallyRender: ({ type }) => (type ? type.enum === "HRAM" : false),
      fields: [
        {
          label: "",
          name: "hramObjectLocal.photo",
          type: "file",
          widths: fullWidth
        },
        {
          label: " ",
          name: "hramObjectLocal.description",
          ...baseTextAreaProps
        },
        {
          label: "-",
          name: "hramObjectLocal.webUrl",
          type: "text",
          widths: halfWith
        },
        {
          label: "",
          name: "hramObjectLocal.contacts",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "hramObjectLocal.history",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "hramObjectLocal.religion",
          ...baseTextAreaProps
        },
        {
          label: "   ",
          name: "hramObjectLocal.startWorkTime",
          ...baseTimeFieldProps
        },
        {
          label: "   ",
          name: "hramObjectLocal.finishWorkTime",
          ...baseTimeFieldProps
        },
        {
          label: "     ",
          name: "hramObjectLocal.visiblePriests",
          type: "checkbox",
          widths: fullWidth
        }
      ]
    },
    {
      blockTitle: "",
      conditionallyRender: ({ type }) => (type ? type.enum === "HRAM" : false),
      fields: [
        {
          label: "",
          name: "hramObjectLocal.replace1",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "hramObjectLocal.replace2",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "hramObjectLocal.replace3",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "hramObjectLocal.replace4",
          ...baseTextAreaProps
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    label: Yup.string().required(),
    type: Yup.object()
      .nullable()
      .required(),
    parentId: Yup.object().nullable(),
    address: Yup.string(),
    town: Yup.object().nullable(),
    metro: Yup.object().nullable(),
    coordination: Yup.string(),
    hramObjectLocal: Yup.object({
      photo: Yup.object().nullable(),
      description: Yup.string().required(),
      webUrl: Yup.string(),
      contacts: Yup.string(),
      history: Yup.string(),
      religion: Yup.string(),
      startWorkTime: Yup.string().required(),
      finishWorkTime: Yup.string().required(),
      visiblePriests: Yup.bool()
    }).nullable()
  })
};

const viewEditForm = {
  title: "",
  headerText: "label",
  url: "/api/strMember",
  projection: "formMember",
  method: "patch",
  blocks: [
    {
      blockTitle: " ",
      fields: [
        {
          label: "",
          name: "label",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "type",
          type: "select",
          dictUrl: "/api/strType/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: "",
          name: "parentId",
          type: "select",
          dictUrl: "/api/strMember/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: fullWidth
        },
        {
          label: "",
          name: "address",
          ...baseTextAreaProps
        },
        {
          label: "",
          name: "town",
          type: "select",
          dictUrl: "/api/strDictTown/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: "",
          name: "metro",
          type: "select",
          dictUrl: "/api/strDictMetro/search/present",
          rebuildReceivedOptions: constructRebuildReceivedOptionsLabelLabelValueUniqueId,
          rebuildReceivedValue: rebuildReceivedValueLabelLabelValueUniqueId,
          widths: halfWith
        },
        {
          label: "",
          name: "coordination",
          type: "text"
        },
        { name: "uniqueId", hidden: true }
      ]
    }
  ],
  additionalTabs: [
    {
      tabTitle: " ",
      tabContent: {
        Component: HramTab
      },
      conditionallyRender: ({ firstFormInitialValues: { type } }) =>
        !(type && type.enum === "HRAM"),
      tabButtons: [
        {
          Btn: PrimaryButton,
          requiresFormSubmitRef: true,
          conditionallyRender: ({ modal: { mode } }) => mode === "edit"
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    label: Yup.string().required(),
    type: Yup.object()
      .nullable()
      .required(),
    parentId: Yup.object().nullable(),
    address: Yup.string(),
    town: Yup.object().nullable(),
    metro: Yup.object().nullable(),
    coordination: Yup.string()
  })
};

export default {
  create: computePropsForFormikMUI({ formObj: createForm, mode: "create" }),
  view: computePropsForFormikMUI({ formObj: viewEditForm, mode: "view" }),
  edit: computePropsForFormikMUI({ formObj: viewEditForm, mode: "edit" }),
  createWorkItem: {
    formObj: {}
  }
};
