import { SET_ACTIVE_STEP } from "../constants";

const initialState = {
  activeStep: 0,
  passProps: {}
};

export default function stepperReducer(state = initialState, action) {
  switch (action.type) {
    case SET_ACTIVE_STEP: {
      const {
        payload: { activeStep, passProps }
      } = action;
      return {
        ...state,
        activeStep,
        passProps
      };
    }
    default: {
      return state;
    }
  }
}
