import React from "react";
import { Divider, Typography, Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import * as PropTypes from "prop-types";
import { RenderRow } from "./renderRow";
import ReconstructDescription from "../utils/reconstructDescription";
import defineItemVisibility from "../utils/dependenciesUt";
import constructKeyForRow from "../utils/constructKeyForRow";

const getStyles = makeStyles(theme => ({
  divider: {
    marginLeft: theme.spacing.unit,
    height: "1px",
    backgroundColor: theme.palette.primary.light,
    flex: "1  1 auto"
  }
}));

export const RenderBlock = props => {
  const {
    block: { label, attributes, description },
    formObj,
    values
  } = props;
  const classes = getStyles();
  return (
    <Grid item>
      <Grid container spacing={8} direction="column">
        <Grid item>
          <Grid container alignItems="center">
            <Typography variant="subtitle2" style={{ fontSize: "15px" }}>
              {label.toUpperCase()}
            </Typography>
            <Divider className={classes.divider} />
          </Grid>
        </Grid>
        {description !== label && typeof description !== "undefined" && (
          <Grid item>
            <Typography component="div">
              <ReconstructDescription htmlInput={description} />
            </Typography>
          </Grid>
        )}
        {
          <>
            {attributes.group.map(
              row =>
                defineItemVisibility(row, formObj, values) && (
                  <Grid
                    item
                    key={constructKeyForRow(row)}
                    xl={12}
                    lg={12}
                    md={12}
                    xs={12}
                  >
                    <Grid container spacing={8}>
                      <RenderRow row={row} formObj={formObj} />
                    </Grid>
                  </Grid>
                )
            )}
          </>
        }
      </Grid>
    </Grid>
  );
};

RenderBlock.propTypes = {
  formObj: PropTypes.shape({}).isRequired,
  block: PropTypes.shape({
    label: PropTypes.string,
    attributes: PropTypes.shape({}).isRequired,
    description: PropTypes.string
  }).isRequired,
  values: PropTypes.shape({}).isRequired
};
