import { SwapVert } from "@material-ui/icons";
import { format } from "date-fns";
import { FormikMUI } from "../../../../components/formikMUi/formikNewRealisation";
import { findFieldsWithDictThenConstructArrayToDownload } from "../../../../utils/commonFormFunctions";
import { constructInitialValuesForEmptyForm } from "../../../../components/formikMUi/utils/initialValuesUtils";
import Yup from "../../../yup";
import { StepperMUI } from "../../../../components/stepperMUI";
import {
  DownloadReportButtons,
  DownLoadReportStep
} from "../../../../components/reports/DownloadReportButtons";
import { filters } from "./filters";
import { increaseActiveStep } from "../../../../components/stepperMUI/reducer";

const firstStep = {
  blocks: [
    {
      fields: [
        {
          name: "report",
          label: "",
          type: "select",
          dictUrl: "/api/reports/allowedReports",
          rebuildReceivedOptions: data =>
            data.map(({ label, name }) => ({ label, value: name })),
          widths: { xs: 12, sm: 12, md: 12, lg: 12, xl: 12 }
        }
      ]
    }
  ],
  validationSchema: Yup.object({
    report: Yup.object()
      .nullable()
      .required()
  })
};

const stepsConfig = [
  {
    label: " ",
    Component: FormikMUI,
    config: findFieldsWithDictThenConstructArrayToDownload(firstStep),
    additionalProps: {
      initialValues: constructInitialValuesForEmptyForm(firstStep),
      formObj: firstStep,
      onSubmit: ({ dispatch }) => (
        { report: { value } },
        { setSubmitting }
      ) => {
        setSubmitting(false);
        const foundFiltersForReport = filters[value];
        if (typeof foundFiltersForReport === "undefined") {
          throw new Error(
            `       : ${value}`
          );
        }
        dispatch(
          increaseActiveStep({
            formObj: foundFiltersForReport,
            initialValues: constructInitialValuesForEmptyForm(
              foundFiltersForReport
            ),
            config: findFieldsWithDictThenConstructArrayToDownload(
              foundFiltersForReport
            ),
            reportName: value
          })
        );
      }
    },
    requiresFormSubmitRef: true
  },
  {
    label: "  ",
    Component: FormikMUI,
    additionalProps: {
      onSubmit: ({ dispatch, passProps: { reportName, formObj: localFO } }) => (
        values,
        { setSubmitting }
      ) => {
        const rebuildedFilters = { ...values };
        localFO.blocks.forEach(({ fields }) =>
          fields.forEach(({ name, type }) => {
            if (type === "date-range") {
              const value = values[name];
              if (value && value[0] && value[1]) {
                const rebuildDates = value.map(x =>
                  format(x, "DD.MM.YYYY HH:mm:ss")
                );
                rebuildedFilters[name] = rebuildDates;
                rebuildedFilters.startDate = rebuildDates[0];
                rebuildedFilters.endDate = rebuildDates[1];
              }
            }
            if (name === "wrappedProvider" && values[name]) {
              const { uniqueId, type: providerType } = values[name].value;
              rebuildedFilters[name] = undefined;
              rebuildedFilters.providerId = uniqueId;
              rebuildedFilters.providerType = providerType;
            }
            if (name === "consumerId" && values[name]) {
              rebuildedFilters[name] = values[name].value;
            }
          })
        );
        setSubmitting(false);
        dispatch(
          // increaseActiveStep({ templateUrl: "/api" })
          increaseActiveStep({ reportName, filters: { ...rebuildedFilters } })
        );
      }
    },
    requiresFormSubmitRef: true
  },
  {
    label: "  ",
    Component: DownLoadReportStep,
    stepButtons: DownloadReportButtons
  }
];

export const baseUrlsToDownLoadReport = {
  report_1: type => `/api/reports/download/report1/${type}`,
  report_2: type => `/api/reports/download/report2/${type}`,
  report_3: type => `/api/reports/download/report3/${type}`,
  report_4: type => `/api/reports/download/report4/${type}`,
  report_5: type => `/api/reports/download/report5/${type}`,
  report_6: type => `/api/reports/download/report6/${type}`
};

const statMV = {
  navigationLabel: "",
  icon: SwapVert,
  requiresFunction: [
    {
      functionName: "member_administration_statistics",
      allowedActions: ["view"]
    },
    {
      functionName: "administration_statistics",
      allowedActions: ["view"]
    }
  ],
  custom: true,
  Component: StepperMUI,
  passProps: {
    stepsConfig
  }
};
export default statMV;
